// slider  
$sliderBG_d  : #9c9a97;    // 슬라이터 bg 기본 
$sliderBG_a  : #21a099;     // 슬라이터 bg 활성화 
$sliderDra_d : #ebebeb;    // 슬라이터 드레거 기본 
$sliderDra_a : #ebebeb;     // 슬라이터 드레거 활성화 

// 슬라이터 BG
.ui-widget-content { background-color: $sliderBG_a;
  height: 4px; border-radius: 2px;
}
.ui-widget-header { background-color: $sliderBG_d;  height: 4px; border-radius: 2px; }

// 슬라이더 드래거
.ui-slider-handle.ui-corner-all.ui-state-default { background-color: $sliderDra_d; 
  width: 11px; height: 11px;; border-radius: 50%; margin-left: -5px;
}
.ui-slider-handle.ui-corner-all.ui-state-default.ui-state-active { background-color: $sliderDra_a; }

