.basis-section {
  &.auction_view-section { margin-top: 0;
  
  }
  @media all and (max-width:$G-wMobile)  {
    &.auction_view-section { margin-top: 0;
    
    }
  }
}

.content-panel{
  
  /* 상품 상세   */
  &.type_panel-product_view { 
    .panel-header { /* height: calc( 80px + 25px + 42px ); */ padding: 0;
      .header-inner{ position: relative;
      }
    }
    .panel-body   { margin-top: 80px;}
    .panel-footer {
      .btn_set_more {}
    }
    @media all and (max-width:$G-wMobile)  {
      .panel-header { padding: 0px; border-bottom: 0; height: auto;}
      .panel-body   { margin-top: 0px;}
      .panel-footer {
        .btn_set_more {}
      }
    }

    // 타이틀 
    .page_name-article { padding: 0px 0px; 
        width: 100%; /* height: calc( 80px + 25px + 42px );  */     
        background-color: #fff; z-index: 2;
        &.hold_start{ width : calc(100% - 100px);  max-width: 1320px; 
          &.hold_stop{ width: 100%; } 
        }
      .row-box { display: flex; justify-content: space-between;
        padding: 80px 0 25px; 
        border-bottom: 1px solid #dedede;
        .col_item:first-child { width: calc(100% - 170px); /* width: per(762px, 1320px); */}
        .col_item:last-child  { width: 170px; /* width: per(426px, 1320px); */ text-align: right;}
      }
      .page_title { display: flex; 
        a { display: inline-block; height: 48px;  text-align: left;  }
        i , span { vertical-align: middle;}
        i { margin-left: -14px; margin-top: 3px;}
        span  { font-size: 30px; font-weight: 600; color:$colorB; line-height: 48px;}
      } 
      .select-box { display: inline-block; width:  172px; 
        select { font-weight: 600;}
      }
      @media all and (max-width:$G-wMobile)  { 
        position: relative !important;
        width: calc( 100% + 50px); height: auto; 
        margin-left: -25px; padding: 0 25px;
        background-color: #f2f2f2;
        .row-box {
          padding: 0; 
          border-bottom: 0px ;
          .col_item:first-child  { width: 100%; }
          .col_item:last-child   { display: none; }
        }
        .page_title { position: relative;
          width: 100%; min-height: 56px; text-align: left; 
          padding: 17px 28px;
          display: flex; flex-wrap: nowrap; justify-content: normal; align-items: center;
          a { display: block;  width: 28px; height: 56px; 
            position: absolute; top:0; left: 0; text-align: left;
          }
          i { margin-left: -15px; margin-top: 20px; vertical-align: middle; 
            position: absolute;
          }
          span    { font-size: 18px; line-height: 1.3; vertical-align: middle; text-align: left; line-height: 25px;
          }
        } 
        
      }
    }

    .row_view { display: flex; 
      >.col_item { }
      /* >.col_item:nth-child(1) { width: 73px;} */
      >.col_item:nth-child(1) { width: calc( 100% - (426px));}
      >.col_item:nth-child(2) { width: 426px;}
      @media all and (max-width:$G-wMobile)  { display: block;
        /* >.col_item:nth-child(1) { width: 100%; display: none; } */
        >.col_item:nth-child(1) { width: 100%; }
        >.col_item:nth-child(2) { width: 100%; }
      }
    }
    @media all and (max-width:$G-wMobile) {
      .row_view { display: flex; 
        >.col_item { }
        /* >.col_item:nth-child(1) { width: 73px;} */
        >.col_item:nth-child(1) { width: calc( 100% - (426px));}
        >.col_item:nth-child(2) { width: 426px;}
        @media all and (max-width:$G-wMobile)  { display: block;
          /* >.col_item:nth-child(1) { width: 100%; display: none; } */
          >.col_item:nth-child(1) { width: 100%; }
          >.col_item:nth-child(2) { width: 100%; }
        }
      }
    }




    // 1. 섭네일
    .product_thumbnail-article{
      .gallery_thumbnail { position: relative; padding: 58px 0; margin-top: -20px; height:calc( 576px + (58px * 2) );
        .gallery_center {  overflow: hidden;
          .swiper-slide { position: relative; 
            .line { position: absolute; top:0; left: 0; width: 100%; height: 100%; z-index: 1;} 
            img { width: 100%;}
            &.active { .line { border: 3px solid $colorP;  } }
          }
        }
        .btn-prev, .btn-next { display: block; position: absolute; font-size: 0; line-height: 0; }
        .btn-prev { top:0  }
        .btn-next { bottom:0 }
      }
    }

    // 2. 비주얼
    .product_view-article{ padding: 0 per(133px , 894px) 0 per(111px, 894px);
      position: relative;
      .product_thumbnail-area{ position: absolute; top:0; left: 0; width: per(86px, 894px); max-width: 86px; 
        .gallery_thumbnail  { padding: 0; margin-top: 0; 
          .swiper-wrapper { display: flex; flex-direction: column; }
          .slide { position: relative; margin-bottom: 9.3023%; height: auto; width: 100%;
            .line { position: absolute; top:0; left: 0; width: 100%; height: 100%; z-index: 1;} 
            img { width: 100%;}
            &.active { .line { border: 3px solid $colorP;  } }
          }
        }
      }
     
      .gallery_center { overflow: hidden;}
      .image-area { position: relative;
        .img-ratio { background-color: #fff;}
        img {width: 100%;}
        .typo-align { position: absolute; top:0; width: 100%; height: 100%;}
        .category_name { 
          display: flex; align-content: center; flex-wrap: wrap; justify-content: center; flex-direction: column;
          width: 100%; height: 100%;  vertical-align: middle;
          font-size: 28px; font-weight: 500; color:#fff;
        }
        @media all and (max-width:$G-wMobile)  { 

        }
      }

      @media all and (max-width:$G-width)  { padding: 0 per(60px , 486px) 0 0;
        .product_thumbnail-area{ position: relative; width: 100%;  margin-top: 20px; max-width: none ;
          .gallery_thumbnail  { padding: 0; margin-top: 0;  width: 100%;
            .swiper-wrapper { display: flex; flex-direction: row; width: 100%; }
            .slide:last-child() {  margin-right: 0; }
            .slide { position: relative; margin-bottom: 0; height: auto; width: 100%; margin-right: 8px;
              .line { position: absolute; top:0; left: 0; width: 100%; height: 100%; z-index: 1;} 
              img { width: 100%;}
              &.active { .line { border: 3px solid $colorP;  } }
            }
          }
        }
      }
      @media all and (max-width:$G-wMobile)  { position: relative;
        padding: 0; width: calc(100% + 50px); margin-left: -25px;
        .product_thumbnail-area{  display: none;}

      }



      .view_scale-area  { padding: 12px 0; text-align: center;
        a {display: inline-block; }
        i, span { vertical-align: middle;}
        span { margin-left: 10px; font-size: 16px; font-weight: 600; color:$colorB2; line-height: 26px; }
        @media all and (max-width:$G-width)  {  padding-top: 35px; }
        @media all and (max-width:$G-wMobile)  { 
          position: absolute; bottom: 0; right: 0; padding: 8px 25px;  
          span { display: none;}
        }
        
      }

      .pagination { display: none;  width: 100%; padding: 15px 0;
        text-align: center; font-size: 0; line-height: 0;
        .swiper-pagination-bullet { width: 10px; height: 10px; margin: 0 4px;
          background-color: #ccc; opacity: 1;
          &.swiper-pagination-bullet-active {  background-color: #555;}
        }
        @media all and (max-width:$G-wMobile)  { 
          display: block;  width: 100%; padding: 15px 0;
           .swiper-pagination-bullet { width: 6px; height: 6px; margin: 0 2px; }
        }
      }
    }

    // 3. 상품 상세 
    .product_detail-article{ width: 426px;
      .article-header{}
      .article-body{}
      .article-footer{}
      @media all and (max-width:$G-wMobile)  {  width: 100%; 
        position: relative !important; top: auto !important;
      }

      .index-area    { padding-top: 45px; display: flex; justify-content: space-between; font-size: 0; line-height: 0;
        .index-box {
          span { font-size: 26px; line-height: 30px; font-weight: 600; color:$colorB2; }
        }
        .btn-box   { position: relative; text-align: right;
          a { display: inline-block;
            &.sns_share {
              &.on i {  &::after {  @include sp_icons($icon-view_sns_on) } }
            }
            &.work_heart {
              &.on i {  &::after {  @include sp_icons($icon-view_heart_on) } }
            }
          }
          a + a { margin-left: 20px;}
        }
        /* sns - layer */
        .sns_layer-area { display: none; 
          &.active { }
        }
        .sns-layer { position: absolute; right: 0; 
          margin-top: 10px; padding: 15px 20px; background-color: #fff;
          display: flex;  @include shadow(1px, 3px, 10px, .07 );
          .sns-item { position: relative;  text-align: center;
            .txt { width: 70px; font-size: 16px; line-height: 1.5; text-align: center; }
          }
          .sns-item + .sns-item { margin-left: 15px; padding-left: 15px;
            @include list-div_bar( 5px, 0, 43px, #bdbdbd);
          }
        }
        @media all and (max-width:$G-wMobile)  { padding-top: 45px;
          .index-box {
            span { font-size: 18px; line-height: 30px; }
          }
          .btn-box   {
            a {
              &.sns_share {
                &.on i {  &::after {  @include sp_icons2x($icon-view_sns_on-2x) } }
              }
              &.work_heart {
                &.on i {  &::after {  @include sp_icons2x($icon-view_heart_on-2x) } }
              }
            }
            a + a { margin-left: 5px;}
          }
        }
      }
      .artist-area {  display: flex; flex-direction: column; justify-content: center;
        padding-top: 10px; padding-bottom: 25px;
        min-height: 137px; 
        .name { 
          strong { /* @include ellipsis-line(2); */ }
        }
        .desc { margin-top: 5px; 
          span { /* @include ellipsis-line(2); */ 
          }
        }
        strong { font-size: 40px; font-weight: 600; color:$colorB; display: block; line-height: 58px; }
        span   { font-size: 24px; font-weight: 500; color:$colorB2; line-height: 38px;}
        /* strong + span { margin-left: 20px;} */
        @media all and (max-width:$G-wMobile)  { height: auto;  padding: 10px 0 20px ;
          .name  { 
            strong { font-size: 17px; line-height: 25px; }
            span {margin-top: 5px;}
          }
          .desc  { margin-top: 8px; }
          strong { font-size: 27px;  }
          span   { font-size: 16px; color:$colorB; line-height: 24px;}
          strong + span { margin-left: 0px;}
        }
      }
      .price-area    { padding: 26px 0; border-top: 1px solid #ccc; border-bottom: 1px solid #ccc;
        .price-list {  position: relative; padding-left: 60px;
          dt { position: absolute; left: 0; 
            font-size: 18px; font-weight:600; color: $colorB2; 
            line-height: 37px; height: 37px;
          }
          dd { font-size: 18px; font-weight:500; color: $colorB2; 
            line-height: 37px; height: 37px;
            text-align: right; 
          } 
          strong { font-weight: bold; color:$colorE; }
          em     { font-weight: 500; color:$colorE; margin-left: 5px;}
          b      { font-weight: 700;} 
          s      { font-size: 16px; font-weight: bold; color:$colorS; text-decoration: none; }
        }
        @media all and (max-width:$G-wMobile)  {  padding: 20px 0;
          .price-list {  position: relative; padding-left: 60px;
            dt, dd { font-size: 15px; line-height: 20px; height: auto; min-height: 28px; padding: 4px; } 
            em     { font-weight: 500; }
            b      { font-weight: 700; } 
            s      { font-size: 15px;  }
          }
        }
      }
      .button-area   { padding: 30px 0 28px;
        .btn_set + .btn_set { margin-top: 20px;}
        .btn_set { .btn_item { position: relative; width: 100%;} }
        .btn_set.cols_2 { 
          .btn_item:first-child { margin-right: 5px;}
          .btn_item:last-child { margin-left: 5px;}
        }
        .op_hover { display: none; position: absolute; top:0; left: 0; width: 100%;}
        .hover_change:hover{ 
          .op_hover { display: block;}
        }
        .btn_black.btn_2 {display: flex; flex-direction: column; justify-content: center;
            height: 56px; 
          em     { display: block; font-size: 14px; font-weight: 500; line-height: 1.4; }
          strong { display: block; font-size: 16px; font-weight: bold;line-height: 1.4; }
        }
        @media all and (max-width:$G-wMobile)  { padding: 25px 0 22px;
          .btn_set + .btn_set { margin-top: 0px;}
          .btn_set.only-pc { display: none;}
        }
      }
      .other-area    {  display: flex; justify-content: space-between; 
        .inquiry-box, .print-box { font-size: 0; line-height: 0; }
        .print-btn {width: 30px;}
        i, span { vertical-align: middle;}
        i + span { margin-left: 5px;}
        i { margin-left: -3px;}
        span { font-size: 18px; font-weight: 600; color:$colorB2; line-height: 30px;}
        @media all and (max-width:$G-wMobile)  { 
          .print-btn {width: 25px;}
          span { font-size: 16px; line-height: 25px;}
        }
      }

      /*=====  ====*/
      .saleprice-area { padding: 30px 0; border-top: 1px solid #ccc;
        dl { display: flex; justify-content: space-between;}
        dt, dd { font-size: 18px; color:$colorB2; font-weight: 600; line-height: 30px; }
        dd { font-weight: bold;}
        @media all and (max-width:$G-wMobile)  { padding: 25px 0; 
          dt, dd { font-size: 15px; }
        }
      }
      .inquirybtn-area {padding: 30px 0; border-top: 1px solid #ccc;
        .btnset {  display: flex; align-items: center;
          .btn-box { position: relative;}
          .btn-box:first-child { width: 360px;  .btn{ width: 100%;}}
          .btn-box:last-child  { width: calc( 100% - 360px); text-align: right;
          }
        }
        @media all and (max-width:$G-wMobile)  { padding: 25px 0; 
          .btnset {  
            .btn-box:first-child { width: 100% }
            .btn-box:last-child  { display: none;    }
          }
        }
      }
      .inquiryinfo-area {
        .inquiryinfo-box { position: relative; padding-left: 40px;
          i { position: absolute; top:0; left: 0; }
          .txt { font-size: 18px; font-weight: 500; line-height: 1.72;}
        }
        @media all and (max-width:$G-wMobile)  {
          .inquiryinfo-box {  padding-left: 30px;
            i { position: absolute; top:0; left: 0; }
            .txt { font-size: 14px; line-height: 1.5;}
          }
        }
      }



      .info-area    { position: relative; padding: 30px 0;  border-top: 1px solid #ccc;
        strong { font-size: 20px; color: $colorB2; font-weight: 600; }
        ul { margin-top: 15px;
          li { font-size: 18px; color:$colorD; font-weight: 500; line-height: 30px;}
        }
        .print-box { position: absolute; right:0; bottom:30px; font-size: 0; line-height: 0;}
        @media all and (max-width:$G-wMobile)  {  padding: 25px 0;
          strong { font-size: 15px; }
          ul { margin-top: 10px;
            li { font-size: 14px; line-height: 24px;}
          }
          .print-box { position: absolute; right:0; bottom:27px;}
        }
      }
      .inquiry-area { margin-top: 16px;
        .inquiry-box { padding: 40px;  border: 1px solid #555555; }
        .title { font-size: 22px; font-weight: 700; color:$colorB; line-height: 30px; 
          i, span { vertical-align: middle;}
          i + span { margin-left: 18px;}
        }
        .desc { margin-top: 20px;
          font-size: 20px; font-weight: 500; color:$colorD; 
          em { font-weight: 600; color:$colorB2; }
        }
        dl + dl { margin-top: 0; }
        dl { position: relative; padding-left: 20px; margin-top: 15px;
          dt { position: absolute; top:0; left: 0;
            font-size: 18px; font-weight: 500; color:$colorS; line-height: 30px; }
          dd { font-size: 18px; font-weight: 500; color:$colorB2; line-height: 30px;}
        }
        .btn_set { margin-top: 30px;
          .btn { width: 160px;}
        }
        @media all and (max-width:$G-wMobile)  { margin-top: 10px;
          .inquiry-box { padding: 30px 20px; }
          .title { font-size: 17px;  line-height: 25px; 
            i + span { margin-left: 10px;}
          }
          .desc { margin-top: 15px; font-size: 17px; }
          dl + dl { margin-top: 0; }
          dl { position: relative; padding-left: 16px; margin-top: 15px;
            dt {
              font-size: 15px; line-height: 27px; }
            dd { font-size: 15px; line-height: 27px;}
          }
          .btn_set { margin-top: 20px;
            .btn { width: 160px;}
          }
        }
      }
      
      
    }

    // 상품 상세정보
    .product_detail_view-article {
      width: calc( 100% - (426px)); 
      .view_editor-warp { padding: 70px per(133px , 894px) 110px 0;}
      @media all and (max-width:$G-width)  { 
        .view_editor-warp { 
          padding: 70px per(60px , 486px) 110px 0;
        }
      }
      @media all and (max-width:$G-wMobile)  { width: 100%;
        .view_editor-warp { padding: 70px 0 50px;}
      }
    }
    

    // 최근 본 작품
    .product_recent_work-article { border-top: 1px solid #dedede;
      .article-header  {  padding: 72px 0 25px;
        .title { font-size: 34px; font-weight: bold; color: $colorB; line-height: 58px; } 
        @media all and (max-width:$G-wMobile)  {  margin-top: 20px;  padding: 30px 0 30px;
          .title { font-size: 22px; line-height: 1.36;
            span { vertical-align: middle;}
          } 
        }
      }
      .article-body { 
        @media all and (max-width:$G-wMobile)  { 
          .scroll-type { overflow: hidden; overflow-x: auto;  flex-wrap: nowrap; }
        }
        @media all and (max-width:$G-wMobile)  {  
          .scroll-type { width: calc( 100% + 50px); margin-left: -25px; 
            padding: 0 25px;
            .product-area { }
          }
        }
      }

      .product-list { display: flex; flex-direction: row; 
        width: calc( 100% + 20px ); margin-left: -10px;
        li { width: 20%; padding: 0 10px; padding-bottom: 33px; 
          .li-inner { position: relative;   border: 1px solid #ccc; height: 100%; }
          &.last-item { display: none;}
        }
        
        @media all and (max-width:$G-wMobile)  { 
          li { width: 272px; }
        }
        @media all and (max-width:$G-wMobile)  { 
          width: calc( 100% + 10px ); margin-left: -5px;
          /* width: auto; margin-left: 0; */
          li { width: 220px;  padding: 0 5px; padding-bottom: 17px; 
            /* &.last-item { display: block;  padding: 0 10px; } */
          }
          &::after { content: ""; display: block; padding: 0 10px;}
        }
  
        .item-article { 
          .image-area { position: relative;
            .img-ratio { background-color: #f6f6f6;}
            img {width: 100%;}
            .typo-align { position: absolute; top:0; width: 100%; height: 100%;}
            .category_name { 
              display: flex; align-content: center; flex-wrap: wrap; justify-content: center; flex-direction: column;
              width: 100%; height: 100%;  vertical-align: middle;
              font-size: 28px; font-weight: 500; color:#fff;
            }
          }
          .typo-area { padding: 0px 20px;
            > div { position: relative; }
            .num_heart-box{ display: flex; justify-content: space-between; padding: 18px 0 0px; }
            .info-box {  padding: 15px 0 15px; }
  
            .num      { font-size: 18px; font-weight: 600; color:$colorB2 ; line-height: 30px; }
            .heart    { width: 30px; height: 30px; cursor: pointer;
              &.on i {  &::after {  @include sp_icons($icon-heart_on) } }
              i { margin-right: -3px; }
            }
            .title    { font-size: 22px; font-weight: bold; color:$colorB2; letter-spacing: 0; @include ellipsis();
              span { display: block; @include ellipsis(); }
            }
            .sub      { font-size: 18px; font-weight: 500;  color:$colorB2; letter-spacing: 0;}
            .desc     { margin-top: 3px; font-size: 18px; font-weight: 500; color:$colorB2;letter-spacing: 0px; 
              span { display: block; @include ellipsis(); }
            }
            
          }
          .product_cancle-area { display: none; position: absolute; top:0; left: 0;
            width: 100%; height: 100%; background-color: #f6f6f6; 
            .area-inner  {  display: flex; flex-direction: column; justify-content: center; align-items: center;
              height: 100%; text-align: center;
            }
            .typo { margin-top: 5px;
              font-size: 18px; font-weight: 500; color:$colorS; line-height: 1.64;
              span {}
            }
          }
          @media all and (max-width:$G-wMobile)  {
            .typo-area { padding: 0 20px;
              > div { position: relative; }
              .num_heart-box{ display: flex; justify-content: space-between; padding: 14px 0 0px; }
              .info-box {  padding: 0px 0 15px; }
  
              .num      { font-size: 14px; line-height: 30px; }
              .heart    { width: 30px; height: 30px; i { margin-right: -5px;}
                  &.on i {  &::after {  @include sp_icons2x($icon-heart_on-2x) } }
              }
              .title    { font-size: 16px; line-height: 30px;}
              .sub      { font-size: 14px; }
              .desc     { margin-top: 5px; font-size: 14px; letter-spacing: -1px; }
            }
          }
        }
  
  
      }
    }



  }

}


/* 모바일 응찰하기 */
.sticky_bidding-article { display: none; position: fixed; bottom: 0; width: 100%; z-index: 9;
  @include shadow( -5px, 0, 10px, .07);
  .btn_set {  position: relative; display: flex; width: 100%; height: 65px; }
  .btn_lot-box { position: relative; width:per(250px, 750px); height: 100%; 
    background-color: #fff; 
    button { display: block; width: 100%; height: 65px; }
    .txt { padding-top: 30px; width: 100%; height: 65px; border: 0; 
      font-size: 15px; color:$colorB2; font-weight: 600;  line-height: 1.3; vertical-align: bottom; text-align: center;
    }
    i { position: absolute; top:10px; left: 50%; margin-left: -10px; }
  }
  .btn-box    { position: relative; width:per(500px, 750px); height: 100%; 
    button { width: 100%; height: 65px; background-color: $colorMain;
      font-size: 15px; line-height: 65px; font-weight: 600; color:#fff; text-align: center;
    }
  }

  @media all and (max-width:$G-wMobile)  { display: block;
