.content-panel{
  /* 위탁안내 */
  &.type_panel-consignment { margin-top: 96px;

    .consignment-area {
      .article-header { margin-bottom: 50px;
        .title { color:$colorB;}
      }
      .article-body {
        //.sub_tab-area { margin-bottom: 50px;}
        .cont-sub { margin-top: 50px;}
      }

    }
    .consignment-area + .consignment-area { margin-top: 105px;}

    @media all and (max-width:$G-wMobile)  {
      margin-top: 40px;
      .consignment-area {
        .article-header { margin-bottom: 15px; }
        .article-body {
          //.sub_tab-area { margin-bottom: 5px;}
          .cont-sub { margin-top: 25px;}
        }
      }
      .consignment-area + .consignment-area { margin-top: 50px; }
    }
  }

  //dot list
  .dot_list {
    li { position: relative; padding-left: 13px; word-break: keep-all;
      &:before {
        content:"·"; position: absolute; top:0;left:2px;
      }
      span{ display:contents;}
      a { display:inline-block; text-decoration: underline;}
    }
    @media all and (max-width:$G-wMobile)  {
      li { position: relative; padding-left: 13px; word-break: keep-all;
        span{ display:contents;}
        a { display:inline-block; text-decoration: underline;}
      }
    }
  }

  .notice_02 { color:#db1b1b;}

  //위탁안내
  .consignment_info { display:flex; flex-direction: row; flex-wrap: wrap; width: calc( 100% + 20px); margin-left: -10px;
    li { width: 33.33335%; padding: 0 10px;
      .inner { border:1px solid #ccc; height:100%;
        .img { 
          img { width: 100%;}
        }
        dl { padding: 30px;
          dt { margin-bottom: 10px; color: $colorB2;
            span { color:$colorP;}
          }
          dd { word-wrap: break-word;  word-break: keep-all;}
        }
      }
    }

    @media all and (max-width:$G-wMobile)  {
      width: 100%; margin-left:0;
      li { width: 100%; padding: 0; margin-top: 10px;
        &:first-child { margin-top: 0;}
        .inner { 
          dl { padding: 20px;
            dt { margin-bottom: 10px;}
          }
        }
      }
    }
  }

  //위탁절차안내 -위탁안내,중국미술품위탁 공통
  .sell_item-title { margin-bottom: 20px;
    .step  { font-size: 16px; font-weight: bold; color:$colorS; }
    .title { font-size: 26px; font-weight: bold; color:$colorB2; }
    @media all and (max-width:$G-wMobile)  {  margin-bottom: 10px;
      .step  { font-size: 13px;}
      .title { font-size: 18px; margin-top: 2px;}
    }
  }

  .sell_item-area { margin-top: 35px; display: flex; justify-content: space-between; flex-wrap: wrap;
    .refer { margin-top: 30px; padding-top: 30px; border-top: 1px solid #ccc; 
      .dot_list li{ color:$colorS; }
    }
    @media all and (max-width:$G-wMobile)  { margin-top: 15px;
      .refer { margin-top: 15px; padding-top: 15px; }
    }

    //위탁절차 step1-신청접수
    .sell-document { width: calc(50% - 10px); border: 1px solid #ccc;
      .item-header { text-align:center; padding: 60px 0 25px; color:$colorB2; 
        .icon-box { margin-bottom: 15px; img{width: 142px;}}
      }
      .item-body { padding: 30px;
        .mail_txt { 
          .icon-box { display:inline-block; margin: 3px 5px 0 0;}
          a { display:inline-block; text-decoration: underline;}
        }
        strong { font-weight: 600; color:$colorB2;}
        dl {
          dt { margin-top: 25px;}
        }
        .btn-box { margin-top: 25px;
          a { margin-right:6px;}
        }
      }
    }
    @media all and (max-width:$G-wMobile)  {
 
      .sell-document { width: 100%;     
        & + .sell-document { margin-top:10px;}
        .item-header { padding: 25px 0 0;
          .icon-box { margin-bottom: 5px;
            img{width: 71px;}
          }
        }
        .item-body { padding: 20px;
          .mail_txt { 
            .icon-box { display:inline-block; margin: 3px 5px 0 0;}
            a { display:inline-block; text-decoration: underline;}
          }
          dl {
            dt { margin-top: 16px;}
          }
          .btn-box { margin-top: 20px;}
        }
      }
    }

    .sell-box { border: 1px solid #ccc; display: flex; width: 100%;
      .item-inner { width: 100%;}

      //위탁절차 step2-심의
      .screening_step { display: flex; flex-wrap: wrap; padding: 60px 0; 
        li { text-align: center; width: 50%; border-left:1px solid #ccc;
          &:first-child { border-left:0;}
          dl { margin-top: 35px;
            dt { font-weight: 600; color:$colorB2; margin-bottom: 5px;}
            dd { line-height: 1.5;}
          }
        }
        @media all and (max-width:$G-wMobile)  { padding: 0; flex-wrap: nowrap; flex-direction: column;
          li { width: 100%; border-left:0;  padding: 0px 19px;
            .li-inner { padding: 15px 0 20px; border-top:1px solid #ccc; }
            &:first-child .li-inner { border-top:0;}
            .icon-box { margin-top: 10px;}
            dl { margin-top:10px;
              dt { margin-bottom: 5px;}
            }
          }
        }
      }

      //위탁절차 step3-감정및계약
      .contract_step { display: flex; flex-wrap: wrap; justify-content: center; padding: 50px 0;
        li { text-align: center; width: 25%; max-width: 300px; position: relative;
          &::before { content:''; display:inline-block; width:15px; height:28px; position: absolute; left:0; top:30px; background:url('/images/pc/sell/icon-sell_step.png')}
          &:first-child {
            &::before { display:none;}
          }
          dl { margin-top: 20px;
            dt { font-size: 20px; font-weight: 700; color:$colorP; margin-bottom: 10px;}
            dd { font-size: 20px; color:$colorB2; font-weight: 600;}
          }
        }
        @media all and (max-width:$G-wMobile)  { padding: 5px 0;
          li { width: 100%; max-width: none; padding: 20px;
            display: flex; 
            &::before { content:''; display:inline-block; width:19px; height:11px; position: absolute; left:40px; top:-7px; background:url('/images/pc/sell/icon-sell_step_mo.png'); background-size: contain}
            &:first-child {
              &::before { display:none;}
            }
            .icon-box { display:inline-block; margin-right: 18px; vertical-align: top; width: 65px;}
            dl { margin-top: 0; display:block; text-align: left;
              width: calc( 100% - 65px) ;
              dt { font-size: 15px; margin-bottom: 3px;}
              dd { font-size: 15px;}
            }
          }
        }
      }

      //위탁절차 아이콘 하나일때
      .onebox_step { display: flex; flex-wrap: nowrap; padding: 60px;
        .icon-box { margin-right:65px; img { width: 142px;}}
        dl { width: 100%;
          &:first-child dt { margin-top:0;}
          dt { margin-bottom: 10px; }
          dd { 
            .notice { margin-top: 15px; color:$colorS}
          }
        }
        dl + .notice_02 { margin-top: 25px;} 
        .item_tit { color:$colorB2; font-weight: 600; margin-bottom:20px;}

        @media all and (max-width:$G-wMobile)  { padding: 25px 20px;  flex-direction: column;
          .icon-box {  margin:0; img { width: 71px;}}
          dl { 
            dt { margin-bottom: 5px; margin-top:10px;}
            dd { 
              .notice { margin-top: 10px;}
            }
          }
          dl + .notice_02 { margin-top: 10px;} 
          .item_tit { margin-bottom:10px;}
        }
      }
    }

  }
}