/* 로그인 */
.type_panel-login{max-width: 600px; margin: 0 auto; padding: 40px 80px 93px;
  .panel-body{
    .title{text-align: center;
      .tt2{color:$colorB;}
    }
  }
  .id_pw_wrap{margin-top: 35px;
    input{height: 54px;}
    .pw_box{margin-top: 20px;}
  }
  .checkbox_wrap{margin-top: 20px;}
  .login_btn{margin-top: 30px;
    .btn{width: 100%;}
  }
  .login-find{margin-top: 30px;
    ul{ display: flex;flex-wrap: wrap;justify-content: center;
      li{ position: relative; display: flex;flex-wrap: wrap;}
      li + li {margin-left: 15px; padding-left: 15px; @include list-div_bar(4px, 0, 18px, #ccc) }
    }
    .tt6{color: #333333;}
  }
  .sns_set{border-top: 1px solid #ccc;margin-top: 50px; padding-top: 30px;
    .title{font-size: 20px; color: #333333;font-weight: 600;
      span{margin-left: 5px;}
    }
    .sns_icon_wrap{margin-top: 30px;display: flex;justify-content: center;
      ul{display: flex;justify-content: center;
        li{ position: relative; padding: 0 18px;}
      }
      .sns_latest_wrap{  position: absolute; bottom: -42px; left: 50%; margin-left: -46px; }
    }
  }

  /* 로그인에러 */
  .error_wrap{background-color: #f6f6f6;padding: 20px;margin-top: 30px;
    p{font-size: 16px; line-height: 1.7;color: $colorE;}
  }
}
@media all and (max-width:$G-wMobile)  {
  /* 로그인 */
  .type_panel-login{padding: 15px 0 60px; 
    .id_pw_wrap{margin-top: 25px;
      .pw_box{margin-top: 14px;}
    }
    .checkbox_wrap{margin-top: 14px;}
    .login_btn{margin-top: 18px;}

    .login-find{margin-top: 25px;
      ul{
        li + li {margin-left: 8px; padding-left: 8px; @include list-div_bar(4px, 0, 11px, #ccc) }
      }
    }
    .sns_set{border-top: 0px;margin-top: 50px; padding-top: 0px;
      .title{font-size: 17px;
        span{margin-left: 3px;}
      }
      .sns_icon_wrap{margin-top: 18px;
        ul{
          li{padding: 0 10px;}
        }
        .sns_latest_wrap{bottom: -32px; left: 50%; margin-left: -40px;
          img{width: 81px;height: 35px; max-width: none; max-height: none;}
        }
      }
    }
  /* 로그인에러 */
    .error_wrap{margin-top: 15px;
      p{font-size: 13px; line-height: 1.7;color: $colorE;}
    }
  }
}
/* 아이디찾기 */
.type_panel-searchid{ max-width: 620px; margin: 0 auto; padding: 40px 80px 100px;
  .panel-header{padding-top: 0;
    .title{text-align: center;
      p{color: $colorB;font-size: 34px;font-weight: 700;}
    }
    
  }
  .panel-body{
    .txt_wrap{color: $colorB2;padding: 15px 0 35px; border-bottom: 1px solid #ccc;
      .mark_dot-list{
        li{padding-left: 0.6em;}
      }
      li + li{margin-top: 10px;}
      strong{font-weight: 700;}
      em{color: $colorP;font-weight: 700;}
    }
    .radio_wrap{padding-left: 20px; margin-top: 35px;}
    .info_wrap{ padding: 30px 20px 40px;border-bottom: 1px solid #ccc;
      dl + dl{margin-top: 10px;}
      dl{ position: relative; display: flex; align-items: center; 
        dt{ width: 120px;
          font-size: 20px; font-weight: 700;color: $colorB2;}
        dd{ width: calc(100% - 120px);}
      }
    }
    .search_btn{margin-top: 30px;
      .btn_set-float{
        a{min-width: 170px;}
      }
    }
  }
  .panel-bottom{
    .info_box{background-color: #f6f6f6;padding: 30px;margin-top: 40px;
      strong{font-size: 18px;color: #333333;font-weight: 700;}
      ul{color: #777777;margin-top: 10px;
        li{padding-left: 0.8em;
          &::after{background: #777777;}
          a{display: inline-block; text-decoration: underline; margin-left: 20px; }
        }
      }
      .info_txt{color: #777777;padding-left: 13px;margin-top: 3px;
        span{
          a{ text-decoration: underline;}
        }
      }
      .btn_set{height: 43px;padding-left: 13px;margin-top: 18px;
        span{line-height: 30px;color: #555555;font-weight: 500;}
      }
    }
  }
}

@media all and (max-width:$G-wMobile)  {
  /* 아이디찾기 */
  .type_panel-searchid{padding: 15px 0 60px;
    .panel-header{padding-bottom: 0;
      .title{
        p{font-size: 22px;}
      }
    }
    .panel-body{
      .txt_wrap{padding: 30px 0 30px; 
        .mark_dot-list{
          li{
            &:after{top: 7px;}
          }
        }
        li + li{margin-top: 5px;}
      }
      .radio_wrap{padding-left: 10px;margin-top: 25px;}
      .info_wrap{ padding: 20px 10px 25px;
        dl + dl{margin-top: 15px;}
        dl{ position: relative; flex-direction: column;align-items:inherit;
          dt{ width: 100%;font-size: 15px;}
          dd{ width: 100%; margin-top: 7px;}
        }
      }
      .search_btn{margin-top: 30px; width: 100%;
        .btn_set-float{
          a{width: 100%;}
        }
      }
    }
    .panel-bottom{
      .info_box{padding: 20px 15px;margin-top: 35px;
        strong{font-size: 15px;}
        ul{margin-top: 7px;
          li{padding-left: 0.6em;
            &::after{top: 7px;}
            a{margin-left: 10px; }
          }
        }
        .info_txt{margin-top: 3px;font-size: 13px; padding-left: 7px;}
        .btn_set{height: 32px;padding-left: 7px; margin: 15px 0 8px;
          span{line-height: 25px; font-size: 13px;}
        }
      }
    }
  }

}
 

