.error{ height: 100%;
  .error-inner { padding: 174px 0;}
  .etc_page-article { max-width: 900px; margin: 0 auto;
    .article-header { text-align: center; }
    .article-body   { text-align: center; margin-top: 45px; }
    .article-footer { text-align: center; margin-top: 50px; .btn { width: 180px;} } 
  }
  @media all and (max-width:$G-wMobile) {
    .error-inner { padding: 85px 25px;}
    .etc_page-article { 
      .article-body   { text-align: center; margin-top: 20px; }
      .article-footer { text-align: center; margin-top: 35px; .btn { width: 160px;} } 
    }
  }

  .img-box { text-align: center; img{ display: inline-block;}}
  .title { font-size: 32px; color:$colorB2; font-weight: 700; }
  .desc  { margin: 10px auto 0; font-size: 20px; color: $colorD; font-weight: 500;  line-height: 1.78; }
  .alert-box { margin: 0 auto; margin-top: 30px; padding-top: 30px; 
    width: 100%; max-width: 500px; border-top: 1px solid #ededed; 
    p + p { margin-top: 15px;}
    p { font-size: 18px; font-weight: 600; color:$colorB2;
      em { color:$colorP;}
    }
  }

  @media all and (max-width:$G-wMobile) {
    .img-box { text-align: center; img{ display: inline-block; width: 150px;}}
    .title { font-size: 22px; }
    .desc  { margin: 10px auto 0; font-size: 15px;  line-height: 1.5; }
    .alert-box { margin: 0 auto; margin-top: 20px; padding-top: 20px; 
      p + p { margin-top: 10px;}
      p { font-size: 14px; }
    }
  }

}



/* =====================  loading =====================*/
.page_loading { display: none;
  position: fixed;top: 0;left: 0;
  width: 100%;  height: 100%; min-height: 100%; z-index: 999999; 
overflow-x: hidden;overflow-y: hidden;
  background-color: rgba(0,0,0, 0.5);
  .center_teable { display: table; table-layout: fixed; width: 100%;  height: 100%;}
  .center_center { display: table-cell;width: 100%;
      padding: 20px 0;text-align: center;vertical-align: middle;   
      .img{ display: inline-block; background-color: transparent;};
  }
}
/* //=====================  loading =====================*/
