
/* =============================== 섹션 이름  */
.basis-section{
  /* 비주얼 영역 */
  &.key_visual-section{  margin-top: 0; padding: 0; max-width: 2560px;
    .bgimg { height: 600px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      background-color: #fff;
    }
    .type-dday { background-image: url(/images/pc/auction/keyvisual_dday2.jpg);}
    .type-dcount { background-image: url(/images/pc/auction/keyvisual_dcount2.jpg);}
  }

  /* 진행,예정 - 텝 */
  &.tab-auction_other-section {}
  
  /* 진행,예정 경매 - 리스트 */
  &.auction_other_list-section { margin-top: 0px; }
  @media all and (max-width:$G-wMobile)  {
    &.key_visual-section{
      .bgimg { width: 100%; height:0; padding-bottom: 111.1111%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
      }
      .type-dday { background-image: url(/images/mobile/auction/keyvisual_dday.jpg);}
      .type-dcount { background-image: url(/images/mobile/auction/keyvisual_dcount.jpg);}
    }
    &.tab-auction_other-section  { padding: 0; }
    &.auction_other_list-section { margin-top: 0px; }
  }

  &.bidding_application-section{ margin-top: 65px;
    @media all and (max-width:$G-wMobile)  {
      margin-top: 10px;
    } 
  }



  /* 경매결과 - 텝 */
  &.tab-auction_result-section {}
  
  /* 경매결과 - 리스트 */
  &.auction_result_list-section {
    margin-top: 0;
    @media all and (max-width:$G-wMobile)  {
      margin-top: 0;
    }
  }

  /* 제로경매 - 리스트 */
  &.zerobase_list-section {
    margin-top: 70px;
    @media all and (max-width:$G-wMobile)  {
      margin-top: 0;
    }
  }

  /* 제로경매 - view */
  &.zerobase_view-section { 
    margin-top: 70px; margin-bottom: 115px;
    @media all and (max-width:$G-wMobile)  {
      margin-top: 0;  margin-bottom: 35px;
    }
  }

}





/* =============================== 페널이름 */
.content-panel{
  /* =====진행,예정 경매 리스트===== */
  &.type_panel-auction_list { 
    @media all and (max-width:$G-wMobile)  {
    }
  }
  /* ===경매 리스트=== */
  .auction-list {
    li {
      .li-inner { padding: 40px 0; border-bottom: 1px solid #ccc; }
      @media all and (max-width:$G-wMobile)  {
        .li-inner { padding: 30px 0;}
      }
    }

    /* 리스트 아이템 */
    .item-article { display: flex; justify-content: space-between; flex-direction: row-reverse;
      .typo-area  { width: calc( 100% -280px); }
      .image-area { width: 280px; .img-align { background-color: #ededed;}}
      @media all and (max-width:$G-wMobile)  { display: block; justify-content:normal;
        .typo-area  { width: 100%; }
        .image-area { width: 100%; max-width: 210px;
          margin: 0 auto;
          margin-bottom: 20px; 
        }
      }

      .product_info {
        .state-box  {
          span.type-online { border: solid 1px #3e9ccc; color:#3e9ccc; }
          span.type-live   { border: solid 1px #10a68f; color:#10a68f; }
          span.type-d_day  { border: solid 1px #333; color:#fff; background-color: #333;}
          span { display: inline-block; padding: 0 10px;
            border: solid 1px #3e9ccc;
            font-size: 16px; font-weight: bold; letter-spacing: .4px; line-height: 19px; color:#3e9ccc;
          }
        } 
        .title-box  { margin-top: 10px;
          span { font-size: 26px; font-weight: 600; color:$colorB2; }
        }
        .info-box   { margin-top: 20px;
          dl { position: relative; padding-left: 85px;}
          dt, dd { font-size: 18px; line-height: 1.78;}
          dt { position: absolute; left: 0; padding-right: 10px; margin-right: 10px; font-weight: 600; width: 75px;
            &::before { content: ""; position: absolute; margin-top: 9px; right: -1px;
              width: 1px; height: 14px; background-color: #ccc;
            }
            color:$colorB2;
          }
          dd {  color:$colorD; }
        }
        .btn-box    { margin-top: 20px;
          .btn { width: 170px;
            &:disabled {  color: $colorS; background-color: #e4e4e4; border-color: #e4e4e4; }
          } 
        }
      }
      @media all and (max-width:$G-wMobile)  {

        .product_info {
          .state-box  {
            span { display: inline-block; padding: 0 8px;
              font-size: 11px; font-weight: bold; letter-spacing: 0; line-height: 13px;
            }
          } 
          .title-box  { margin-top: 5px;
            span { font-size: 18px; }
          }
          .info-box   { margin-top: 10px;
            dl { padding-left: 50px;}
            dt, dd { font-size: 13px; line-height: 1.69;}
            dt { padding-right: 8px; margin-right: 8px;
              &::before { margin-top: 5px; height: 11px; }
            }
          }
          .btn-box    { margin-top: 15px;
            .btn { width: 100%; } 
          }
        }
      }

    }
  }

  /* 작품응찰신청 */
  &.type_panel-proceeding_bidding { 
    .panel-header { position: relative;
      .title { 
        span { font-size: 32px; font-weight: 600; color:$colorB2; }
      }
    }
    .panel-footer { padding-top: 30px; 
      .button-area {
        .btn_set{ justify-content: center;
          .btn { width: 230px; margin: 0 5px;}
        }
      }
    }

    @media all and (max-width:$G-wMobile)  {
      .panel-header { padding: 30px 0;
        .title { 
          span { font-size: 22px;}
        }
        .guide_box { top:35%;}
      }
      .panel-footer { padding-top: 0px; 
        .button-area {
          .btn_set{ 
            .btn { width: calc(50% - 5px); }
            .btn:first-child { margin-left: 0;}
            .btn:last-child { margin-right: 0;}
          }
        }
      }
    }


    .form_table_bidding { display: flex; flex-wrap: wrap; border-top: 1px solid #999; 
      > li { width: 100%; flex-direction: column; padding:30px 0; border-bottom:1px solid #cccccc;
        &:last-child { border-bottom:0;}
        &.no-line { border-bottom:0; padding-bottom:0;}
        .list_tit { color:$colorB2; margin-bottom: 25px; }
     
        .br-mo { display:none;}
        @media all and (max-width:$G-wMobile)  { padding:25px 0 25px;
          .br-mo { display:block;}
          .list_tit { color:$colorB2; margin-bottom: 10px; }
        }


        //안내및비용
        .product_info-area { display: flex;  justify-content: space-between;
          .guide, .member_pay { display: flex; flex-direction: column; justify-content: center;}
          .guide { border-right: 1px solid #ccc; 
            width: per(920px, 1320px);  padding-right: per(50px, 1320px);
            .product-box { display: flex; flex-wrap: nowrap; align-items: center;
            }
            .img { width: 136px; height: 136px; min-width: 136px;
              margin-right: 25px; 
              background: #f6f6f6;
              text-align: center;
              img {height: 100%;}
            }
            .product_info { 
              .num  .tb1 { color:$colorB2; }
              .name .tt4 { color:$colorB2; }
              .desc .tb1 { color:$colorB2; }
            }
          }
          .member_pay { width: per(400px, 1320px);
            padding-left: per(50px, 1320px);
            p {  display: flex; ; }
            .tit, .won { color:$colorB2; font-weight: 600; }
            .tit { width: 85px; font-size: 18px; line-height: 34px;  }
            .won { width: auto; text-align: right; 
              display: block; 
              font-size: 26px; 
              i { margin-left: 3px; color:$colorS; }
            }
          }
          @media all and (max-width:$G-wMobile)  {
            flex-wrap: nowrap;  flex-direction: column;
            .guide { border-right:0px; border-bottom:1px solid #ccc;; padding-bottom:20px; margin-bottom:20px; width: 100%; padding-right: 0;
              .img { width: 96px; height: 96px; min-width: 96px; margin-right: 15px;}
            }
            .member_pay {  padding: 0 10px; width: 100%;
              p { padding: 0; }
              .tit { width: 65px; }
              .won { margin-top: 5px; margin-left: 0px; 
                width: calc(100% - 65px);
                font-size: 20px; 
              }
            }
          }
        }

        //응찰방법
        .bidding-area { display:flex; flex-direction: column; line-height: 0;
          .btn_radios { display: flex;
            .btn_item + .btn_item{ margin-left: 10px;}
            .btn_item{
              &.active {
                .btn { border-color: $colorMain; span {  color:$colorP;  } }
              }
            }
          }
          .gray-box { margin-top: 30px;
            .list-box.scroll-type { max-height: 470px;}
          }
          @media all and (max-width:$G-wMobile)  {
           .btn_radios { width: calc( 100% + 5); margin-left: -2.5; margin-top: 5px;
              .btn_item { position: relative;
                 width: 33.3333%; padding: 0 2.5px; }
              .btn_item + .btn_item{ margin-left: 0px;}
              .btn { height: 60px; padding: 8px 4px; width: 100%;
                span { font-size: 14px; line-height: 1.3;}
              }
            }
            .gray-box .list-box.scroll-type { max-height: 385px;}
          }
        }

        // 회원 정보 확인
        .contact-area   {
          .gray-box {  padding:35px 40px; }
          .check-box { margin-top: 30px;
            .notice { font-size: 18px; font-weight: 600; color:$colorB2}
            .checkbox-box { margin-left: 30px;}
            label { font-size: 18px; font-weight: 600; color:$colorB2 }
          }
          @media all and (max-width:$G-wMobile)  {
            .gray-box  {  padding:15px 20px; }
            .check-box { margin-top: 20px; display: flex; justify-content: space-between;
              .notice { font-size: 14px; }
              .checkbox-box { margin-left: 0px;}
              label { font-size: 14px;  }
            }
          }
        }

        // 약관
        .terms-area {
          .all_check{ display: flex; justify-content: space-between;
            label { font-size: 20px; font-weight: 600; color:$colorB2;}
            em { font-size: 20px; font-weight: 500; color:$colorP;}
            button { font-size: 0; 
              &.on { i { @include rotate(180deg)}}
            }
          }
          .gray-box { margin-top: 15px; }
          .trp_acitem +  .trp_acitem { margin-top: 15px; }
          .trp_acitem {
            .checkbox-box { width: 100%;}
            label { font-size: 18px; color:$colorD;  margin-right: 10px;}
            .link_target { display: inline-block; 
              font-size: 18px; color:$colorB2;
              text-decoration: underline;
            }
          }
          @media all and (max-width:$G-wMobile)  {
            .all_check{
              label { font-size: 15px; }
              em { font-size: 15px; }
            }
            .gray-box { margin-top: 15px; }
            .trp_acitem +  .trp_acitem { margin-top: 10px; }
            .trp_acitem {
              .checkbox-box { width: 100%;}
              label { font-size: 14px; line-height: 1.5;}
              .link_target { font-size: 14px; line-height: 1.5; }
            }
          }
        }

        .gray-box { background: #f6f6f6; padding:25px 40px;
        
          .title { 
            span{font-size: 18px; color:$colorB2; font-weight: 600;
              line-height: 1.78;
            }
          }
          .price_mark { display: flex;  align-items: center;
            padding-right: 170px; position: relative;
            .select-box { width: 260px;  margin-left: 20px;
              select { text-align: center;}
              .select2-selection__rendered { text-align: center;}
            }
            .price { padding-left: 20px;
              span { font-size: 18px; line-height: 1.5; } 
            }
            .unit  { position: absolute; top:15px; right: 0px;
              span { font-size: 16px; line-height: 1.5;}
            }
          }
          .dtdd-list + .dtdd-list { margin-top: 10px;}
          .dtdd-list { position: relative; display: flex; 
            dt { width: 100px;}
            dd { width: calc(100% - 100px);
              span { font-weight: 600; }
            }
            span { font-size: 18px; color:$colorB2; line-height: 1.7;}
          }
          @media all and (max-width:$G-wMobile)  {  padding:20px 20px;
            .title { margin-bottom: 10px;
              span{font-size: 14px; line-height: 1.5; }
            }
            .price_mark { display: block; padding-right: 0;
              .select-box { width: 100%; margin-left: 0;}
              .price { margin-top: 10px; padding-left: 0; width: 100%; 
                span { font-size: 14px; }
              }
              .unit  { position: relative; top:auto; right: auto;
                margin-top: 20px;
                span { font-size: 13px; line-height: 1.5;}
              }
            }
            .dtdd-list + .dtdd-list { margin-top: 5px;}
            .dtdd-list {  
              dt { width: 75px;}
              dd { width: calc(100% - 75px); }
              span { font-size: 14px; line-height: 1.5;}
            }
          }

        }

        .help-box { position: relative;
          margin-top: 25px; padding-left: 30px;
          i { position: absolute; top:5px; left: 0;}
          ul {
            span, em { font-size: 18px; line-height: 32px;}
            em   { color:$colorP; font-weight: 600; }
          }
          @media all and (max-width:$G-wMobile)  {
            margin-top: 18px; padding-left: 20px;
            i { position: absolute; top:2px; left: 0;}
            ul {
              span, em { font-size: 14px; line-height: 21px;}
            }
          }
        }

      }
    
     
    }
    

  }


  /* 카운터 */
  &.type_panel-counter{
    .panel-header { padding: 30px 0;
      .title { font-size: 32px; font-weight: bold; color:$colorB; text-align: center; } 
    }
    .panel-body   { margin-top: 20px; }
    .panel-footer { margin-top: 70px; 
      .tac { .btn{width: 170px;} }
    }
    @media all and (max-width:$G-wMobile)  {
      .panel-header { padding: 30px 0;
        .title { font-size: 22px;  } 
      }
      .panel-body   { margin-top: 0px; }
      .panel-footer { margin-top: 35px; 
        .tac { .btn{width: 175px;} }
      }
    }

    
    .countdown-article {
      .trp_counter-area { margin-top: 30px;
        display: flex; justify-content: center; 
        .dot { position: relative; width: 30px; height: 180px; 
          &::before , &::after {content: ""; display: block;
            position: absolute; top:50%; left: 50%;
            @include transform( translate(-50%, -50%));
            width: 6px; height: 6px; border-radius: 50%;
            background-color: #21a099;
          }
          &::before { margin-top: -6px; }
          &::after  { margin-top: 6px; }
        }
      }
      @media all and (max-width:$G-wMobile)  {
        .trp_counter-area { margin-top: 25px;
          .dot { position: relative; width: 18px; height: 90px; 
            &::before , &::after {content: ""; display: block;
              position: absolute; top:50%; left: 50%;
              @include transform( translate(-50%, -50%));
              width: 4px; height: 4px; border-radius: 50%;
              background-color: #21a099;
            }
            &::before { margin-top: -4px; }
            &::after  { margin-top: 4px; }
          }
        }
      }
    }

    .schedule-article { text-align: center;
      .schedule_name { font-size: 42px; font-weight: 600; color:$colorB; line-height: 60px; }
      .info-list { display: flex; justify-content: center;  margin-top: 20px;
        li { position: relative; font-size: 20px; font-weight: 500; 
          em { font-weight: 600; color:$colorB;}
          span { margin-left: 15px;}
        }
        li + li {  margin-left: 30px; padding-left: 30px;
          @include list-div_bar(5px, 0px, 18px, #ccc ); 
        }
      }
      @media all and (max-width:$G-wMobile)  {
        .schedule_name { font-size: 25px; line-height: 1.5; }
        .info-list { display: block; justify-content: initial;  
          width: 280px; margin: 20px auto 0;
          li { font-size: 14px; text-align: left; display: flex;
            em { min-width: 50px;}
            span { margin-left: 15px;}
          }
          li + li {  margin-left: 0px; padding-left: 0px;
            margin-top: 10px;
            &::before { display: none;}
          }
        }
      }
    }

    /* ===== countdown ==== */
    .countdown-article {
      .trp_counter-area{
        /* Skeleton */
        ul.flip {
          position: relative; float: left; margin: 5px; width: 160px; height: 90px;
          font-size: 80px; font-weight: bold; line-height: 87px;
          border-radius: 6px; box-shadow: 0 2px 5px rgba(0, 0, 0, .7);
          li { z-index: 1;
            position: absolute; left: 0; top: 0;
            width: 100%; height: 100%;
            &:first-child { z-index: 2; }
            .li-box { display: block; height: 100%; perspective: 800px; 
              div { z-index: 1;
                position: absolute; left: 0;
                width: 100%; height: 50%;
                overflow: hidden;
                .shadow { position: absolute; width: 100%; height: 100%; z-index: 2; }
                &.up { transform-origin: 50% 100%; top: 0; 
                  div.inn { top: 0; } 
                }
                &.up:after { content: ""; position:absolute; top:44px; left:0; z-index: 5;
                  width: 100%; height: 3px; background-color: rgba(0,0,0,.4);
                }
                &.down { transform-origin: 50% 0%; bottom: 0; 
                  div.inn { bottom: 0; }    
                }
                div.inn { position: absolute; left: 0; z-index: 1;
                  width: 100%; height: 200%;
                  color: #ccc; text-shadow: 0 1px 2px #000; text-align: center;
                  background-color: #333; border-radius: 6px;
                }
              }
            }
          }
          li.on{ z-index: 4;} 
        }
        &.type-3,  &.type-day{
          ul.flip  { width: 180px; height: 180px; box-shadow: initial; 
            margin: 0;
            li {
              div.up{
                &:after { 
                  top:calc(100% - 1px); height: 1px;
                  background-color: rgba(0, 0, 0, 0.07);
                }
              }
              div.down{
                div.inn {  color:#4db4af; background-color: #f9f9f9;}
              }
              div div.inn { font-size: 110px; font-weight: 600; color:#00948c;
                line-height: 180px; 
                border: 1px solid #ccc; border-radius: 0;
                text-shadow: 0 1px 1px #006666;
                background-color: #f6f6f6;
              }
            }
          }
          @media all and (max-width:$G-wMobile)  {
            ul.flip  { width: 90px; height: 90px;
              li {
                div div.inn { font-size: 55px; line-height: 90px; }
              }
            }
          }
        }
        &.type-day  {
          ul.flip  { width: 473px; }
          @media all and (max-width:$G-wMobile)  {
            ul.flip  { width: 245px; }
          }
        }
  
      }
      &.play{
        .trp_counter-area{
          ul li{
            &.before {z-index: 3;
              .up { z-index: 2; animation: turn2 .5s linear both; }
            }
            &.active { animation: asd .5s .5s linear both; z-index: 2; 
              .down { z-index: 2; animation: turn .5s .5s linear both; }
            }
          }
          ul li.before .up .shadow {
            background: -moz-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, .1)), color-stop(100%, rgba(0, 0, 0, 1)));
            background: linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            background: -o-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            background: -ms-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            background: linear-gradient(to bottom, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            animation: show .5s linear both;
          }
          ul li.active .up .shadow {
            background: -moz-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, .1)), color-stop(100%, rgba(0, 0, 0, 1)));
            background: linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            background: -o-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            background: -ms-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            background: linear-gradient(to bottom, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
            animation: hide .5s .3s linear both;
          }
          ul li.before .down .shadow {
            background: -moz-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 1)), color-stop(100%, rgba(0, 0, 0, .1)));
            background: linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            background: -o-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            background: -ms-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            background: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            animation: show .5s linear both;
          }
          ul li.active .down .shadow {
            background: -moz-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 1)), color-stop(100%, rgba(0, 0, 0, .1)));
            background: linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            background: -o-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            background: -ms-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            background: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
            animation: hide .5s .3s linear both;
          }
        }
      }
    }

    /* SHADOW DOWN*/
    @keyframes asd {
      0% { z-index: 2; }
      5% { z-index: 4; }
      100% { z-index: 4; }
    }
    @keyframes turn {
      0% { transform: rotateX(90deg); }
      100% { transform: rotateX(0deg); }
    }
    @keyframes turn2 {
      0% { transform: rotateX(0deg); }
      100% { transform: rotateX(-90deg); }
    }

    @keyframes show {
      0% { opacity: 0; }
      100% { opacity: 1; }
    }
    @keyframes hide {
      0% { opacity: 1; }
      100% { opacity: 0; }
    }


  }

  /* 경매결과 - 텝 */
  &.type_panel-result_tab {
    > .panel-header { padding: 20px 0 0;}
    @media all and (max-width:$G-wMobile) {
      > .panel-header { padding: 25px 0 0;}
    }
    .search_tab-article {padding: 40px 0 30px;
      .article-body{ display: flex; justify-content: space-between; flex-direction: row-reverse;
        .col1 { width: calc( 100% - 250px);}
        .col2 { width: 275px;}
      }
      @media all and (max-width:$G-wMobile)  {
        padding: 30px 0 0;
        .article-body{ 
          flex-direction: column; justify-content: initial; 
          .col1, .col2 { width: 100%; }
          .col1 { margin-top: 5px;}
        }
      }
      .count-box { 
        font-size: 18px; font-weight: 600; line-height: 42px;
        span { color: $colorB;}
        em   { color: $colorP;}
        @media all and (max-width:$G-wMobile)  {
          font-size: 14px; line-height: 43px;
        }
      }
    }
  }

  /* 경매결과 - 리스트 */
  &.type_panel-product_result_list {
    position: relative;
    .product-list { display: flex; flex-wrap: wrap;
      width: calc(100% + 20px); margin-left: -10px;
      li { width: 50%; padding: 0 10px; margin-bottom: 50px;
        .li-inner { border: 1px solid #d3d3d3; }
      }
      @media all and (max-width:$G-wMobile)  {
        width: 100%; margin-left: 0px;
        li { width: 100%; padding: 0; margin-bottom: 20px; }
      }
    }
    .item-article {
      .image-area{
        .img-ratio { padding-bottom: 56.1538%; /* 56.25 */}
      }
      .product_info { position: relative;}
      .typo-area { padding: 35px 40px 40px;
        .title-box { 
          .title { font-size: 26px; font-weight: 600; color:$colorB2;}
        }
        .info-box{ margin-top: 10px; width: calc(100% - 170px);
          dl { font-size: 18px; display: flex;}
          dl + dl { margin-top: 5px;}
          dt { font-weight: 600; color:$colorB2; min-width: 88px; }
          dd { font-weight: 500; margin-left: 10px; padding-left: 10px;
            position: relative; 
            @include list-div_bar( 6px , 0, 14px, #ccc)
          }
        }
        .btn-box{ margin-top: 0; position: absolute; right:0; bottom:0;
          .btn { width: 160px;}
        }
      }
      @media all and (max-width:$G-wMobile)  {
        .typo-area { padding: 15px 20px 20px;
          .title-box { 
            .title { font-size: 18px; }
          }
          .info-box{ margin-top: 15px; width: auto;
            dl { font-size: 13px; }
            dl + dl { margin-top: 5px;}
            dt { width: 64px;}
            dd { margin-left: 8px; padding-left: 8px;
              @include list-div_bar( 3px , 0, 12px, #ccc)
            }
          }
          .btn-box{ margin-top: 25px; position: static;
            .btn { width: 100%;}
          }
        }
      }
    }
    .panel-footer {
      .set-pc_mb { margin-top: 10px; 
        .paging-area { margin-top: 0;}
      }
      @media all and (max-width:$G-wMobile)  {
        .set-pc_mb { margin-top: 10px;
          .only-mb { text-align: center;
            .btn { width: 160px; }
          }

        }
      } 
    }
  }

  /* 제로베이스 - 리스트 */
  &.type_panel-zerobase_list {
    .panel-header { display: flex; justify-content: space-between;
      padding: 30px 0;
      .title {span{ font-size: 26px; font-weight: bold; color:$colorB2; line-height: 42px;}}
      .btn_set { position: relative; width: 320px; 
        .select-box { width: 100%;}
      }
      @media all and (max-width:$G-wMobile)  { display: block; justify-content: initial;
        .title {span{ font-size: 18px; line-height: 36px;}}
        .btn_set { width: 100%; margin-top: 10px;
          .select-box { width: 100%;}
        }
      }
    }
    .panel-footer { 
      .only-mb { text-align: center;
        .btn { width: 160px;}
      } 
    }

    .author-list { display: flex; flex-wrap: wrap;
      width: calc(100% + 20px); margin-left: -10px;
      li { width: 25%; padding: 0 10px; margin-bottom: 30px;
        .li-inner { border: 1px solid #ccc;}
      }
      @media all and (max-width:$G-wMobile)  { width: 100%; margin-left: 0px;
        li {width: 100%;  padding: 0;}
      }

      .item-article {
        .img-area  {}
        .typo-area { padding: 15px 20px;
          .info-box{
            .name { 
              span { font-size: 22px; font-weight: bold; color: $colorB2; line-height: 1.6; }
              .sub { font-size: 18px; font-weight: 500; color: $colorB2;  line-height: 1.6; }
            }
            .name_eng { span { font-size: 18px; font-weight: 500; color:$colorB2;  line-height: 1.6; }}
          }
          .btn-box { margin-top: 10px;
            .btn_set { width: 100%; .btn { width:100%}}
          }
        }
        @media all and (max-width:$G-wMobile)  {
          .typo-area { 
            .info-box{
              .name { 
                span { font-size: 16px; line-height: 1.5; }
                .sub { font-size: 14px; line-height: 1.5; }
              }
              .name_eng { span { font-size: 14px; line-height: 1.5; }}
            }
            .btn-box { margin-top: 10px;
              .btn_set { width: 100%; }
            }
          }
        }
      }

    }





  }

  /* 제로베이스 - info */
  &.type_panel-zerobase_view {
    .autho_info-article { position: relative; display: flex; flex-wrap: wrap;
      .image-area { width: per( 650px, 1320px ) }
      .typo-area  { position: relative; width: per(670px, 1320px); 
        padding-left: per(130px, 1320px); 
        .author_info { overflow: hidden;
          display: flex; flex-direction: column;
           /*  height: 100%;  justify-content: center */
        }
        &.ot_more{  .more-box {display: block;}           
          &.ot_close { .author_info { height: 560px;} }
        }
      }
      @media all and (max-width:$G-wMobile) { display: block; flex-wrap: nowrap; margin-top: 40px;
        .image-area { width: 100%; }
        .typo-area  { width: 100%; padding-left: 0; margin-top: 25px; 
          &.ot_more{  .more-box {display: block;} 
            &.ot_close { .author_info { height: 510px;} 
            }
          }
        }
      }

      .info-box { padding-top: 15px;
        .category { font-size: 26px; font-weight: 600; color:$colorP;}
        .name     { 
                    font-size: 42px; font-weight: 600; color:$colorB2;}
        .name_eng { font-size: 24px; font-weight: 500; color:$colorS;}
        .year     { margin-top: 15px; 
                    font-size: 24px; font-weight: 500; color:$colorS;
        }
        .sns_info { margin-top: 30px; font-size: 0;
          a {display: inline-block; margin: 0 5px;}
        }
      }
      .desc-box { margin-top: 35px; padding-top: 30px; border-top: 1px solid #ccc;
        .desc { font-size: 18px; font-weight: 500; line-height: 1.6; 
          p + p { margin-top: 30px;}
        }
        .link_line { text-decoration: underline; }
        strong  { font-weight: bold; }
        .video_img-box { position: relative; display: flex; flex-wrap: wrap;
          width: calc(100% + 20px); margin-left: -10px;
          a { position: relative; display: block; width: 50%;
            padding: 10px;
          }
          img { width: 100%;}
        }
      }
      .more-box { display: none; text-align: center; margin-top: 40px;
        .btn { width: 140px; 
          i { width: auto; margin-left: 10px;} 
          span { color:$colorD; }
          &.on { i{ @include rotate(180deg); } }
        }
      }
      @media all and (max-width:$G-wMobile)  {
        .info-box {  
          .category { font-size: 18px;    font-weight: bold;}
          .name     { margin-top: 15px;   font-size: 27px; }
          .name_eng { margin-left: 15px;  font-size: 16px;}
          .year     { margin-top: 10px; 
            font-size: 16px;
          }
          .sns_info { margin-top: 20px;
            a {display: inline-block; margin: 0 4px;}
          }
        }
        .desc-box { margin-top: 20px; padding-top: 15px;
          .desc   { font-size: 15px;  line-height: 1.5; 
            p + p { margin-top: 30px;}
          }
          .link_line { text-decoration: underline; }
          strong  { font-weight: bold; }
          .video_img-box { position: relative; display: flex; flex-wrap: wrap;
            width: calc(100% + 20px); margin-left: -10px;
            a { position: relative; display: block; width: 50%;
              padding: 10px;
            }
            img { width: 100%;}
          }
        }
        .more-box {
          .btn { width: 115px; }
        }
      }

    }
  }
  
  /* 제로베이스 - product list */
  &.type_panel-zerobase_product_list { border-top: 1px solid #ccc;
    .panel-header { display: flex; justify-content: space-between;
      padding:  30px 0;
      .total { font-size: 18px; font-weight: 600; color:$colorB2; 
        span  { line-height: 42px;}
        em    { line-height: 42px; color:$colorP;}
      }
      .btn_set { .select-box{width: 140px;} }
      @media all and (max-width:$G-wMobile)  {
        .total { font-size: 15px; 
          span  { line-height: 43px;}
          em    { line-height: 43px;}
        }
      }
    }
    .panel-body   {}
    .panel-footer {
      @media all and (max-width:$G-wMobile)  {
        .only-mb { text-align: center;
          .btn { width: 160px;}
        }
      }
    }


    .product-list { position: relative; width: calc( 100% + 20px); margin-left: -10px;
      display: flex; flex-wrap: wrap;
      li { width: 20%; padding: 0 10px; padding-bottom: 25px; 
        .li-inner { border: 1px solid #ccc; }
      }
      @media all and (max-width:$G-maxWidth)  {
        li { width: 25%; }
      }
      @media all and (max-width:$G-wMobile)  { width: 100%; margin-left: 0;
        li { width: 100%;  padding: 0 0px; margin-bottom: 30px;}
      }
    }
    .item-article { 
      .image-area { position: relative;
        .img-ratio { background-color: #f6f6f6;
          .img-align { padding: 5px;}
        }
        img {width: 100%;}
        .typo-align { position: absolute; top:0; width: 100%; height: 100%;}
        .category_name { 
          display: flex; align-content: center; flex-wrap: wrap; justify-content: center; flex-direction: column;
          width: 100%; height: 100%;  vertical-align: middle;
          font-size: 28px; font-weight: 500; color:#fff;
        }
      }
      .typo-area { padding: 0px 20px;
        > div { position: relative; }
        .num_heart-box{ display: flex; justify-content: space-between; padding: 18px 0 0px; }
        .info-box {  padding: 15px 0 15px; }
        .standard-box {}

        .num      { font-size: 18px; font-weight: 600; color:$colorB2 ; line-height: 30px; }
        .heart    { width: 30px; height: 30px; 
          &.on i {  &::after {  @include sp_icons($icon-heart_on) } }
          i { margin-right: -3px; }
        }
        .title    { font-size: 22px; font-weight: bold; color:$colorB2; letter-spacing: 0;}
        .sub      { font-size: 18px; font-weight: 500;  color:$colorB2; letter-spacing: 0;}
        .desc     { margin-top: 3px; font-size: 18px; font-weight: 500; color:$colorB2;letter-spacing: 0px; 
          span { display: block; @include ellipsis(); }
        }
        .standard {  margin-top: 3px; 
          span { font-size: 16px; font-weight: 500; color:$colorS; line-height: 1.5; }
          .material  { display: block; @include ellipsis(); }
          .size_year { 
            span { position: relative; display: inline-block;  }
            span + span { margin-left: 10px; padding-left: 10px; @include list-div_bar(6px, 0, 12px, #bbb); }
          }
        }
        
      }
      .product_cancle-area { display: none; position: absolute; top:0; left: 0;
        width: 100%; height: 100%; background-color: #f6f6f6; 
        .area-inner  {  display: flex; flex-direction: column; justify-content: center; align-items: center;
          height: 100%; text-align: center;
        }
        .typo { margin-top: 5px;
          font-size: 18px; font-weight: 500; color:$colorS; line-height: 1.64;
          span {}
        }
      }
      @media all and (max-width:$G-wMobile)  {
        .typo-area { padding: 0 20px;
          > div { position: relative; }
          .num_heart-box{ display: flex; justify-content: space-between; padding: 14px 0 0px; }
          .info-box {  padding: 0px 0 15px; }

          .num      { font-size: 14px; line-height: 30px; }
          .heart    { width: 30px; height: 30px; i { margin-right: -5px;}
              &.on i {  &::after {  @include sp_icons2x($icon-heart_on-2x) } }
          }
          .title    { font-size: 16px; line-height: 30px;}
          .sub      { font-size: 14px; }
          .desc     { margin-top: 5px; font-size: 14px; letter-spacing: -1px; }
          .standard { margin-top: 5px; 
            span { font-size: 13px; line-height: 1.5; }
            .size_year { 
              span { position: relative; display: inline-block;  }
              span + span { margin-left: 10px; padding-left: 10px; @include list-div_bar(5px, 0, 10px, #bbb); }
            }
          } 
        }
      }
    }



  }

}