
/* 섹션 이름  (언론보도, 채용공고, 공지사항)*/
.basis-section{
  /* 게시판 섹션 */
  &.bbs-section { margin-top: 100px;
    /* 공지사항 게시판 섹션 수정 */
    &.bbs-media-section { margin-top: 0;
      .panel-header { padding-top: 0; padding-bottom: 80px; }
    }
    /* 게시판 탑 */
    .bbs-top { display: flex; justify-content: space-between;
      .bbs_count-area { font-weight: 600; height: 42px; line-height: 42px; font-size: 18px;
        .point { color: $colorP; }
      }
      .bbs_search-area {
        .bbs_search-slctbox { width: 150px; }
        .bbs_search-box { display: flex; border: 1px solid #B2B2B2;
          input { width: calc(100% - 38px); height: 40px; border: 0; }
          input::placeholder { font-size: 16px; }
          .bbs_search-btn {
            width: 38px; height: 40px; padding: 0; font-size: 0; cursor: pointer; 
            background: url(/images/pc/icon/ic_search_black_24.svg) no-repeat center center / 21px 21px; 
          }
        }
      }
    }
    /* 게시판 테이블 */
    .table-wrap { margin-top: 25px;
      .data-empty { border-top: 1px solid $colorL; border-bottom: 1px solid #ccc; padding: 300px 0; font-size: 18px; color: $colorD; line-height: 1;
        .txt_empty { font-weight: 500; }
      }
    }
    .data-table {
      width: 100%;
      thead { display: none; }
      tr { border-bottom: 1px solid #ccc;}
      th, td { height: auto; }
      td { padding: 50px 30px; color: $colorD; border: 0; }
      .bbs-subject { font-size: 24px; text-align: left; font-weight: 600; color: $colorB; @include ellipsis-line(2);
        .tit, span { @include ellipsis-line(2); color: $colorB; }
      }
      .bbs-media, .bbs-date { font-size: 18px; white-space: pre-line; font-weight: 500; }
      .bbs-date { width: 180px; }
      .icon_down { margin-left: 40px; vertical-align: text-top; }
      /* 언론보도 게시판 테이블 */
      &.bbs-table1 {
        td { padding: 40px 30px;}
        .bbs-subject {
          .bbs-media { display: block; margin-bottom: 8px; color: $colorD; }
          .tit { color: $colorB; }
        }
      }
      /* 채용공고 게시판 테이블 */
      &.bbs-table2 {
        td { padding: 30px; height: 120px; }
        .bbs-date.long { width: 300px; white-space: nowrap; }
        .bbs-subject {
          display: flex;
          align-items: center;
          span { vertical-align: middle;}
        }
        .icon-wrap { margin-left: 20px; vertical-align: middle; display: flex; align-items: center; }
        .mem-icon { width: 55px; height: 22px; line-height: 20px; font-size: 14px; font-weight: 700; text-align: center; border: 1px solid;
          &.icon-new { border-color: #3e9ccc; color: #3e9ccc;}
          &.icon-senior { border-color: $colorP; color: $colorP;}
          &.icon-recruiting { border-color: $colorB2; background-color: $colorB2; color: #fff; }
        }
        .mem-icon + .mem-icon { margin-left: 10px; }
      }
      /* 공지사항 게시판 테이블 */
      &.bbs-table3 {
        .bbs-division { width: 190px; }
        .bbs-subject { display: flex; align-items: center; justify-content: space-between;
          .tit { width: calc(100% - 30px);}
          .icon_down{ width: 30px;}
        }
      }
    }
    /* 페이징 */
    .paging-area { margin-top: 50px; }
    /* 상세보기 공통 */
    .view-header { border-top: 1px solid $colorL; background-color: $colorBGw; padding: 25px 40px; display: flex; justify-content: space-between; align-items: center;
      > .title-area {
        .title { font-size: 24px; font-weight: 700; color: $colorB; line-height: 1.36; }
        .desc { margin-top: 0;  font-size: 18px; color: $colorD; }
      }
      > .date-area { font-size: 18px; font-weight: 700; }
    }
    .view-header + .file-area { padding: 25px 40px; border-bottom: 1px solid #ccc; font-size: 18px; line-height: 1.5;
      .file-item {
        a{
          > i { vertical-align: sub; }
          > i + span { margin-left: 5px;}
        }
      }
    }
    .view-body { margin-top: 60px; padding: 0 40px;
      .view_editor-wrap { font-size: 18px; line-height: 1.5; padding-bottom: 60px; }
    }
    .view-footer { border-top: 1px solid #ccc;
      .button-area { padding: 0; margin-top: 60px; }
    }
    /* 공지사항 리스트 */
    .type_panel-notice {
      .bbs-top .bbs_search-area { display: block;
        .bbs_search-box { margin-left: 0; }
      }
    }
    /* 공지사항 상세보기 */
    .type_panel-notice_view {
      .panel-header { padding: 0; 
        .view-header { padding: 30px 40px; min-height: 120px;
          > .date-area { position: relative;}
          > .date-area::before { display: block; content: ""; position: absolute; width: 1px; height: 60px; left: -40px; top: 50%; transform: translateY(-50%); background-color: #ccc; }
        }
      }
      @media all and (max-width:$G-wMobile) {
        .panel-header {
          .view-header { padding: 25px 20px; min-height: auto;
            > .date-area::before { display: none; }
          }
        }
      }
    }
    /* 채용공고 상세보기 */
    .type_panel-incruit_view {
      .panel-header { padding: 0; }
      .incruit_view-header {
        .title-area { display: flex; flex-direction: column; justify-content: center; }
        .button-area {
          .btn { width: 170px; }
          .btn + .btn { margin-left: 5px; }
          .btn span + i { width: auto; }
        }
      }
    }
    /* 채용공고 지원하기 */
    .type_panel-board {
      .panel-header { padding: 0 0 10px;
        .desc .tb1 { line-height: inherit; }
      }
      .incruit_apply-table { border-top: 1px solid $colorL;
        th { width: 165px; font-size: 20px; background: none; }
        input[type="text"], input[type="email"] { width: 455px; }
        .file-btn { width: 170px; }
        .mark_list-wrap { margin-top: 10px; }
        .trp.file-box { width: 170px;
          // (from: _form.scss)
          label.screen-reader-text { position:absolute; margin:-1px; padding:0; height:1px; width:1px; overflow:hidden; clip:rect(0,0,0,0); border:0; }
          .trp-Filetext { width: calc( 100% - 120px); height: $formH; padding: 8px 10px 8px 10px;
            font-size: 16px; vertical-align: middle;   border: 1px solid $lineC_d; outline: none; 
            -webkit-appearance: none;
            -moz-appearance: none;
          }
          .btn { position: absolute; top:0px; right:0px; min-width: 110px;  height: $formH; /* background:#757575; color:#fff; font-size:16px; font-weight: 300; */
            -webkit-appearance: none;
            -moz-appearance: none;
          }
          .trp-Filehidden { position: absolute; top:0; left:0; width:100%;  height: $formH;  opacity:0; cursor:pointer; z-index: 1;}

          @media all and (max-width:$G-wMobile)  { 
            .trp-Filetext {  font-size: 14px; padding: 5px 15px 5px; line-height: 22px; height: 43px;} 
            .btn {  font-size: 14px; height: 43px; }
            .trp-Filehidden { height: 43px;}
          }
        }
      }
      .table-wrap.policy {
        margin-top: 80px;
        .trp.checkbox-box label { font-size: 20px; font-weight: 600; }
        .accordion-list > li{
          .header-area .accordion_name { padding: 18px 20px; }
          .con-area { display: block; }
        }
        ol.mark_num-list li { list-style: decimal; margin-left: 18px; font-size: 18px; font-weight: 500; line-height: 28px; }
        ol.mark_num-list li + li { margin-top: 1rem; }
      }
      .button-area { margin-top: 80px;
        .btn + .btn { margin-left: 5px; }
      }
    }
  }
  /* 문의하기 */
  &.bbs-section + .last-section {
    margin-top: 100px;
    .content-panel > .panel-header {
      padding: 0;
    }
  }
  
  @media all and (max-width:$G-wMobile)  {
    &.bbs-section { margin-top: 40px;
      /* 공지사항 게시판 섹션 수정 */
      &.bbs-media-section { padding: 0;
        .panel-header { padding: 0 0 40px 0; }
        .panel-body { padding: 0 25px; }
      }
      /* 게시판 탑 */
      .bbs-top { display: flex; flex-direction: column-reverse;
        .bbs_count-area { width: auto; font-size: 14px; margin-top: 10px; }
        .bbs_search-area { width: auto;
          .bbs_search-slctbox {
            select { font-size: 14px; padding-left: 10px; }
          }
          .bbs_search-box { width: auto;
            input { font-size: 14px; height: 41px; padding: 5px 0 5px 15px; }
            input::placeholder { font-size: 14px; }
            .bbs_search-btn { height: 41px; }
          }
        }
        & + .table-wrap { margin-top: 0;}
      }
      /* 게시판 테이블 */
      .table-wrap .data-empty { padding: 150px 0; font-size: 13px; }
      .bbs-table {
        th, td { height: auto; }
        tr { display: block; position: relative; padding: 25px 0; }
        td { padding: 0; }
        .bbs-media { text-align: left; width: auto; font-size: 13px; }
        .bbs-date { width: auto; font-size: 14px; text-align: left; display: block; margin-top: 8px; }
        .bbs-subject { font-size: 17px; line-height: 1.3; display: block; }
        /* 언론보도 게시판 테이블 수정 */
        &.bbs-table1 {
          td { padding: 0; }
          .bbs-subject {
            .tit { font-size: 17px; line-height: 1.5; }
          }
        }
        /* 채용공고 게시판 테이블 */
        &.bbs-table2 {
          tr { display: flex; flex-direction: column-reverse; }
          td { padding: 0; height: auto; }
          .bbs-subject { order: 1; display: flex; flex-direction: column-reverse; align-items: baseline; padding-top: 0; }
          .bbs-date.long { text-align: left; margin-top: 7px; position: static; width: auto; }
          .icon-wrap { margin-left: 0; margin-bottom: 7px; display: block; }
          .mem-icon { display: inline-block; width: 36px; height: 15px; line-height: 13px; font-size: 11px; }
          .mem-icon + .mem-icon { margin: 0; }
        }
        /* 공지사항 게시판 테이블 */
        &.bbs-table3 {
          tr { display: flex; flex-direction: column; }
          .bbs-subject { margin-top: 8px; }
          .bbs-division { width: auto; text-align: left; }
        }
      }
      /* 페이징 */
      .paging-area { margin-top: 30px; }
      /* 상세보기 공통 */
      .view-header { flex-direction: column; padding: 25px 20px;
        > .title-area { width: 100%;
          .title { font-size: 17px; }
          .desc { font-size: 14px; }
        }
        > .title-area + .date-area { width: 100%; padding-top: 5px; font-size: 13px; } 
      }
      .view-header + .file-area { padding: 20px; font-size: 14px; }
      .view-body { margin-top: 25px; padding: 0 20px;
        .view_editor-wrap { font-size: 14px; padding-bottom: 25px; }
      }
      .view-footer {
        .button-area { margin-top: 30px;
          .btn { width: 160px; }
        }
      }
      /* 채용공고 상세보기 */
      .type_panel-incruit_view { margin-top: 40px;
        .incruit_view-header { padding: 25px 20px;
          .button-area { width: 100%; margin-top: 20px;
            .btn { width: calc(50% - 5px); }
            .btn + .btn { margin-left: 5px; }
          }
        }
      }
      /* 채용공고 지원하기 */
      .type_panel-board {
        .panel-header { padding-bottom: 0;}
        .incruit_apply-table { border-bottom: 1px solid #ccc; display: block; margin-top: 0; padding-bottom: 25px;
          tr { display: flex; flex-direction: column; width: 100%; }
          th, td { width: 100%; height: auto; border: 0; padding: 0; }
          th { padding-top: 20px; font-size: 15px; }
          td { padding-top: 5px; }
          input[type="text"], input[type="email"] { width: 100%; }
          .file-btn { width: 160px; }
        }
        .table-wrap.policy { margin-top: 45px;
          .trp.checkbox-box label { font-size: 15px }
          ol.mark_num-list li { font-size: 14px; line-height: 1.5; }
        }
        .button-area {
          margin-top: 30px;
          .btn.btn_lg { width: calc(50% - 5px); }
          .btn.btn_lg + .btn.btn_lg { margin-left: 5px; }
        }
      }
    }
    /* 문의하기 섹션*/
    &.bbs-section + .last-section {
      margin-top: 50px;
    }
  }  
}




/* =============================== footer */
.content-panel{
  
  /* 위치안내 */ 
  &.type_panel-location {

    .location_list_wrap {width:  calc( 100% + 52px); margin-left: -26px;
      .location_list { display:flex; flex-wrap: wrap; margin-top: -52px;
        > li { width: 50%; margin-top: 52px;}
        .list_inner { margin:0 26px; border:1px solid #ccc; height: 100%;
          .location_info { padding: 38px; position: relative; 
            dl { 
              dt { color:#333; margin-bottom: 25px;}
              dd {
                ul {
                  li { color:#555; padding-left: 150px; position: relative; font-weight: 400;
                    b { color:#333; font-weight:500; width: 150px; position: absolute; left:0;}
                  }
                }
              }
            }
            .btn_wrap { position: absolute; right: 38px; top: 28px;
              .btn { width: 140px;}
            }
          }
        }
      }
    }

    @media all and (max-width:$G-wMobile)  {
      .location_list_wrap { width:100%; margin-left: 0;
        .location_list { margin-top: -10px;
          > li { width: 100%; margin-top: 30px;}
          .list_inner { margin:0;
            .img {
              img { width: 100%;}
            }
            .location_info { padding: 25px 20px 20px;
              dl { 
                dt { margin-bottom: 20px;}
                dd {
                  ul {
                    li { padding-left: 80px; margin: 0 5px;
                      b { width: 80px;}
                    }
                  }
                }
              }
              .btn_wrap { right: 20px; top: 20px;
                .btn { width: 115px; }
              }
            }
          }
        }
      }
    }

  }

  //FAQ 
  &.type_panel-faq {

    .faq-area { margin-top: 100px;
      .tab-area { margin-bottom: 80px;}
      .accordion-list {
        &.faq {
          .faq_cont {
            .faq_img  { margin-top: 30px;}

            .btn_wrap { margin-top: 20px;
              a { width: 170px;
                span { color:$colorD;}
              }
            }
          }
        }
      }
    }
    @media all and (max-width:$G-wMobile)  {
      .faq-area { margin-top: 40px;
        .tab-area { margin-bottom: 40px;}
        .accordion-list {
          &.faq {
            .con-area {
              .faq_img  { margin-top: 15px;}
  
              .btn_wrap { margin-top: 15px;
                a { width: 160px;
                  span { font-size: 13px;}
                }
              }
            }
          }
        }
      }
    }
  }

  //1:1문의
  &.type_panel-inquiry { 

    .panel-body {
      .form_table-list {
        li {
          &.file_row { 
            .th, .td { margin-top: 10px}
          }
          .half { width: 455px !important;}

          //파일첨부
          .trp.file-box { width: 170px;
            .btn { border:1px solid #ccc; background:#fff; color:$colorD; font-weight: 600; }
            &:hover .btn { background:#F0F0F0; border-color:#bdbdbd;}
          }
          .dot_list {
            li { color:$colorS}
          }

        }
      }
    }
   
    .panel-footer { padding-top: 60px; 
      .button-area {
        .btn_set{
          &.btn_double { justify-content: center; }
        }
        .btn { width: 170px; margin: 0 4px;}
      }
    }

    @media all and (max-width:$G-wMobile)  {
      .panel-body {
        .form_table-list {
          li {
            &.file_row { 
              .th, .td { margin-top: 0}
            }
            .half { width: 100% !important;}
            //파일첨부
            .trp.file-box { width: 160px;}
          }
        }
      }

      .panel-footer { padding-top: 30px; 
        .button-area {
          .btn_set{
            &.btn_double { justify-content: space-between; 
              .btn { width: calc(50% - 5px);}
            }
          }
          .btn { width: 100%; margin: 0;}
        }
      }
    }
  }
}

