
/* 페이지타이틀 */
.page_title-section {
  &.list_page-section {
    .section-inner.full_size { height: 280px;
      .padding-inner { position: relative; padding: 0 60px; height: 100%; max-width: 1920px; margin: 0 auto;
        .center-box { position: relative; padding: 0;}
      }
    }
    @media all and (max-width:$G-wMobile)  {
      .section-inner.full_size {  height: 198px;
        .padding-inner { padding: 0 25px;}
      }
    }

    /* 경매 안내 */
    .auction_head_info-article { height: 100%;
      .center-box { height: 100%;
        display: flex; flex-direction: column;  justify-content: center;
        &.ing, &.view { padding-right: 450px; }
        .page_title {  height: auto; text-align: left; }
        .event_day-list { display: flex; margin-top: 5px;
          li { position: relative;
            span { font-weight: 500; font-size: 20px; }
            span:last-child { margin-left: 5px; }
          }
          li + li { margin-left: 20px; padding-left: 20px; @include list-div_bar(7px, 0px, 16px,#bbb); }
        }
        .btn_set { margin-top: 25px;
          .btn { width:132px; color:$colorB; }
          .btn + .btn{ margin-left: 10px; }
        }
        
      }
      @media all and (max-width:1279px)  {
        .center-box { 
          .page_title { @include ellipsis-line(3); .th1 {  line-height: 1.1;}}
          .event_day-list { display: flex; flex-direction: column;
            li { 
              span:last-child { margin-left: 5px; } 
            }
            li + li { margin-left: 0px; padding-left: 0px; &:before { display: none; } }
          }
        }
      }
      @media all and (max-width:$G-wMobile)  {
        .center-box { padding-right: 0px; 
          &.ing, &.view { padding-right: 0px; }
          .event_day-list { display: flex;  flex-direction: row; margin-top: 10px;
            li { position: relative;
              span {  font-size: 15px; }
              span:last-child { margin-left: 5px; }
            }
            li + li { margin-left: 8px; padding-left: 8px; @include list-div_bar(5px, 0px, 12px,#bbb); }
          }
          .btn_set { margin-top: 15px;
            .btn { width:auto;  font-size: 14px; padding: 0 25px; line-height: 35px; }
            .btn + .btn{ margin-left: 10px; }
          }
        }
      }
    }

    /* pc-top, mb-sticky */
    .proceeding-article { position: absolute; bottom: 60px; right: 60px;
      .article-inner { width: 470px; padding: 25px 40px; border-radius: 12px; 
        border: solid 1px #dedede; background-color: #fff;
        display: flex; flex-direction: column; justify-content: center;
        .column + .column { margin-top: 6px;}
        .column { display: flex; padding-right: 40px; }
        .note_msg { font-size: 24px; font-weight: 700; color:$colorP; margin-right: 30px; line-height: 38px; }
        .ac-list { display: flex; 
          li { position: relative; font-size: 22px; line-height: 38px; font-weight: 600;
            .count { color:$colorB2 }
            .name  { color:$colorD }
          }
          li + li { margin-left: 13px; padding-left: 13px;
            @include list-div_bar(9px, 0px, 20px, #ccc ); 
          }
        }
        .note_etc {  
          span, strong { line-height: 30px; vertical-align: middle; } 
          span    { font-size: 18px; font-weight: 500;  color:$colorD; }
          strong  { font-size: 26px; font-weight: bold; color:$colorB; margin-left: 5px; }
        }
        i { position: absolute; top:50%; right: 30px; margin-top: -15px; }
      }
      @media all and (max-width:$G-wMobile)  { position: fixed; left: 0; right: 0; bottom: 0; z-index: 10;
         
        .article-inner { width: 100%; padding: 13px 25px; border-radius: 12px; 
          border-bottom-left-radius: 0; border-bottom-right-radius: 0; @include shadow( 0px, -5px, 10px, .07 );
          .column + .column { margin-top: 3px;}
          .column { display: flex; font-size: 16px; padding-right: 40px; 
            &.view {
              .note_msg { font-size: 16px; margin-right: 18px; line-height: 23px; }
            }
          }
          .note_msg { font-size: 16px; margin-right: 18px; line-height: 37px; }
          .ac-list { 
            li {font-size: 14px; line-height: 37px; }
            li + li { margin-left: 8px; padding-left: 8px;
              @include list-div_bar(13px, 0px, 11px, #ccc ); 
            }
          }
          .note_etc {  font-size: 0; line-height: 0;
            span, strong { line-height: 18px; vertical-align: middle; } 
            span    { font-size: 13px; font-weight: 500;   }
            strong  { font-size: 15px; font-weight: bold;  margin-left: 8px; }
          }
          i { right: 20px; margin-top: -12px; }
        } 
      }
    }

  }
}


/* 리스트 섹션 */
.tab-auction-section  { margin-top: 80px; padding:0 60px; max-width: 1920px;}
.auction_list-section { margin-top: 0px;  padding:0 60px; max-width: 1920px;}
.tab-wrap { background: #fff; z-index: 1; padding-top: 2px;}
.tab-wrap.hold_start { position: fixed; z-index: 1; left: 0; right: 0;
  .tab-area { max-width: 1920px; margin: 0 auto; padding: 0 60px;}
}
@media all and (max-width:$G-width)  {
  .tab-auction-section  { margin-top: 80px;padding:0 50px; max-width: 1420px; }
  .auction_list-section { margin-top: 0px; padding:0 50px; max-width: 1420px; }
  .tab-wrap.hold_start .tab-area { padding: 0 50px;}
}
@media all and (max-width:$G-wMobile)  {
  .tab-auction-section  { margin-top: 33px; padding:0px; }
  .auction_list-section { margin-top: 0px;  padding:0 25px; }
  .tab-wrap.hold_start { top: 57px !important;
    .tab-area { padding: 0;}
  }
}


/* 리스트 컨텐츠 - 패널 */
.content-panel{

  /* 텝메뉴 , 검색 */
  &.type_panel-search_tab {
    .panel-header { padding:0; } 
    .panel-body   { padding:0; } 
    .search_tab-article { padding: 30px 0;
      .article-body { display: flex;  justify-content: space-between; }
      .col_item { display: flex; 
        >.select-box { min-width: 152px;
          .trp-dropdown-area.h42-line button {padding: 10px;}
          .trp-dropdown_list-box {min-width: 172px;padding: 0;top: 52px;}
          select {
            //&:focus , &:hover {border-color: $colorB2;}
          }
        }
        >div + div{ margin-left: 10px;}
        >.search-box { width: 230px;
          input { 
            //&:focus , &:hover {border-color: $colorB2;}
          }
        }
      }
      .col_item.mb-col1{
        > .count { height: 42px; line-height: 22px; padding: 10px 0; color: #333;
          em { font-weight: 600;}
        }
        > .select-box { min-width: 90px; position: relative; margin-left: 14px; padding-left: 15px;
          &::before { display: block; content: ""; position: absolute; left: 0; top: 50%; width: 1px; height: 16px; background: #ccc; transform: translateY(-50%);}
          .trp-dropdown-area.h42-line button { padding: 10px 0;
            span { margin: 0;}
          }
        }
        .js-dropdown-btn.active i { transform: rotate(180deg);}
      }
    }
    @media all and (max-width:$G-wMobile)  {
      .panel-body   { padding:0 25px; } 
      .search_tab-article { padding: 21px 0 30px;
        .article-body { display: block;  justify-content: initial; }
        .col_item.mb-col1 { justify-content: space-between;
          >.count { height: 35px; line-height: 23px; padding: 6px 0;}
          >.select-box { min-width: auto; position: static;
            &::before { display: none;}
            .trp-dropdown-area.h42-line button {padding: 6px 40px 6px 0px;
              .form-select_arrow_md { right: -9px;}
            }
            .trp-dropdown_list-box {top: 35px; min-width: 200px; right: 0;}
          }
          >.search-box { margin-top: 10px; margin-left: 0; width: 100%; }
        }
        .col_item.mb-col2 { flex-wrap: wrap;
          >.search-box { width: 100%;}
          >.select-box { flex : 1; margin-top: 10px; margin-left: 0;}
          >.select-box:last-child { margin-top: 10px;  margin-left: 10px;}
        }
      }
    }
  }

  /* 상품 리스트 */
  &.type_panel-product_list {
    .search_tab-article { padding: 30px 0;
      .article-body { display: flex;  justify-content: space-between; }
      .col_item { display: flex; 
        .select-box { min-width: 152px;
          select {
            //&:focus , &:hover {border-color: $colorB2;}
          }
        }
        .select-box + .select-box{ margin-left: 10px;}
        .select-box + .search-box{ margin-left: 10px;}
        .search-box { width: 230px;
          input { 
            //&:focus , &:hover {border-color: $colorB2;}
          }
        }
      }
    }
    @media all and (max-width:$G-wMobile)  {
      .search_tab-article { padding: 21px 0 30px;
        .article-body { display: block;  justify-content: initial; }
        .col_item.mb-col1 { display: block; 
          .select-box { display: inline-block; }
          .search-box { margin-top: 10px; margin-left: 0; width: 100%; }
        }
        .col_item.mb-col2 { 
          .select-box { flex : 1; }
          .select-box:first-child  { margin-top: 10px;  margin-right: 5px;}
          .select-box:last-child   { margin-top: 10px;  margin-left:  5px;}
        }
      }
    }
    
    .product-list { display: flex; flex-direction: row; flex-wrap: wrap;
      width: calc( 100% + 20px ); margin-left: -10px;
      li { width: 20%; padding: 0 10px; padding-bottom: 25px; 
        .li-inner { position: relative;   border: 1px solid #ccc; height: 100%; }
      }
      @media all and (max-width:$G-maxWidth)  {
        li { width: 25%; }
      }
      // [0527]상품리스트 구간 수정(2개 레이아웃 삭제 : 4개유지)
      /* @media all and (max-width:$G-width)  {
        li { width: 50%; }
      } */
      @media all and (max-width:$G-wMobile)  { width: calc( 100% + 10px ); margin-left: -5px;
        li { width: 100%;  padding: 0 5px; padding-bottom: 17px; }
      }

      .item-article { 
        .image-area { position: relative;
          .img-ratio { background-color: #f6f6f6;
            .img-align { padding: 5px;}
          }
          img {width: 100%;}
          .typo-align { position: absolute; top:0; width: 100%; height: 100%;}
          .category_name { 
            display: flex; align-content: center; flex-wrap: wrap; justify-content: center; flex-direction: column;
            width: 100%; height: 100%;  vertical-align: middle;
            font-size: 28px; font-weight: 500; color:#fff;
          }
        }
        .typo-area { padding: 0px 20px;
          > div { position: relative; }
          .num_heart-box{ display: flex; justify-content: space-between; padding: 18px 0 0px; }
          .info-box {  padding: 15px 0 15px; }
          .price-box { padding: 15px 0 15px;  border-top: 1px solid #ccc;}
          .bidding-box { padding: 15px 0 15px;  border-top: 1px solid #ccc;
            display: flex; justify-content: space-between;
            &.col_2 { flex-direction: column; justify-content: center;
              padding-top: 0;
              .deadline_set { }
              .btn_set      { .btn { width: 100%; }}
            }
          } 
          .state-box { position: relative; display: flex;
            padding:20px 0; border-top: 1px solid #ccc;
            .state { min-width: 93px; font-size: 0; 
              >div { display: block; min-width: 63px; height: 38px;
                font-size: 16px; font-weight: 600; color:$colorP; 
                line-height: 36px; text-align: center; vertical-align: middle;
              }
              &.op-ing > div { background-color: #fff;
                border: 1px solid #4dada7; color:$colorP; 
              }
              &.op-end > div { background-color: #ededed;
                border-color: #ededed; color:$colorS;
              }
            }
            .other { padding-left: 20px; 
              div { font-size: 16px; font-weight: 500; color: $colorB2; }
            }
          }

          .num      { font-size: 18px; font-weight: 600; color:$colorB2 ; line-height: 30px; }
          .heart    { width: 30px; height: 30px; cursor: pointer;
            &.on i {  &::after {  @include sp_icons($icon-heart_on) } }
            i { margin-right: -3px; }
          }
          .title    { font-size: 22px; font-weight: bold; color:$colorB2; letter-spacing: 0; @include ellipsis();
            span { display: block; @include ellipsis();}
          }
          .sub      { font-size: 18px; font-weight: 500;  color:$colorB2; letter-spacing: 0;}
          .desc     { margin-top: 3px; font-size: 18px; font-weight: 500; color:$colorB2;letter-spacing: 0px; @include ellipsis();
            span { display: block; @include ellipsis();}
          }
          .standard { margin-top: 3px; font-size: 16px; font-weight: 500; color:$colorS; 
            span { line-height: 1.5; }
            .size_year { 
              span { position: relative; display: inline-block;  }
              span + span { margin-left: 10px; padding-left: 10px; @include list-div_bar(6px, 0, 12px, #bbb); }
            }
          } 
          .price-list {  position: relative; padding-left: 60px; min-height: 26px;
            dt { position: absolute; left: 0; 
              font-size: 16px; font-weight: 600; color:$colorB2; line-height: 26px;}
            dd { font-size: 16px; font-weight: 500; color:$colorB2; text-align: right; line-height: 26px; height: 26px;}
            strong { font-size: 16px; font-weight: 700; color:$colorE; }
            em     { font-size: 16px; font-weight: 500; color:$colorE; margin-left: 5px; }
            s      { font-size: 16px; font-weight: bold; color:$colorS; text-decoration: none; }
          }
          .deadline_set { font-size: 16px; font-weight: 500; color:$colorB2; line-height: 48px; }
          .btn_set      { .btn { min-width: 130px;}}
        }
        .product_cancle-area { display: none; position: absolute; top:0; left: 0;
          width: 100%; height: 100%; background-color: #f6f6f6; 
          .area-inner  {  display: flex; flex-direction: column; justify-content: center; align-items: center;
            height: 100%; text-align: center;
          }
          .typo { margin-top: 5px;
            font-size: 18px; font-weight: 500; color:$colorS; line-height: 1.64;
            span {}
          }
        }

        
        // [0527]상품리스트 구간 수정(폰트스타일 추가)
        @media all and (max-width:1280px) {
          .typo-area {
            .title    { font-size: 16px; line-height: 30px;}
            .sub      { font-size: 14px; }
            .desc     { margin-top: 5px; font-size: 14px; }
            .standard { font-size: 13px; }
            .price-list {  padding-left: 60px; min-height: 24px;
              dt { position: absolute; left: 0; 
                font-size: 14px; color:$colorB2; line-height: 24px;}
              dd { font-size: 14px; line-height: 24px; height: auto; min-height: 24px;}
              strong { font-size: 14px; font-weight: 700; }
              em     { font-size: 14px; font-weight: 500; display: block; }
              s      { font-size: 14px; font-weight: 700; }
            }
            .deadline_set { font-size: 14px; line-height: 43px; }

            // 레이아웃
            .info-box { padding: 0 0 15px;}
            .bidding-box { flex-direction: column; }
            .btn_set { .btn { min-width: 100%;}}
            @media (min-width:$G-wMobile) {
              .standard .size_year {
                span + span { display: block; padding-left: 0; margin-left: 0;
                  &::before { display: none;}
                }
              }
            }
          }
        }

        @media all and (max-width:$G-wMobile)  {
          .typo-area { padding: 0 20px;
            > div { position: relative; }
            .num_heart-box{ display: flex; justify-content: space-between; padding: 14px 0 0px; }
            .info-box {  padding: 0px 0 15px; }
            .price-box { padding: 15px 0 15px;  }
            .bidding-box { padding: 15px 0 15px; 
              display: flex; justify-content: space-between;
            } 
            .state-box { position: relative; display: flex;
              padding:20px 0; border-top: 1px solid #ccc;
              .state { width: 80px; 
                >div { width: 80px; height: 32px;
                  font-size: 13px; line-height: 32px;
                }
              }
              .other { width: calc(100% - 60px); padding-left: 15px; 
                >div { font-size: 14px; line-height: 1.2; }
              }
            }

            .num      { font-size: 14px; line-height: 30px; }
            .heart    { width: 30px; height: 30px; i { margin-right: -5px;}
                &.on i {  &::after {  @include sp_icons2x($icon-heart_on-2x) } }
            }
            .title    { font-size: 16px; line-height: 30px;}
            .sub      { font-size: 14px; }
            .desc     { margin-top: 5px; font-size: 14px; letter-spacing: -1px;
              @include ellipsis();
            }
            .standard { margin-top: 5px; font-size: 13px;
              span { line-height: 1.5; }
              .size_year { 
                span { position: relative; display: inline-block;  }
                span + span { margin-left: 10px; padding-left: 10px; @include list-div_bar(5px, 0, 10px, #bbb); }
              }
            } 
            .price-list {  padding-left: 60px; min-height: auto;
              dt { position: absolute; left: 0; 
                font-size: 14px; color:$colorB2; line-height: 24px;}
              dd { font-size: 14px; line-height: 24px; min-height: 24px;}
              strong { font-size: 14px; font-weight: 700; }
              em     { font-size: 14px; font-weight: 500; }
              s      { font-size: 14px; font-weight: bold; }
            }
            .deadline_set { font-size: 14px; line-height: 43px; }
            .btn_set      { .btn { min-width: 135px;}}
          }
          .product_cancle-area {
            .typo { margin-top: 5px;
              font-size: 14px; font-weight: 500; line-height: 1.5;
              span {}
            }
          }
        }
      }

      /// 취소상품
      li.cancel {
        .item-article { 
        .product_cancle-area { display: block; }
        }
      }

    }

    .set-pc_mb {
      .only-mb { text-align: center; margin-top: 13px;
        .btn { width: 160px;}
      }
    } 


  }
  

}