
    /* 문의하기 & 고객센터 */
    .inquiry-article { position: relative; margin-top: 93px;
      .inquiry-box { display: flex; justify-content: space-between;
        padding: 41px 40px;
        border: 1px solid #dedede; background-color: $colorBGw;
        .typo-area { display: flex; flex-wrap: nowrap;  align-content: center;
          padding-left: 10px; }
        .btn-area  { @include before-middle();
          a { min-width:  160px; }
        }

        .icon-box { margin-right: 20px;
          i { border-radius: 50%; overflow: hidden; background-color: #fff; }
        }
        .title-box { min-width: 75px;}
        .title-box, .manager-box { font-size: 22px; vertical-align: middle; margin-right: 40px;
          strong, span , em { line-height: 60px;}
          strong { color:$colorB; font-weight: 700;  }
          span   { color:$colorD; font-weight: 500; }
          em     { color:$colorB2; font-weight: 600;}
        }
        .desc-box { 
          span { font-size: 16px; line-height: 1.5; }
        }
        .info-guide { display: flex; flex-direction: column;
          .manager-box {
            span, em { line-height: 1;}
          }
        }
        .contact-box {
          ul { margin-top: 15px;
            li{ position: relative; display: inline-block;}
            li + li { padding-left: 16px; margin-left: 10px; @include list-div_bar( 3px , 0px, 14px, #ccc )}
          }
          span , em, a { line-height: 1; font-size: 20px; font-weight: 500; display: inline; }
          span { color:$colorS; }
          em   { color:$colorB2 }
        }
      }
      @media all and (max-width:$G-width) {
        .inquiry-box { display: block; justify-content: initial; 
          max-width: 910px; margin: 0 auto;
          .typo-area { display: block; flex-wrap: initial;  align-content: center; }
          .btn-area  { margin-top: 15px;}
          
          .icon-box {  display: inline-block; }
          .title-box, .manager-box { 
            strong, span , em { line-height: 60px;}
          }
          .title-box { display: inline-block; }
          .manager-box { margin-top: 10px; display: block;
            strong, span , em { line-height: 1.36;}
          }
          .info-guide { display: flex;
            .manager-box { display: inline-block;
              span, em { line-height: 1.36;}
              em { display: block; margin-top: 10px;}
            }
            .contact-box { display: inline-block;}
          }
          .contact-box { display: block; margin-top: 10px;
            ul { margin-top: 0;
              li{ position: relative; display: inline-block;}
              li + li { padding-left: 16px; margin-left: 10px; @include list-div_bar( 10px , 0px, 14px, #ccc )}
            }
            span , em, a { line-height: 30px; font-size: 20px;  }
          }
        }

      }
      @media all and (max-width:$G-wMobile) {  margin-top: 35px;
        .inquiry-box { display: block; justify-content: initial;
          padding: 28px 18px;
          .typo-area { display: block; flex-wrap: initial;  align-content: center;
            padding-left: 0px; }
          .btn-area  { margin-top: 15px;
            a { min-width:  160px;}
          }

          .icon-box { display: inline-block; margin-right: 13px; }
          .title-box, .manager-box { font-size: 17px; vertical-align: middle; margin-right: 0;
            strong, span , em { line-height: 44px;}
            strong { color:$colorB; font-weight: 700; margin-right: 40px; }
            span   { color:$colorD; font-weight: 500; }
            em     { color:$colorB2; font-weight: 600;}
          }
          .title-box { display: inline-block; }
          .manager-box { margin-top: 10px;
            strong, span , em { line-height: 1.36;}
          }
          .info-guide { display: block;
            .manager-box { display: block;}
            .contact-box { display: block;}
          }
          .contact-box {
            ul { 
              li{ position: relative; display: block; margin-top: 10px;}
              li + li {  padding-left: 0px; margin-left: 0px; 
                &::before { display: none;}
              }
            }
            span , em, a { line-height: 1.36; font-size: 15px;  }
            span { color:$colorD; }
            em   { color:$colorB2 }
          }
        }


      }
      
  
      /* (공통)하단고객센터 */
      &.type-customer { position: relative; margin-top: 80px;
        .inquiry-box { display: flex; justify-content: space-between; padding: 43px 50px 40px; border: 1px solid #dedede; background-color: $colorBGw;
          .typo-area { display: flex; flex-wrap: nowrap;  align-content: center; 
            align-items: center;
            padding-left: 10px; 
          }
          .btn-area  { @include before-middle();
            a { min-width:  160px; }
          }

          .icon-box { margin-right: 20px;
            i { border-radius: 50%; overflow: hidden; background-color: #fff; }
          }
          .title-box { font-size: 22px; vertical-align: middle; margin-right: 20px;
            strong, span , em { line-height: 60px;}
            strong { color:$colorB; font-weight: 700; margin-right: 40px; }
            span   { color:$colorD; font-weight: 500; }
            em     { color:$colorB2; font-weight: 600;}
          }
          .info-guide { display: block;
            .contact-box { display: block;
              ul { 
                li { position: relative; display: inline-block;}
                li + li { padding-left: 16px; margin-left: 10px; @include list-div_bar( 8px , 0px, 14px, #ccc )}
              }
              span , em, a { font-size: 20px; font-weight: 500; display: inline; 
                line-height: 1.5;
              }
              span { color:$colorS; }
              em   { color:$colorB2 }
            }
            .message-box { display: block; margin-top: 10px;}
          }
        }

        @media all and (max-width:$G-width) {
          .inquiry-box {
            .info-guide { width: 64%;}
          }
        }

        @media all and (max-width:$G-wMobile) {  margin-top: 50px;
          .inquiry-box { display: block; justify-content: initial;
            padding: 28px 18px;
            .typo-area { display: block; flex-wrap: initial;  align-content: center; padding-left: 10px; }
            .btn-area  { margin-top: 15px;
              a { min-width:  160px;}
            }

            .icon-box { display: inline-block; margin-right: 13px; }
            .title-box { font-size: 17px; margin-right: 0;
              strong, span , em { line-height: 44px;}
              strong { margin-right: 0; }
            }
            .title-box { display: inline-block; }
            .info-guide { display: block; width: 73%;
              .contact-box {
                ul { 
                  li{ display: block; margin-top: 5px; font-size: 15px;}
                  li + li {  padding-left: 0px; margin-left: 0px; 
                    &::before { display: none;}
                  }
                }
                span , em, a { font-size: 15px;  }
                span { color:$colorD; }
                em   { color:$colorB2 }
              }
            }
          }
        }

      }


    }

   