.content-panel{
 
  /* 결제 */
  &.type_panel-payment { 
    .panel-header { position: relative;
      .guide_box { position: absolute; right:0; top:50%;}
    }
    .panel-footer { padding-top: 60px; 
      .button-area {
        .btn_set{
          &.btn_double { justify-content: center; }
        }
        .btn { width: 230px; margin: 0 4px;}
      }
    }

    @media all and (max-width:$G-wMobile)  {
      .panel-header { padding-bottom: 30px;
        .guide_box { top:35%;}
      }
      .panel-footer { padding-top: 30px; 
        .button-area {
          .btn_set{
            &.btn_double { justify-content: space-between; 
              .btn { width: calc(50% - 5px);}
            }
          }
          .btn { width: 100%; margin: 0;}
        }
      }
    }
  }
}

//결제테이블
.form_table_pay { display: flex; flex-wrap: wrap; border-top: 1px solid #999; 
  > li { width: 100%; flex-direction: column; padding:30px 0; border-bottom:1px solid #cccccc;
    // &:first-child { border-top:0;}
    &.no-line { border-bottom:0; padding-bottom:0;}
    .list_tit { color:$colorB2; margin-bottom: 10px; }
    .terms-area { margin-top: 20px;
      label { }
      a { display: inline-block; margin-left: 10px; color:$colorB2; 
        line-height: 24px;
      }
    }
    .br-mo { display:none;}

    //안내및비용
    .table_box { display: flex;  justify-content: space-between;
      .guide, .member_pay { display: flex; flex-direction: column; justify-content: center;}
      .guide { border-right: 1px solid #ccc; width: 70%; padding-right: 50px;}
      .member_pay { width:27%; 
        p { display:flex; justify-content: space-between; align-items: center;
          &.fees span{ color:$colorS; 
            &.tit { font-weight: 600;}
          }
        }
        .tt5, .tt3 { color:$colorB2; font-weight: 600; }
        .tit { width:103px;}
        .won { width: auto; text-align: right; 
          i { margin-left: 3px; color:$colorS; }
        }
      }
    }

    //결제정보
    .pay_info { display:flex; flex-direction: column; line-height: 0;
      &.td_item{ 
        >li{ padding-left:160px;
          .th{ width: 140px;}
        }
      }
      >li { position: relative; margin: 5px 0; padding-left:105px;
        // &:first-child { margin-top:0;}
        .th, .td { line-height: 28px;}
        .th { color:$colorB2; font-weight: 600; width: 100px; 
          position: absolute; top:0px; left:0;
        }
        .pay { font-weight:700; color:$colorB2; 
          .total { color : #db1b1b;}
        }
      }
    }

    .list_tit + .btn_wrap,
    .list_tit + .total_pay_box  { margin-top: 25px;}
    .btn_wrap { margin-bottom:10px;
      a { margin-right:7px; min-width: 160px;}
    }

    //총결제금액
    .total_pay_box { border:1px solid #5f5f5f; text-align: center; font-weight: 600; color:$colorB2; padding: 45px 20px; margin-bottom: 10px;
      i { font-weight: 600;}
      span { color : #db1b1b;}
    }
    .check {  display: flex; flex-wrap: wrap; align-items: center; margin-top: 12px;
      .terms_link { margin-left:10px; color:$colorB2; text-decoration: underline;}
    }
    .check-header{margin-top: 5px;
      .trp.checkbox-box label.tb1 {font-size: 20px; font-weight: 600;}
      .colorP{font-weight: 500;}
    }
    .check-body{background-color: #f6f6f6; padding: 22px 40px 37px 40px; margin-top: 18px;
    }

    //가상계좌정보
    .account_info { background:#f6f6f6; padding: 40px; color:#333; 
      .tit { color:#333; font-size:22px; font-weight: 600; margin-bottom: 15px;}
      .txt { margin: 0 0 20px;
        span { color:$colorP; font-weight: 700;}
      }
      .pay_info { font-weight: 600;
        .th { font-weight: 500;}
        .td { strong { font-weight: 600;}}
      }
     
    }

    //작품 아카데미 결제
    .product-box { display: flex; flex-wrap: nowrap; align-items: center;
      .img { width: 136px; height: 136px; min-width: 136px; background:#f6f6f6;  text-align: center; margin-right: 25px;
        img { height: 100%;}
      }
      .product_info {
        dt { color:$colorB2; margin-bottom: 10px;
          .sub_txt { display:block}
        }
        dd {
          span {position: relative; color:$colorS;}
          .size_year { 
            span + span { margin-left: 10px; padding-left: 10px; 
              @include list-div_bar(8px, 0, 12px, #bbb); 
            }
          }
        }
      }
    }

  }

  @media all and (max-width:$G-wMobile)  {
    > li { padding:25px 0 25px;
      .br-mo { display:block;}
      .list_tit { color:$colorB2; margin-bottom: 10px; }
      .terms-area { margin-top: 20px;
        .trp.checkbox-box {
          label { line-height: 1.7; }
        }
        a { margin-left: 0px; 
          line-height: 1.7;
        }
      }
  
      //안내및비용
      .table_box { flex-wrap: nowrap;  flex-direction: column;
        .guide { border-right:0px; border-bottom:1px solid #ccc;; padding-bottom:20px; margin-bottom:20px; width: 100%; padding-right: 0;}
        .member_pay { width:100%;  padding: 0 10px;
          p { padding: 3px 0;}
          .tit { width:40%;}
        }
      }
  
      //결제정보
      .pay_info { 
        &.td_item{ 
          >li{ padding-left:75px;
            .th{ width: 70px;}
          }
        }
        >li { padding-left: 75px; margin: 3px 0;
          .th { width: 70px;  max-width: none;}
        }
      }
  
      .list_tit + .btn_wrap,
      .list_tit + .total_pay_box  { margin: 15px 0 5px;}
      .btn_wrap { display: flex; flex-wrap: nowrap; flex-direction: row; justify-content: space-between;
        a { margin-right:0; width: calc(50% - 5px); min-width: auto;}
      }
  
      //총결제금액
      .total_pay_box { padding: 25px 10px;}
      .check { margin-top: 12px; flex-direction: column; align-items: flex-start;
        .terms_link { margin-left:26px; margin-top: 0px;}
      }
      .check-header{margin-top: 0px;
        .trp.checkbox-box label.tb1 {font-size: 15px;}
      }
      .check-body{background-color: #f6f6f6; padding: 5px 20px 20px 20px; margin-top: 12px;
        .trp.checkbox-box label{line-height: 21px;}
      }
      //가상계좌정보
      .account_info { padding: 20px; 
        .tit { font-size:15px; margin-bottom:10px;}
        .txt { margin: 0 0 10px;}
       
      }

      //작품 아카데미 결제
      .product-box {
        .img { width: 96px; height: 96px; min-width: 96px; margin-right: 15px;}
        .product_info {
          dt { color:$colorB2; margin-bottom: 5px;}
          dd {
            .size_year {
              span + span { margin-left: 10px; padding-left: 10px; 
                @include list-div_bar(5px, 0, 11px, #bbb); 
              }
            }
          }
        }
      }

    }

  }
}



/* ============ 팝업 ========= */
.trp.popupfixed-wrap { 
  &.offline_payment-popup {
    .pop-header {
      .title-box {
        .txt_title { font-size: 34px; font-weight: 600; }
      }
    }

    .articles-area + .articles-area{ margin-top: 30px;}
    @media all and (max-width:$G-wMobile)  {
      .articles-area + .articles-area{ margin-top: 20px;}
    }
    .articles-area{
      .area-header{
        .title { font-size: 22px; color:$colorB2; font-weight: 600; line-height: 45px; }
      }
      .area-body{}
      @media all and (max-width:$G-wMobile)  {
        .area-header{
          .title { font-size: 16px; line-height: 35px; }
        }

      }
    }

    /* 테이블 */
    .table_base.name-payment_price {
      thead, tbody, tfoot {
        tr {
          th:first-child, td:first-child {border-left:1px solid #ccc;  }
          th:last-child, td:last-child {border-right:1px solid #ccc;  }
          th, td { padding: 3px 20px; }
        }
      }
      thead {
        th { border: 0; border-top:1px solid #999; border-bottom:1px solid #999; background-color: #fff;}
        th:nth-child(2) { width: 25%; }
      }
      tbody {
        td { border: 0; border-bottom:1px solid #ccc; padding: 3px 20px; }
      }
      tfoot { 
        th { background-color: #f3f3f3; border-bottom:1px solid #ccc; }
      }
      strong { font-weight: 600; }
      em { margin-left: 10px; font-weight: 600; color:$colorE;}
    }

    /* 아코디언 */
    .payment_price-accordion { border-left: 1px solid #ccc;border-right: 1px solid #ccc;
      .accordion-header { border-top: 1px solid #999;
        .accordion-inner { display: flex;  justify-content: space-between;          
          position: relative; padding:20px;
        }
        .title {
          strong { font-size: 14px; color:$colorB2; font-weight: 600; line-height: 17px;}
        }
        .count { padding-right: 35px;
          em, span { font-size: 14px; color: $colorD; line-height: 17px;}
          em   { font-weight: 600; }
          span { font-weight: 500; margin-left: 5px;}
        }
        i { position: absolute; top:22px; right:18px; }
        .on {
          i { @include rotate( 180deg ); }
        }
      }
      .accordion-body { display: none; border-top: 1px solid #ccc;
      }
      .accordion-footer { border-top: 1px solid #ccc; border-bottom: 1px solid #ccc;
      } 

      .work-list {
        >li { padding:22px 20px 10px; .li-inner { position: relative;}}
        >li + li { 
          .li-inner { &::before{ position: absolute; top: -22px;
                      content: ""; width: 100%; height: 1px; background: #ccc; } 
          } 
        }
        .item-header { display: flex; justify-content: space-between; }
        .item-body   { margin-top: 15px;}

        .image-area { position: relative; width: 65px;
          .img-ratio { background-color: #f6f6f6;}
          img {width: 100%;}
          .typo-align { position: absolute; top:0; width: 100%; height: 100%;}
        }
        .typo-area { width: calc(100% - 75px); padding-left: 10px;
          .num   { font-size: 13px; font-weight: 600;; color:$colorB2; }
          .title { margin-top: 12px; @include ellipsis();
            font-size: 14px; font-weight: 500; color:$colorB2; 
            strong { font-weight: bold;}
          }
          .desc  { margin-top: 5px; font-size: 13px; font-weight: 500;; color:$colorB2; @include ellipsis(); }
        }
      }
      
      .price-list { position: relative; padding-left: 70px;
        dt, dd { font-size: 14px;line-height: 35px; }
        dt { position: absolute; left: 0; 
          font-weight: 600; color:$colorB2; line-height: 35px;}
        dd { font-weight: 500; color:$colorD; text-align: right; min-height: 35px;
          strong { font-weight: 600; color:$colorB2; }
          em     { font-size: 16px; font-weight: 600; color:$colorE; } 
        }
      }

      .calculation-box { padding: 10px 20px; }
      .total-box {  padding: 10px 20px; border-top: 1px solid #ccc;
        background-color: #f3f3f3;
      }

    }


    /* 확인사항, 결제안내 : 도트리스트 */
    .mark_dot-list { 
      li { font-size: 18px; line-height: 30px; }
      @media all and (max-width:$G-wMobile)  {
        li { font-size: 14px; line-height: 20px; 
          &:after { top : 7px; }
        }
      }
    }

    /* 결제안내 */
    .payment_info-box{ margin-top: 10px; padding: 25px; border: 1px solid #ccc; 
      .text-account { text-align: center; 
        strong {}
        em   { position: relative; color:$colorP; font-weight: 600; margin-left: 15px; padding-left: 15px;
          @include list-div_bar( 5px, 0, 18px, #ccc); 
        }
        span { color:$colorP; font-weight: 500;}
      }
      .text-notice{ margin-top: 18px; text-align: center; 
        i { margin-top: 3px; margin-right: 5px;}
        span { color:$colorB2; }
      }
      @media all and (max-width:$G-wMobile) { margin-top: 10px; padding: 22px; 
        .text-account {
          strong { display: inline-block; margin-bottom: 2px;}
          em   { margin-left: 0px; padding-left: 0px;
            &::before { display: none;}
          }
        }
        .text-notice{ margin-top: 10px; 
          i { margin-top: 2px; margin-right: 2px;}
        }
      }
    }

    .contact_us { padding: 15px 0; 
      font-size: 18px; font-weight: 600; 
      span, a { display: inline-block;}
      @media all and (max-width:$G-wMobile) { padding: 20px 0 13px; 
        font-size: 14px; 
      }
    }


  }
}