/* =============  jq-Grid ============= */
.table-box { position: relative; width:100%;
  &.scroll_y { .ui-jqgrid .ui-jqgrid-bdiv { overflow-x: hidden;}}
}

$jqgrid_line :#ddd; 
.ui-jqgrid[id^=gbox_] { box-sizing: border-box; 
  // 공통
  &.ui-widget.ui-widget-content{ border: 0; border-top:1px solid $jqgrid_line }
  tr.ui-widget-content { border-color: $jqgrid_line;  }
  .ui-widget-content { border:0;  }

  .ui-jqgrid-htable, .ui-jqgrid-btable{ box-sizing: border-box; max-width: 100%;
    .ui-jqgrid-labels, .ui-widget-content { overflow: hidden; }
    th, td { height: 30px; padding: 0 2px 0 2px; }
  }


  // ====================  해더 ====================  //
  .ui-jqgrid-hdiv { 
    >.ui-jqgrid-hbox {}
    .ui-jqgrid-htable{
      th { background-color: #f6f6f6; }
    }
  }


  // ====================  바디 ====================  //
  //ui-jqgrid-titlebar ui-jqgrid-caption ui-widget-header ui-corner-top ui-helper-clearfix"
  //ui-jqgrid-titlebar ui-jqgrid-caption ui-widget-header ui-corner-top ui-helper-clearfix
  .ui-jqgrid-bdiv {
    >div { overflow: hidden; }
    .ui-jqgrid-btable {
      tr.ui-row-ltr td { text-align: center; }
    }
  }

  // ====================  패이징 ====================  //
  .ui-jqgrid-pager { border-top: 0; border-bottom: 1px solid $jqgrid_line !important;
    height: auto;
    .ui-pg-table {
      td { height: 30px; padding:0px; border: 1px solid transparent;
        input[type="text"] { width: auto;}
      }
    }
  }


}