// 버튼 정열
.btn_bottom { margin-top: 40px;}
.btn_set-float { @include clearfix(); 
  &.tac { text-align: center;}
  .btng-l  { float: left; }
  .btng-r  { float: right;}
}

.btn_set { display: flex; 
  .btn_item { flex: 1; text-align: center; 
    .btn { width: 100%;}
  }
}
.btn_set_more { text-align: center;
  .btn { width: 170px;}
  @media all and (max-width:$G-wMobile)  {
