    .tab-list {  display: flex; justify-content: flex-start; 
      height: 62px;
      li { width: auto;
        a { padding: 0 0px; margin-right: 60px; font-size: 24px; font-weight: 500; 
          box-sizing: border-box; color:$colorS; height: 61px;
        }
        em { font-size: 22px; font-weight: 500; color:$colorL; }
        &.active {
          a { font-size: 24px; font-weight: 700; color:$colorP; }
          em { color:$colorP; }
