  &.type-sorting { border:0px; width: calc(100% + 10px); margin-left: -5px;
    .tab-list { height: auto; border:0px; 
      li { border:1px solid #dedede; margin:0 5px;
        a { display:block; height:54px; line-height: 52px; color:$colorB2;  background:#f6f6f6; border:2px solid transparent;}

        &.active {
