          a { border-color: #4dada7; 
            &::after { display: none;}
          }
        }
      }
    }
    @media all and (max-width:$G-wMobile)  {
      height: auto; overflow: auto; overflow-x: auto;
      .tab-list { display: block; height: auto; border:0px; padding:0; min-width: 100%; margin-top: -5px;
        li { border:0; padding:5px 5px; margin:0; width: 33.33333%; float:left;
          a { display:block; height:54px; line-height: 52px; color:$colorB2;  background:#f6f6f6; border:2px solid #dedede;}
  
          &.active {
            a { border-color: #4dada7; 
              &::after { display: none;}
            }
          }
        }
      }
    }

  }
  
}
.tab-cont, .tab_cont {
  display: none;
  &.active {display: block;}
}



//서브tab  == 현재까지 사용안함
.sub_tab-area { //display: flex; flex-wrap: wrap;
  .tab-list { display: flex; flex-wrap: wrap; justify-content: center; height: 82px;
    li { width: auto; margin: 0 9px;
      a { position:relative; display: block; height: 82px; color:$colorD;  white-space:nowrap; min-width: 204px;
        span { display:block; color:$colorL; font-weight: 700;}
        &::before { content:''; display:inline-block; width:100%; height: 4px; position:absolute; bottom:0; background:linear-gradient(-45deg, transparent 4px, #ccc 0);}
      }
      &.active {
        a { color: $colorB2; 
          span { color:$colorP;}
          &::before { content:''; display:inline-block; width:100%; height: 4px; position:absolute; bottom:0; background:linear-gradient(-45deg, transparent 4px, $colorP 0);}
        }
      }
    }

    &.type2 {
      li { 
        a { min-width: 265px;}
      }
    }
  }
}
.tab-cont-sub {
  display: none;
  &.active {display: block;}
}

@media all and (max-width:$G-wMobile)  {
  .tab-cont {
     display: none;
     &.active {display: block;}
  }

  //서브tab
  .sub_tab-area {
    .tab-list { height: 50px; width: calc( 100% + 12px); margin-left: -6px; height:100%; justify-content: space-between;
      li { width: 50%; margin: 10px 0 10px; padding: 0 6px;
        a { height: 50px; min-width: auto;
          &::before { content:''; display:inline-block; width:100%; height: 2px; position:absolute; bottom:0; background:linear-gradient(-45deg, transparent 2px, #ccc 0);}
        }
        &.active {
          a { 
            &::before { content:''; display:inline-block; width:100%; height: 2px; position:absolute; bottom:0; background:linear-gradient(-45deg, transparent 2px, $colorP 0);}
          }
        }
      }

      &.type2 {
        li { 
          a { min-width: auto;}
        }
      }
    }
  }

}





/* ================== accordion MENU */
.accordion-wrap.noneline { border-top: 0 }
.accordion-wrap { border-top: 1px solid #acacac; }
// 일반 아코디언
.accordion-list{ border-top: 1px solid #acacac;
  >li { border-bottom: 1px solid #d4d4d4; 
    .header-area { position: relative; background-color: $colorBGw;
        .accordion_name  { display: block; 
            @include ellipsis();
        }
        .acc_btn {  position: relative; box-sizing: border-box;
          display: block; padding: 20px 50px 28px 20px;
          i { position: absolute; top:18px; right:15px; }
        }
    }
    >.con-area { display: none;  padding: 30px;  }
  }
  >li.on { 
      .header-area { 
          .acc_btn { 
            i { @include rotate(180deg); }
          }
      }
  }
  
  //약관일때
  &.policy {
    .header-area { position: relative; white-space: normal;
      .accordion_name { padding:24px 50px 24px 30px; white-space: normal;
        &.eng_accordion{ padding:15px 50px 15px 30px;}
      }
      .acc_btn { position: absolute; right: 25px; top: 50%; height: 30px; margin-top: -15px; padding: 0; 
        i { position: static;}
      }
    }
  }

  //faq
  &.faq {
    .header-area { position: relative; white-space: normal; background-color: #fff;
      .accordion_name { padding:44px 50px 44px 65px; white-space: normal; color:#333;
        .faq { font-size: 28px; font-weight: 600; color:$colorP; display:inline-block; position: absolute; left: 30px; top: 40px;}
        .icon-accordion_arrow_down { position: absolute; right: 25px; top: 50%; height: 30px; margin-top: -15px; padding: 0;  }
      }
    }
    .con-area { background:#f6f6f6; padding: 55px 60px 55px 104px; position: relative;
      .faq { font-size: 28px; font-weight: 600; color:$colorD; display:inline-block; width: 44px; margin-left: -44px; float:left;}
    }

    >li.on { 
      .header-area { 
        .icon-accordion_arrow_down { @include rotate(180deg); }
      }
    }
  }

  @media all and (max-width:$G-wMobile)  {
    >li { 
      .header-area { 
        .acc_btn { padding: 16px 40px 16px 16px;}
      }
      >.con-area { display: none; padding: 15px;  }
    }
    
    //약관일때
    &.policy {
      .header-area { 
        .accordion_name { padding:15px 45px 15px 15px; white-space: normal;
          &.eng_accordion{ padding:10px 45px 10px 15px;}
        }
        .acc_btn { width: 30px; height: 30px; line-height: 30px; text-align: center; right: 10px;}
      }
    }

    //faq
    &.faq {
      .header-area {
        .accordion_name { padding:17px 50px 17px 20px;
          .faq { font-size: 18px; position: absolute; left: 0; top: 16px;}
          .icon-accordion_arrow_down { position: absolute; right: 14px; top: 50%; height: 16px; margin-top: -8px;}
        }
      }
      .con-area { padding: 25px 20px 25px 42px;
        .faq { font-size: 18px; width: 20px; margin-left: -20px;}
      }
    }


  }

}


/* ================== gallery-wrap 스와이퍼 기본속성 */
.gallery-wrap {  position: relative;
  .gallery_container { position: relative; overflow: hidden; font-size: 0; line-height: 0;
    .swiper-slide img{ width:100%; }
  }
  .btn-next, .btn-prev { overflow: hidden;
    display: block; width: 50px; height: 50px; 
    position: absolute; top: 50%; margin-top: -25px; z-index:2;
    background-color: rgba(0,0,0,0.0);
    @include transition(all, .3s, ease-in-out);
  }
  &:hover { 
    .btn-next { background-color: rgba(0,0,0,0.5); }
    .btn-prev { background-color: rgba(0,0,0,0.5); }
    .btn-next , .btn-prev {
      &:hover {  background-color: rgba(0,0,0,0.7); }
    }
  }
  .btn-next { right: 0; }
  .btn-prev { left: 0; }
  .pagination { width: 100%; padding: 15px 0;
    text-align: center; font-size: 0; line-height: 0;
    .swiper-pagination-bullet { width: 10px; height: 10px; margin: 0 4px;
      background-color: #ccc; opacity: 1;
      &.swiper-pagination-bullet-active {  background-color: #555;}
    }
  }
  @media all and (max-width:$G-wMobile)  { 
    .gallery_container { overflow: visible; 
      .swiper-slide img{  }
    }
    .btn-next { display: none; }
    .btn-prev { display: none; }
    .pagination { width: 100%; padding: 15px 0;
      .swiper-pagination-bullet { width: 6px; height: 6px; margin: 0 2px; }
    }
  }

}



