/* 윈도우 팝업 해더 */
header.win_header{ background-color: #243233;
  .header-inner { padding: 16px 60px; 
    max-width: 1920px; margin: 0 auto;
    .header_logo { display: block; width: 160px; height: 28px;
      background: url(/images/pc/logo/SA_logo_white.svg) no-repeat center center;
      background-size: 100%;
    }
  }
  @media all and (max-width:$G-wMobile)  {
    .header-inner { padding: 20px 25px; 
      .header_logo { width: 125px; height: 21px;
      }
    }
  }
}