.content-panel{
  
  /* 진행 중인 아카데미 */
  &.type_panel-ing_academy {}
  .service-article{ position: relative; 
    &.option-more {
      &.more_show{
        .service-list { 
          li { display: block; }
        }
        .article-footer .btn_set-float{ display: none; }
      }
    }
    .article-body{}
    .article-footer .btn_set-float{ display: none; }
    .service-list { display: flex; flex-direction: row; flex-wrap: wrap;
      width: calc( 100% + 20px ); margin-left: -10px;
      li { width: 25%; padding: 0 10px; padding-bottom: 33px; 
        &.deadline {
          .img-align { 
            &::after { content: ""; position: absolute; top:0; left: 0; right: 0; bottom:0;
              display: block;
              background-color: rgba(0,0,0,.6);
            }
          }
          .alert-box { text-align: center;}
          .alert { display: inline-block; padding: 3px 20px 3px;   min-width: auto;  
            font-size: 20px; line-height: 24px; height: 31px;
            border:1px solid #BDBDBD; color:#fff;
          }
          @media all and (max-width:$G-wMobile)  {
            .alert-box { margin-top: 10px;}
            .alert { padding: 2px 10px 2px;  min-width: auto; 
              font-size: 15px;  line-height: 19px; height: 23px;
            }
          }
        }
      }
      .li-inner { position: relative; }
      .image-box { position: relative;
        img {width: 100%;}
        .typo-align { position: absolute; top:0; width: 100%; height: 100%;}
        .category_name { 
          display: flex; align-content: center; flex-wrap: wrap; justify-content: center; flex-direction: column;
          width: 100%; height: 100%;  vertical-align: middle;
          font-size: 28px; font-weight: 600; color:#fff;
        }
      }
      .typo-box { padding: 12px 0;
        .state { font-size: 0; 
          span + span { margin-left: 6px;}
          span { display: inline-block; padding: 0 16px; line-height: 22px;
            font-size: 14px; font-weight: bold; color:#fff;
            &.allowed  { background-color: $colorMain; }
            &.deadline { background-color: #333; }
            &.progress { background-color: $colorBGd; color:$colorP;}
            &.end      { background-color: #f5f5f5; color:$colorL;}
          }
        }
        .title { margin-top: 15px;
          font-size: 20px; font-weight: 500; line-height: 30px;
          text-align: left;  @include ellipsis-line(2);
          em, span { display: inline; }
          em   { color:$colorP; margin-right: 10px; }
          span { color:#333; }
        }
      }

    }
    @media all and (max-width:$G-wMobile)  {
      &.option-more {
        .service-list { 
          li {display: none;
            &.mb_show { display: block; }
          }
        }
      }
      .article-body{}
      .article-footer{ 
        .btn_set-float { display: block;  margin-top: 15px;}
        .btn {  width: 160px; }
      }
      .service-list { width: calc( 100% + 10px ); margin-left: -5px;
        li { width: 50%; padding: 0 5px; padding-bottom: 17px; }
        .image-box { 
          .category_name { font-size: 18px; }
        }
        .typo-box { padding: 12px 0 6px;
          .state { font-size: 0; 
            span + span { margin-left: 3px;}
            span { display: inline-block; padding: 0 4px; line-height: 15px;
              font-size: 11px; 
            }
          }
          .title { margin-top: 10px; font-size: 14px;  line-height: 23px;
            em, span { display: inline; }
            em   { color:$colorP; margin-right: 8px;}
            span { color:#333;  }
          }
        }
      }
    }
  }
  .service-article_sns { margin-top: 60px;
    .banner { display: flex; 
      .img-area { width: per(620px, 1320px ); img {width: 100%;}}
      .typo-area { width: per(700px, 1320px ); background-color: #f6f6f6;
        padding: 10px 30px 10px 100px;
        display: flex; flex-direction: column; justify-content: center;
        .area-header {
          font-size: 32px;  line-height: 1.5; font-weight: bold;
          color:#333;
          em    { display: inline; color:$colorP; }
          span  { display: inline; }
        }
        .area-body   {  margin-top: 20px;
          font-size: 18px; font-weight: 500; line-height: 1.8; color:#555; 
        }
        .area-footer { margin-top: 20px;
          font-size: 18px; font-weight: 600; color: #333;
          a, span { vertical-align: top; display: inline;}
          i       { vertical-align: top; margin-top: 2px; margin-right: 3px;}
        }
      }
    }
    @media all and (max-width:$G-wMobile)  { margin-top: 50px;
      .banner { display: block; 
        .img-area { width: 100%  }
        .typo-area { width: 100%;
          padding: 25px 20px; background-color: $colorBGw;
          display: block; flex-direction: initial; justify-content: normal;
          .area-header { font-size: 22px;  line-height: 1.36; }
          .area-body   {  margin-top: 10px;
            font-size: 13px;  line-height: 1.5; 
          }
          .area-footer { margin-top: 10px; font-size: 13px; 
            a, span { vertical-align: top; display: inline;}
            i       { vertical-align: top; margin-top: 0px; margin-right: 3px;}
          }
        }
      }
    }
  }

  /* 아카데미 리스트 */
  &.type_panel-ing_academy {
    .inquiry-article { margin-top: 93px;}
    @media all and (max-width:$G-width) {
      .inquiry-article { margin-top: 35px; }
    }
  }
    .academy-article { position: relative;
      .academy-list { display: flex; flex-direction: row; flex-wrap: wrap;
        width: calc( 100% + 14px); margin-left: -7px;
        li { padding: 0 7px; width: 16.6666%;
          .li-inner { position: relative; height: 230px; background-color: #f6f6f6;
            border: 1px solid #dedede;
          }
          a { display: block; border: 1px solid transparent; height: 100%; }
          &:hover { 
            .li-inner { border: 1px solid #4dada7; } 
            a {  border: 1px solid #4dada7; }
          }
        }
      }
      .item-area { display: flex;  flex-direction: column; flex-wrap: nowrap;
        align-content: center; justify-content: center; text-align: center;
        height: 100%; 
        .item-header {  
          .icon-box { margin: 0 auto; width: 80px; height: 80px; 
            border-radius: 50%; background-color: #fff; overflow: hidden; }
        }
        .item-body   { margin-top: 25px;
          .title { font-size: 24px; font-weight: 600; color:#333; }
          .desc  { margin-top: 10px;
            font-size: 14px; font-weight: 500; color:#777; 
          }
        }
        .item-footer { margin-top: 20px; text-align: center; }
      }
      @media all and (max-width:$G-wMobile)  {
        .academy-list {
          width: calc( 100% + 10px); margin-left: -5px;
          li {  padding: 0 5px; width: 50%; margin-bottom: 10px;
            .li-inner { height: 170px; }
          }
        }
        .item-area {
          .item-header {  
            .icon-box { margin: 0 auto; width: 50px; height: 50px; }
          }
          .item-body   { margin-top: 15px;
            .title { font-size: 17px; }
            .desc  { margin-top: 6px; font-size: 10px; }
          }
          .item-footer { margin-top: 10px; }
        }

      }
    }
    
    /* 스와이퍼 */
    .swiper-article {  margin-top: 100px;  
      .container-ratio {}
      .container-center {}
      @media all and (max-width:$G-wMobile) { margin-top: 40px; 
        .mb-full { padding: 0 25px; overflow: hidden; }
        .gallery-wrap { width: 100%; }
      }
      @media all and (max-width:719px) { 
      
      }

      
    }



  /* ==== 게시판 상세 ====  */
  &.type_panel-edit_view{ padding: 0; margin-top: 100px;
    >.panel-header{ padding: 0; }
    >.panel-body  { margin-top: 40px;}
    >.panel-footer{ border-top: 1px solid #cccccc;
      .inquiry-article  { margin-top: 100px;}
      .button-area { padding: 0; margin-top: 60px;
        .btn.btn_lg {  width: 180px; } 
      }
    }
    @media all and (max-width:$G-wMobile)  { margin-top: 42px;
      >.panel-body  { margin-top: 10px;}
      >.panel-footer{  
        .inquiry-article  { margin-top: 50px;}
        .button-area { padding: 0; margin-top: 30px;
          .btn.btn_lg {  width: 160px; } 
        }
      }
    }
    
  }


}