
/* =============================== 섹션 이름  */
.basis-section{
   /* 약관 */
   &.terms-section{ margin-top: 100px; }
   @media all and (max-width:$G-wMobile)  {
      &.terms-section { margin-top: 40px; }
   }

   &.privacy-section{ margin-top: 100px; }
   @media all and (max-width:$G-wMobile)  {
      &.privacy-section { margin-top: 40px; }
   }

   &.rules-section{ margin-top: 100px; }
   @media all and (max-width:$G-wMobile)  {
      &.rules-section { margin-top: 40px; }
   } 

   &.type_tab-introduce {margin-top: 0;}
   &.introduce-section {margin-top: 100px;}
   @media all and (max-width:$G-wMobile)  {
      &.type_tab-introduce {}
      &.introduce-section {margin-top: 40px;}
   } 
}





/* =============================== 페널이름 */
.content-panel{
  /* 약관 */  
   &.type_panel-terms { 
      .panel-header {padding: 0; margin-bottom: 100px;}
      .tab_move-bar {
         ul { display: table; table-layout: fixed; width: 100%; border: 1px solid #dedede; border-collapse: collapse;
            li { display: table-cell; border: 1px solid #dedede; position: relative; text-align: center; vertical-align: middle;
            &:hover:after { display: block; content: ""; position: absolute; height: 2px; background: #4dada7; width: 100%; bottom: 0;}
               a{ display: inline-block; font-size: 18px; line-height: 30px; padding: 12px 0; font-weight: 600; color: $colorB2;
                  .teb_txt{font-size: 14px; font-weight: 700; color: $colorP;}
                  .teb_title{font-weight: 700; color: #333; line-height: 1.5; margin-top: 5px; }
               }
            }
         }
      }
      .terms_view-box {
        & + .terms_view-box {margin-top: 93px;}
        h3 {font-size: 32px;line-height: 1;color: #111;font-weight: 600; margin-bottom: 33px;}
        p {
           color: #555;
           & + p {margin-top: 12px;}
        }
        h4 {font-size: 24px;line-height: 1;color: #111; font-weight: 600; margin: 33px 0 16px;}
      }
   }
   @media all and (max-width:$G-wMobile)  {
      &.type_panel-terms { 
         .panel-header {padding: 0 0 10px; margin-bottom: 50px;}
         .tab_move-bar {
            ul {
               display: flex; flex-direction: column; 
               li {
                  width: 100%;text-align: center; border: 0;
                  & + li {border-top: 1px solid #bdbdbd;border-left: 0;}
                  a {
                     display: block; font-size: 14px;line-height: 50px; 
                  }
               }
            }
         }
        .terms_view-box {
           & + .terms_view-box {margin-top: 46.5px;}
           h3 {font-size: 22px;line-height: 1; margin-bottom: 11.5px;}
           p {
              & + p {margin-top: 21px;}
           }
           h4 {font-size: 17px;line-height: 1;color: #111; font-weight: 600; margin: 21.5px 0 12.5px;}
        }
     }

  }

  /* 
  본문 내의 특수문자 ⑤ 는 pretendard variable 로 교체해야합니다. woff2 기준 2.1mb 라 추가로 로딩하지는 않았습니다. 
  https://github.com/orioncactus/pretendard/tree/main/dist/web/variable
  */
  &.type_panel-privacy {
     .panel-header {
        margin-bottom: 52px; padding: 0;
        .privacy-title {
           display:flex; justify-content: space-between; padding:0 0 24px; border-bottom: 1px solid #999; margin-bottom: 50px;
           h3 {font-size: 32px;line-height: 42px;color: #111; font-weight: 600;}
           .privacy-selector {
              width:325px
           }
        }
     }
     .privacy_view-box {
        h4 {font-size: 24px;line-height: 34px;  margin:42px 0 12px;  font-weight: 600;color: #111;}
        p {color: #555;}
        table.privacy-assign_table {margin: 10px 0;}
     }
  }

  @media all and (max-width:$G-wMobile)  {
     &.type_panel-privacy {
        .panel-header {
           margin-bottom: 38px; padding: 0;
           .privacy-title {
              flex-direction: column; justify-content: flex-start; padding:0 0 25px; margin-bottom: 21.5px;
              h3 {font-size: 22px;line-height: 1;margin-bottom: 14px;width: 100%; }
              .privacy-selector {
                 width:100%
              }
           }
        }
        .privacy_view-box {
           h4 {font-size: 17px;line-height: 25px; margin:17.5px 0 7.5px;}
           table.privacy-assign_table {margin: 15px 0;}
        }
     }
  }


  &.type_panel-rules { 
     .rules_view-box {
        h3 {font-size: 24px;line-height: 32px; color: #111; font-weight: 600;}
        h4 {
           font-size: 18px;line-height: 32px; color: #555; font-weight: 700;
        }
        p {
           color: #555;
           & + h4, 
           & + h3 {margin-top: 32px;}
        } 
     }
 }
  @media all and (max-width:$G-wMobile)  { 
     &.type_panel-rules {
        .rules_view-box {
           h3 {font-size: 17px;line-height: 1; margin-bottom: 10px;}
           h4 {font-size: 14px;line-height: 21px;}
           p {
              color: #555;
              & + h4, 
              & + h3 {margin-top: 21px;}
           }
              
        }
     }
  }

  &.type_panel-introduce {
     .introduce_item-area {
        display:flex; flex-wrap: wrap;justify-content: space-between;
     }
     .introduce_item-box {
        padding: 39px;
        width: calc(50% - 10px); margin-bottom: 20px; border: 1px solid #d3d3d3;
        h4 {font-size: 26px; line-height: 34px; font-weight: 700; color: #333;}
        p {margin-top: 9px;} 
        display:flex; 
        .icon { width: 142px; img { width: 100%;}}
        .desc { width: calc(100% - 142px); padding-left: 40px;}
     }
     .history-area {
        position: relative; z-index: 1;
        .history-header {
           display:flex; justify-content: space-between;
           .select-box {display: inline-block;}      
        }
        .history-body {
           &.active {display: block;}
           display: none;
           margin-top: 100px;
           position: relative;
           &::before {content:""; position: absolute; top:50px; left:129px; width: 1px; bottom:0; background: #ccc;}
           
        }
        .history-item_area {
           display:flex;
           h5 {width: 129px;font-size: 32px;line-height: 34px;font-weight: 700;color: #333333;}
           .history-item_list { width: calc(100% - 129px);}
           &+.history-item_area {margin-top: 100px;}
        }
        .history-item_list {
           padding-bottom: 20px;
        }
        .history-item {
           &+.history-item {margin-top: 50px;}
           padding-left: 99px;
           position: relative;  z-index: 2;
           &::before {content:""; position: absolute;top:48px;left:0;width: 158px; height: 1px; background: #ccc;}
           &::after {content:""; position: absolute; top:37px;left:-11px;width: 11px;height: 11px; border: 6px solid #21a099;border-radius: 50%; background: #fff;}
           figure {
              position: relative; z-index: 3;
              display:flex; 
              img {width: 258px;}
              figcaption { margin-left: 48px; margin-top: -7px;}
           }
           ul {
              li {
                 font-size: 18px;line-height: 32px;color: #555; position: relative;
                 padding-left: 10px;
                 &::before {content:""; position: absolute; top:14px;left:0;width: 3px;height: 3px;background: #555;}
              }
           }

           &.no-img {
              padding-left: 405px;
              &::before {top:16px;width: 357px; }
              &::after {top:5px;left:-11px;} 
           }

        } 
     }
  }
  @media all and (max-width:$G-wMobile)  { 
     &.type_panel-introduce {
        .introduce_item-area {
           
        }
        .introduce_item-box {
           width: 100%; margin-bottom: 20px; padding: 25px 20px 22px;
           h4 {font-size: 16px; line-height: 1; text-align: center;}
           p {margin-top: 26px;} 
           display:flex;flex-direction: column;flex-wrap: wrap; justify-content: center;
           .icon { text-align: center; width: 71px; margin: 0 auto; }
           .desc { width: 100%; padding-left: 0; margin-top: 13px; }            
        }

        .history-area {
           position: relative; z-index: 1;
           .history-header {
              display:flex; justify-content: space-between; flex-wrap: wrap; 
              .select-box {margin-top: 25px;display: block;width: 100%;}      
           }
           .history-body {
              margin-top: 50px; 
              &::before {top:0; left:66px;}
           }
           .history-item_area { 
              h5 {width: 66px;font-size: 22px;line-height: 25px;flex-shrink: 0;}
              .history-item_list { width: calc(100% - 66px);}
              &+.history-item_area {margin-top: 70px;}
           } 
           .history-item_list {
              padding-bottom: 20px;
           }
           .history-list {flex-grow: 1;}
           .history-item {
              &+.history-item {margin-top: 45px;}
              padding-left: 58px;
              &::before {top:30px;left:0;width: 158px; height: 1px; background: #ccc;}
              &::after {top:22px;left:-8px;width: 9px;height: 9px; border: 4px solid #21a099; }
              figure { 
                 display:flex;flex-wrap: wrap; 
                 img {max-width:171px;}
                 figcaption { margin-left: 0; margin-top: 12px; width: 100%;}
              }
              ul {
                 li {
                    font-size: 13px;line-height:21px; 
                    padding-left: 10px;
                    &::before {top:9px;}
                 }
              }
              &.no-img {
                 padding-left: 58px; padding-top: 2px;
                 &::before {top:12px;width: 45px; }
                 &::after {top:4px;left:-8px;}
              }
           } 
        }
     }
  }
}