/* style_common.scss */
html,body,.wrapper { min-height:100%; width: 100%; }
body {  }
#container { min-height: 100%; }
#contents  {  
  min-height: calc(100vh); 
  min-height: calc(var(--vh, 1vh) * 100);
}
.body-inner { width:$G-width; margin: 0 auto; position: relative; }
.last-section { margin-bottom: 120px; }
@media all and (max-width:$G-wMobile)  {
  .last-section { margin-bottom: 60px; }
}



/* only */
.only-pc { display: block; } .only_ib-pc { display: inline-block; }
.only-mb { display: none;  } .only_ib-mb { display: none; } 
@media all and (max-width:$G-wMobile)  {
  .only-pc { display: none; } .only_ib-pc { display: none; }
  .only-mb { display: block;  } .only_ib-mb { display: inline-block; } 
}

/* ==========================  text item */
.text-notice    { display: block;
  font-size:18px; font-weight: normal; vertical-align: middle;
  line-height: 1.25; letter-spacing: -0.5px; font-style: normal;
  i, span {vertical-align: top;}
}
.colorB { color: $colorB; } // 강조진하게
.colorB2 { color: $colorB2; } // 강조진2하게
.colorP { color: $colorP; } // 포인트 컬러
.colorD { color: $colorD; } // 기본 컬러
.colorS { color: $colorS; } // 서브컬러
.colorL { color: $colorL; } // 저체도
.colorE { color: $colorE; } // 에러


/* ========================== 컨텐츠 간격  */
.mt_st { margin-top: 50px;}  // 섹션
.mt_ct { margin-top: 40px;}  // 컨텐츠
.mt_it { margin-top: 30px;}  // item
.mt_tb { margin-top: 20px;}  // title, body 간격
.mt_p  { margin-top: 10px;}  // 문단

.target_link { 
  &a, &:link {
    text-decoration: underline; 
  }
}


/* ==========================  비율  */
.img-ratio {position: relative; width: 100%; height: 0; 
  padding-bottom: 100%; text-align: center;  
}

/* ==========================  정열  */
.img-align { position: absolute; right: 0; left: 0;
  vertical-align: middle;  width: auto;  height: 100%;
  text-align: center; font-size: 0; overflow: hidden;
  &::before { content: ''; display: inline-block;  height: 100%; width: 0;
    vertical-align: middle; font-size: 0;
  }
  img { display: inline-block; 
    height: auto; width: auto; max-width: 100%; max-height: 100%;
    vertical-align: middle;
  }
}


/* ==========================  섹션 */

/* 섹션 공통 */
.basis-section{ margin-top: 60px; width: 100%; max-width: 1420px; padding:0 50px;
  margin-left: auto; margin-right: auto;
  .section-inner {position: relative; }
  .pc-full { margin-left: -60px; width: calc( 100% + 120px );}
  .mb-full { }
  &.tab-cont-section {margin-top: 0;}
  &.edit_view-section {margin-top: 0;}

}
@media all and (max-width:$G-wMobile)   {
  .basis-section{ margin-top: 20px; padding: 0 25px;}
  .pc-full { margin-left: -25px; width: calc( 100% + 50px );}
  .mb-full { margin-left: -25px; width: calc( 100% + 50px );}
}
section[class$="-section"] { 
  .section-inner.full_size  { 
   
  }
}

/* 페이지 타이틀 */
.page_title-section { 
  .section-inner {   background-color: #f6f6f6; }
  .center-box { min-height: 180px; width: 100%; margin: 0 auto;
    max-width: 1420px; padding: 0 50px;
    display: flex; flex-direction: column; justify-content: center; 
    height: 180px;
  }
  .page_title { color:#111; 
    @include before-middle();
    font-size: 0;line-height: 0;
    span {vertical-align: middle;}
  }
  @media all and (max-width:$G-wMobile)  {
    .section-inner { }
    .center-box { height: auto; min-height: 90px;  padding: 15px 25px; }
    .page_title {
      span { font-size: 26px; }
    }
  }


  &.type-dark { 
    .section-inner { background-color: #243233; }
    .center-box { align-items: flex-start; }
    .page_title { font-family: $subFT; 
      span { font-size: 38px; font-weight: bold; color:#fff;}
    }
    .page_desc  { span {font-size: 18px; font-weight: 500; color:#dedede;} }
    @media all and (max-width:$G-wMobile)  {
      .page_title { span { font-size: 24px; } }
      .page_desc  { span { font-size: 14px; } }
    }
  }

  &.type-img {
    .section-inner { background-position: center; background-repeat: no-repeat;
    }
    .center-box { align-items: flex-start; height: 300px; }
    .page_title { font-family: $baseFT;  
      span { font-size: 42px; font-weight: 600; color:#fff; line-height: 1.5;}
    }
    .page_desc  { span { font-size: 20px; font-weight: 500; color:#fff; line-height: 1.5;} }
    @media all and (max-width:$G-wMobile)  {
      .center-box { height: 250px; }
      .page_title { span { font-size: 25px; line-height: 1.36;} }
      .page_desc  { span { font-size: 14px; line-height: 1.6; } }
    }

    &.zerobase { 
      .section-inner { background-image: url("/images/pc/auction/title_bg-zerobase.jpg"); }
      @media all and (max-width:$G-wMobile)  {
        .section-inner { background-image: url("/images/mobile/auction/title_bg-zerobase.jpg"); }
      }
    }

    &.introduce {
      .section-inner { background-image: url("/images/pc/footer/title_bg-introduce2.jpg"); background-color: #fff; }
      .center-box {height: 600px;}
      .page_desc {margin-top: 5px;
         span{font-size: 24px;line-height: 1.36;font-weight: 400;}
      }
      .page_btn { margin-top: 60px;
         .btn {
            width: 170px; border-color: #fff; color: #fff;
            span {color: #fff;}
            &:hover {
               span {color: #111;}
            }
         }
      }
      @media all and (max-width:$G-wMobile)  {
         .section-inner { background-image: url("/images/mobile/footer/title_bg-introduce.jpg"); -webkit-background-size: cover;         background-size: cover;}
         .center-box {height: 400px;}
         .page_title { span { font-size: 26px; line-height: 1;} }
         .page_desc  { span { font-size: 17px; line-height: 25px; } }
         .page_desc {margin-top: 12px;}
         .page_btn {
            margin-top: 46px;
            .btn {
               width: 160px; 
            }
         }
      }
    }
    &.history {
      .section-inner { background-image: url("/images/pc/footer/title_bg-history.jpg"); }
      .center-box {height: 180px;}
      .page_desc {margin-top: 5px;
         span{font-size: 24px;line-height: 1.36;font-weight: 400;}
      }
      .page_btn { margin-top: 60px;
         .btn {
            width: 170px; border-color: #fff; color: #fff;
            span {color: #fff;}
            &:hover {
               span {color: #111;}
            }
         }
      }
      @media all and (max-width:$G-wMobile)  {
         .section-inner { background-image: url("/images/mobile/footer/title_bg-history.jpg"); -webkit-background-size: cover;         background-size: cover;}
         .center-box {height: 90px;}
         .page_title { span { font-size: 26px; line-height: 1;} }
         .page_desc  { span { font-size: 17px; line-height: 25px; } }
         .page_desc {margin-top: 12px;}
         .page_btn {
            margin-top: 46px;
            .btn {
               width: 160px; 
            }
         }
      }
    }
  }
 
}


/* ==========================  content-panel (type-컨텐츠이름) */
.content-panel{  
  >.panel-header { padding: 35px 0;
    .title {   
      span { color:$colorB; line-height: 40px; }
    }
    .desc { margin-top:20px;}
    
    .ab-r { font-size: 0;}
  }
  >.panel-body {   } 
  >.panel-footer {   } 
  @media all and (max-width:$G-wMobile)  {
    >.panel-header { padding: 15px 0;
      .title {   
        span { line-height: 30px; }
      }
      .desc { margin-top:10px;}
    }
  }


  /* ==== 게시판 상세 ====  */
  &.type_panel-edit_view{ padding: 0; margin-top: 100px;
    >.panel-header{ padding: 0; }
    >.panel-body  { margin-top: 40px;}
    >.panel-footer{ border-top: 1px solid #cccccc;
      .inquiry-article  { margin-top: 100px;}
      .button-area { padding: 0; margin-top: 60px;
        .btn.btn_lg {  width: 180px; } 
      }
    }
    @media all and (max-width:$G-wMobile)  { margin-top: 42px;
      >.panel-body  { margin-top: 10px;}
      >.panel-footer{  
        .inquiry-article  { margin-top: 50px;}
        .button-area { padding: 0; margin-top: 30px;
          .btn.btn_lg {  width: 160px; } 
        }
      }
    }

    /* 진행 중인 OR 신청하기 아카데미 */
    .edit_view-header-area { 
      .area-inner { position: relative; padding: 40px 40px;  display: flex; 
        border-top: 1px solid #999;
        background-color: $colorBGw; }
      .category { position: relative; padding: 0px; text-align: center;      
        i {  vertical-align: middle; }
        span { font-size: 22px; color:$colorB; font-weight: bold; line-height: 40px;}
        margin-right: 20px; padding-right: 20px;
        &::after { position: absolute; top: 10px; right: 0; content: "";
          width: 2px; height: 20px; background-color: #999;
        }
      }
      .title    { position: relative;
        display: flex; flex-direction: column; flex-wrap: nowrap; justify-content: center;
        width: auto; width: calc(100% - 226px - 284px);
        span { font-size: 24px; font-weight: bold; color:$colorB;}
        /* span { @include ellipsis-line(2); } */
      }
      .date { display: flex; flex-direction: column; flex-wrap: nowrap; justify-content: center;
        position: relative; width: 284px;
        @include list-div_bar( 20px, 0, 60px, #ccc);      
        span  { font-size: 18px; line-height: 1.78; text-align: right;}
      }
      @media all and (max-width:$G-wMobile)  {
        .area-inner { padding: 25px 20px;  display: block; flex-direction: initial;}
        .category { display: inline-block;
          i { vertical-align: middle; width: 40px; height: 40px; overflow: hidden; 
            &::after { margin-left: -5px; margin-top: -5px;}
          }
          span { font-size: 15px; padding-right: 0px; line-height: 25px;}
          margin-right: 10px; padding-right: 10px;
          &::after { position: absolute; top: 5px; right: 0; content: "";
            width: 2px; height: 14px; background-color: #999;
          }
        }
        .title    { margin-top: 10px; padding: 0 0px; width:100%;
          span { font-size: 17px; line-height: 25px; }
        }
        .date { display: block; border-top: 1px solid #ccc; margin-top: 17px; padding-top: 20px;
          position: relative; width: 100%;
          &::before{ display: none;}    
          span  { font-size: 13px; line-height: 1.78; text-align: left;}
        }
      }
    }

    /* 게시판 해더 */
    .edit_view-header_notice-area {
      .area-inner { height: 154px; padding: 0 40px;
        border-top: 1px solid #999; background-color: $colorBGw;
        display: flex; flex-direction: column; justify-content: center;
      }
      .title { font-size: 24px; font-weight: 700; color:$colorB; line-height: 1.36; }
      .desc  { margin-top: 0; font-size: 18px; font-weight: 500; color:$colorS;  }
      @media all and (max-width:$G-wMobile)  {
        .area-inner { height: 89px; padding: 0 20px; 
        }
        .title { font-size: 17px; line-height: 1.5; }
        .desc  { font-size: 13px;  }
      }
    }

    /* 아카데미 상세, 등록 */
    .edit_view-area { padding: 0 40px; }
    .edit_view-area + .edit_view-area  { margin-top: 40px;}
    .button-area { padding: 60px 0; text-align: center;
      .btn_set-float{
        .btn { width: 240px;}
      }
      .text-notice { margin-top: 30px; line-height: 20px;  color:$colorB2;
        i { margin-right: 5px; margin-top: -2px;  vertical-align: middle; }
        span { font-size: 18px; font-weight: 500; vertical-align: middle;  }
        span + span { margin-left: 10px; }
      }
    }
    @media all and (max-width:$G-wMobile)  {
      .edit_view-area { padding: 0 20px; }
      .edit_view-area + .edit_view-area  { margin-top: 10px;}
      .btn_set-float{
        .btn { width: 200px;}
      }
      .button-area { padding: 30px 0; text-align: center;
        .text-notice { margin-top: 30px; line-height: 1.3; text-align: center;
          i { margin-right: 0px; margin-top: -3px; }
          span { font-size: 13px; line-height: 1.3; }
          span + span { margin-left: 0px; }
        }
      }
    }
  }



}

.con_box-panel{ border: 1px solid  #ccc; 
  >.p-header { padding: 10px; }
  >.p-body   { padding: 20px 10px; border-top: 1px solid #ccc;}
  >.p-footer { padding: 10px; border-top: 1px solid #ccc; }
}
/* ========================== //  content-panel (컨텐츠이름-panel) */

  

/* ========================== typo list */
.mark_char_item { position: relative; padding-left: 0.7em; 
  &::before { content: "-"; display: inline-block; text-indent: -.7em; } 
}
.mark_char-list {
  li { position: relative; padding-left: 0.7em; 
    &::before { content: "-"; display: inline-block; text-indent: -.7em; vertical-align: top; } 
  }
  li.accent { color:$colorE; }
}

.mark_item { position: relative; padding-left: 1em; text-indent: -1em;  }
.mark-list {
  li { position: relative; padding-left: 1em; text-indent: -1em;  font-weight: 500; line-height: 1.5; }
  li.accent { color:$colorE; }
}

.mark_dot-list{
  li { position: relative; padding-left: 0.5em; 
    &:after { content: ""; display: block; width: 3px; height: 3px; 
      position: absolute; top:11px; left: 0; 
      background-color: $colorD;  
    }
  }   
  @media all and (max-width:$G-wMobile)  {
    li{
      &.text-help{
        &:after { top: 8px;}
      }
    }
  }
  &.type-point {
    li  { line-height: 1.78;
      &:after { content: ""; display: block; width: 4px; height: 4px; 
        position: absolute; top:13px; left: 0;  border-radius: 50%;
        background-color: $colorP;  
      }
      em { color:$colorP;}
    }
    @media all and (max-width:$G-wMobile)  {
      li  { line-height: 1.5;
        &:after { content: ""; display: block; width: 2.5px; height: 2.5px; 
          position: absolute; top:10px; left: 0; 
          background-color: $colorP;  
        }
      }
    }
  }
}
.caution-list{
  li { position: relative; padding-left: 0.5em; 
  }   
  @media all and (max-width:$G-wMobile)  {
  }
}



/* ========================== table_style default */
.table-panel{ position: relative;
  >.panel-header { position: relative; padding: 15px 0;
    .title {  color:$colorB2; line-height: 35px; 
      span { line-height: 35px; }
      em { color:$colorP; }  
    }
    
  }
  >.panel-body   {   } 
  .ab-r { font-size: 0;
    .btn_set  { display: inline-block;}
    .typo_set { display: inline-block; margin-top: 15px;}
  }
  >.panel-footer {}
  .data-empty { border:0; }
  .dataTables_length { font-weight: 700; color:#111; margin-bottom:15px;
    &.result_txt{font-size: 20px;}
    span { 
      em { color:#21a099; margin-left: 5px; }
    }
  }
  @media all and (max-width:$G-wMobile)  { 
    >.panel-header { position: relative; padding: 10px 0;
      .title {  color:$colorB; line-height: 36px; 
        span { line-height: 36px; }
        em { color:$colorP; }  
      }
      
    }
    >.panel-body   {   } 
    .ab-r { font-size: 0;
      .btn_set  { display: inline-block;}
      .typo_set { display: inline-block; margin-top: 15px;}
    }
    .dataTables_length { margin-bottom:10px;
      &.result_txt{font-size: 15px;}
    }

  }
  
};

.table_base {  width: 100%; table-layout:fixed; 
  th, td { 
    text-align: center; padding: 3px 10px; height: 55px;
  }
  th { font-size: 18px; font-weight: 600; color:#333; }
  td { font-size: 16px; font-weight: 500; /* line-height: 34px; */
    @include wordBreak-word();
    strong { color: #333; }
    span   { color: #555; } 
  }
  @media all and (max-width:$G-wMobile)  {
    th, td { 
      text-align: center; padding: 2px 5px; height: 45px;
    }
    th { font-size: 15px; }
    td { font-size: 14px; line-height: 20px; }
  }
  th.vet, td.vet { vertical-align: top;   }
  th.vat, td.vat { vertical-align: top;   }
  th.tal, td.tal { text-align: left;   }
  th.tar, td.tar { text-align: right;  }
  th.tac, td.tac { text-align: center; }
  th.bdl, td.bdl {  border-left:  $borderLine !important; }
  th.bdr, td.bdr {  border-right: $borderLine !important; }
  th.bdt_b, td.bdt_b {  border-top: $borderTop !important; }
  .top_bold_bar { border-top: $borderTop !important; }
  .topbar_none  { border-top: 0px !important;};
  .bottom_bar   { border-bottom: $borderBottom !important; }
}


/* ========================== table_list : .name-XXXX */
.list-table{ 
  th { padding: 15px 20px;  text-align: center; 
    border-bottom: $borderLine;  
    color:$colorB; background-color: #f7f8f8;         
  }
  td {  padding: 15px 20px; text-align: center;
    border-bottom: $borderLine;  
    .td-inner { height: 46px; }
    .td-inner.oneline { padding-top: 13px; }
  }    
  th.tal, td.tal { text-align: left; }
  th.tar, td.tar { text-align: right; }
  th.tac, td.tac { text-align: center; }
  tbody tr:first-child td { border-top: 0;}
  &.top_bold_bar { border-top:1px solid #999;}
  

  @media all and (max-width:$G-wMobile)  {
    td {  padding: 15px 10px; }
  }
}

/* ==========================  form table : .name-XXXX */
.form-table { 
  th { padding:10px 30px; background: #f6f6f6; 
    text-align: left;font-size: 16px; color:$colorB2; line-height: 1.36;
    .data_th { 
      min-height: 48px;
      display: flex; flex-direction: column; flex-wrap: nowrap; justify-content: center;
      span { vertical-align: middle; font-size: 16px; color:$colorB2; line-height: 1.36;}
    }
  }
  th,
  td { padding:10px 20px; border-bottom:1px solid #e6e6e6; height:30px; text-align: left;
    .textarea-box { position: relative; 
      textarea { height: 70px;}
    }
    .data_out { min-height: 48px;
      display: flex; flex-direction: column; flex-wrap: nowrap; justify-content: center;
      span { vertical-align: middle; font-size: 16px; color:$colorD; line-height: 1.36;}
    }
    .edit_module { height: 150px; overflow-y: auto;}
    .form_alert { display: none; margin-top: 5px; color: #ea0530; font-size: 12px; }
  }
  th i { color:#ea0530; margin-left:3px;}
  td label { margin-right:30px;}
  td input,
  td a { vertical-align: middle; }
  tr.disabled {
      th, td { color:#aaaaaa;}
  }

  &.type-reactive{
    
  }
}
/* 반응형 */
.form_table-list { display: flex; flex-wrap: wrap; border-bottom: 1px solid #cfcfcf; 
  >.panel-header { position: relative; 
    .title {  em { color:$colorP; } }
  }
  .panel-body   { border-top: $borderTop;  } 

  &.col_size-50-100 { 
    li { width:50%;}
    @media all and (max-width:$G-wMobile)  { 
      li { width: 100%;}
    }
  }

  &.data_size-165 { 
    .th { width: 165px;}
    .td { width: calc( 100% - 165px);}
    @media all and (max-width:$G-wMobile)  { 
      .th { width: 95px;}
      .td { width: calc( 100% - 95px);}
    }
  }

  >li { display: flex; width: 100%; border-top: 1px solid #cfcfcf; }
  .th,
  .td { display: flex; flex-direction: column; flex-wrap: nowrap; justify-content: center;
    em { color:$colorP; font-weight: bold;}
  }
  .th { padding: 10px 30px; min-height:  70px; background-color: $colorBGw;
    font-size: 18px; font-weight: 500; color:$colorB2;
    span , label { }
  }
  .td { padding: 10px 20px;  
    font-size: 18px; font-weight: 500; color:$colorB2;
  }

  @media all and (max-width:$G-wMobile)  { 
    >li:first-child { border-top: 1px solid #999; }
    .th { padding: 5px 15px; min-height:  45px;
      font-size: 14px; font-weight: 600;
      span , label { }
    }
    .td { padding: 5px 10px;  
      font-size: 14px; 
    }
  }

   //1:1문의스타일
   &.data_size-185 { border-top:1px solid $colorL;
    &.data_size-290{
      .th{ width: 261px;}
      .td { width: calc( 100% - 261px);}
    }
    >li {
      &:first-child { border-top:0;}
    }
    .th { width: 190px; min-height: 46px; line-height: 46px; background:none; font-size: 20px; padding: 10px 20px; display: flex; flex-direction: row; align-items: flex-start; justify-content: flex-start;
      label { font-weight: 600; font-size: 20px;}
      i { color:$colorE; margin-left: 3px; margin-top:3px; vertical-align: middle; }
    }
    .td { width: calc( 100% - 190px);}

    //select 2개
    .col_select { display: flex;
      .select-box + .select-box { margin-left: 10px;}
    }

    //input옆에txt
    .input_grid { display: flex; align-items: center;
      span { margin-left: 10px;}  
    }

    @media all and (max-width:$G-wMobile)  { 
      &.data_size-290{
        .th{ width: 100%;}
        .td { width: 100%;;}
      }
      >li { flex-direction: column; border-top:0;
        &:last-child { padding-bottom:25px;}
      }
      .th, .td { width: 100%;}
      .td {  padding:0;}
      .th { min-height: auto; padding: 24px 0 10px; font-size: 15px; line-height: normal;
        label { font-size: 15px;}
      }
      
      //select 2개
      .col_select { flex-direction: column;
        .select-box + .select-box { margin: 10px 0 0;}
      }


      
    }
  }


}

/* ==========================  data table : .name-XXXX */
.data-table { width:100%;
  border-top: 1px solid #999;
  th, 
  td { border: 1px solid #ccc; }
  th { background:#f3f3f3; }
  tr:first-child > th, tr:first-child > td { border-top: 0px;} 
  tr:last-child  > th { border-bottom: 0px;} 
  tr > th:first-child, tr > td:first-child { border-left: 0px; }
  tr > th:last-child,  tr > td:last-child  { border-right: 0px; }

   &.name-condition {
      th, 
      td { height: 60px;}
      th { background:#f8f8f8; }
   }
}
@media all and (max-width:$G-wMobile)  {
   .data-table { 
      &.name-condition {
         th, 
         td { height: 45px;}
         th { font-size: 14px; }
         td { font-size: 13px; line-height: 20px; }
      }
   }
}
// 데이터없음.
.data-empty{ padding: 50px 0;  text-align: center; color:#999; 
  &.type-big { padding: 100px 0;
    .img_empty { img { width: 74px; }}
    .txt_empty { margin-top: 35px; text-align: center;
      .title { 
        font-size: 24px; font-weight: 600; color:$colorB2; }
      .desc  { margin-top: 10px;
        font-size: 16px; font-weight: 500; color:$colorD; line-height: 1.5;
      } 
    }
    .empty_btn { margin-top: 35px; text-align: center;
      .btn { width: 170px; 
        span { color:#555;}
      }
    }
  }

  @media all and (max-width:$G-wMobile)  {
    padding: 25px 0; 
    &.type-big { padding: 50px 0;
      .img_empty { img { width: 50px; }}
      .txt_empty { margin-top: 25px; 
        .title { font-size: 17px;}
        .desc  { margin-top: 10px; font-size: 13px; line-height: 1.5; } 
      }
    }
  }
}


