.contents, .popupfixed-wrap, .filter_fixed-wrap {
  select,button,input {
    font-family: $baseFT, Malgun Gothic, dotum,"돋움",Arial,AppleGothic, Sans-serif; 
  }
  /* ======================== input text  */
  /* placeholder */
  // ie
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"], 
  textarea { &.placeholdersjs {color: $colorPH;} }
  // Internet Explorer 10 이상에 적용select2Basic42
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"], 
  textarea { &:-ms-input-placeholder {color: $colorPH;} } 
  //  Webkit, Blink, Edge에 적용
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"], 
  textarea { &::-webkit-input-placeholder {color: $colorPH;} }
  // Mozilla Firefox 4 부터 18에 적용
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"], 
  textarea { &::-moz-placeholder    {color: $colorPH;} }
  // input style
  em.star { display: inline-block; height: 14px; padding: 0 5px; color: #f24848; font-weight: bold; }
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"] {
    width: 100%; height: $formH; padding: 8px 15px 8px;
    font-size: 18px; vertical-align: middle;
    border: 1px solid $lineC_d; outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  @media all and (max-width:$G-wMobile)  { 
    input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"] {
      font-size: 14px; padding: 5px 15px 5px; line-height: 22px; height: 43px;
    }
  }

  
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"]
  { &.h56 { height:56px; } }
  @media all and (max-width:$G-wMobile)  { 
    input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"] {
      &.h56{
        font-size: 13px; padding: 5px 15px 5px; line-height: 22px; height: 53px;
      }
    }
  }
  
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"]
  { &.h42 { height:42px; font-size: 16px; } }
  @media all and (max-width:$G-wMobile)  { 
    input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"] { 
      &.h42 { font-size: 13px; padding: 5px 15px 5px; line-height: 22px; height: 43px;}
    }
  }
  
  
  /* ======================== focus, ,disabled, input_error */
  /* focus */
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"]
  { &:focus , &:hover {border-color: $lineC_f;} }
  /* read only,  disabled */
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"] { 
    &:readonly, &.readonly, &:disabled, &.disabled { background-color : $colorBGw;  color: $colorPH;
      &+i:first-child { z-index: 1;}
    }
  }
  /* error */
  input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"]
  { &.input_error {border-color: $lineC_e;} }
  
  .input-box{ position: relative;  vertical-align: top;
    .error_msg { margin-top: 10px; display: inline-block;
      font-size: 16px; line-height: 1.36; color:$colorE;}
    .check_byte-w { padding-right:  90px !important; }
    .check_byte-h { padding-bottom: 50px !important; }
    label, .dash  { display: inline-block; 
      padding: 8px 20px 8px ; box-sizing: border-box; }
    label:frist-child { padding-left: 0;  }
    .dash { padding: 8px 0px 8px 0px; } 
    input[type="text"], input[type="password"], input[type="number"], input[type="url"], input[type="email"], input[type="search"] {
      padding: 8px 15px 8px ;            
    }
    .btn { margin-top:-1px;}
    .byte_check { position: absolute; top:10px; right:15px;
      em   { vertical-align: middle; font-size:14px;  color: #e90000; }    
      span { vertical-align: middle;  font-size:14px; }    
    }
    @media all and (max-width:$G-wMobile)  {
      .error_msg { margin-top: 8px; font-size: 13px; }
    }
  }
  
  /* input 삭제 */
  .input_del { position: relative; display: block; 
    input { padding-right: 25px; padding-right: 48px; } 
    button {  display: none; position: absolute; top:50%; right: 16px; margin-top: -10px;
      font-size: 0;line-height: 0;
    }
    @media all and (max-width:$G-wMobile)  {
      button { margin-top: -8px; }
    }
  }
  
  /* 검색 */
  .search-box {  position: relative;
    input { padding-right: 40px; padding-left: 20px; }
    .form-search_md { position: absolute; top:7px; right: 8px; width: 30px; height: 30px; }
    @media all and (max-width:$G-wMobile)  {
      select{  padding: 10px 15px;  font-size: 13px; line-height: 21px; }
      .form-search_md { position: absolute; top:6px; right: 8px; width: 30px; height: 30px; }
    }
  }
  
  
  /* ======================== textarea  */
  .textarea-box { 
    textarea { width: 100%; height: 145px; padding: 8px 15px 8px; border: 1px solid $lineC_d; font-size: 18px;
      -webkit-appearance: none;
      -moz-appearance: none;
      outline: none;
      &:focus , &:hover {border-color: $lineC_f;}
    }
    .byte_check { margin-top:0px; text-align: right; color:$colorS;font-size:16px;
      em   { vertical-align: bottom; font-size:16px;  color: $colorP; font-weight: 400; }    
      span { vertical-align: bottom;  }
    }

    &.byte {
      display: flex; align-items: flex-end; justify-content: space-around;
      textarea { width: calc(100% - 110px); }
      .byte_check { margin-left:15px; width:95px; text-align: left; }
    }

    @media all and (max-width:$G-wMobile)  {
      textarea { height: 86px;}

      &.byte { align-items: flex-end; flex-direction: column;
        textarea { width: 100%; height: 255px; font-size: 14px;}
        .byte_check { margin-top:10px; font-size: 13px; width: 100%; text-align: right;
          em { font-size: 13px;}
        }
      }

    }
  }


  /* ======================== input byte  */
  .input-byte { 
    display: flex; align-items: center; justify-content: space-around;
    input { width: calc(100% - 110px); }
    .byte_check { margin-left:15px; width:95px; text-align: left; color:$colorS; font-size:16px;
      em   { font-weight: 400; color: $colorP; font-size:16px;}
    }

    @media all and (max-width:$G-wMobile)  {
      align-items: flex-end; flex-direction: column;
      input { width: 100%;}
      .byte_check { margin-top:10px; font-size: 13px; width: 100%; text-align: right;
        em { font-size: 13px;}
      }
    }
  }
  
  
  /* ========================  input radio */
  .trp.radio-box +  .trp.radio-box  { margin-left: 40px; }
  .trp.radio-box { position: relative; display: inline-block;  padding-left: 30px;  min-height: 24px;
    label { display: inline-block; height: 24px; margin-left: 0px; 
      font-size: 18px; font-weight: 500; line-height: 24px;
    }
    em { display: inline-block; height: 20px;    text-indent: 0px; }
    input[type="radio"] { position: absolute; top:0; left: 0; /*  width:100%; */ width: 24px; height: 24px; margin: 0; padding: 0;  opacity: 0; filter: alpha(opacity=0); z-index: 1;  }    
    input[type="radio"] + i { display: inline-block; position: absolute; top: 2px; left: 0px;  }
    input[type="radio"] + i:after { display: block; content: '';  @include sp_icons($form-radio-off); }
    input[type="radio"]:checked + i:after {  @include sp_icons($form-radio-on); }
    input[type="radio"]:disabled + i      {  @include opacity(0.5);
      &:after { display: block; content: '';  /* @include sp_icons($form-radio-dim); */ }
    }
    input[type="radio"]:disabled + i +label  {  @include opacity(0.5);/*  color:$colorL; */ }
    @media all and (max-width:$G-wMobile)  {  padding-left: 25px; min-height: 17px;
      & + .trp.radio-box  { margin-left: 30px; }
      label { height: 17px; 
        font-size: 14px; line-height: 17px;
      }
      input[type="radio"] { width: 17px; height: 17px; }    
      input[type="radio"] + i:after { display: block; content: '';  @include sp_icons2x($form-radio-off-2x); }
      input[type="radio"]:checked + i:after {  @include sp_icons2x($form-radio-on-2x); }
    }
  }
  .trp.radio-box.re { 
    label { display: inline-block; height: 20px; vertical-align: middle;  text-indent: 0px; }
    input[type="radio"] + i { position: relative;  }
  }
  
  
  /* ========================  input checkbox */
  .trp.checkbox-box +  .trp.checkbox-box  { margin-left: 40px; }
  .trp.checkbox-box { position: relative; display: inline-block;  padding-left: 30px; min-height: 24px;
    label { display: inline-block; height: 24px; margin-left: 0px; 
      font-size: 18px; font-weight: 500; line-height: 24px;
    }
    em { display: inline-block; height: 20px;     text-indent: 0px;}
    input[type="checkbox"] { position: absolute; top:0; left: 0;  /*  width:100%; */ width: 24px; height: 24px; margin: 0; padding: 0;  opacity: 0; filter: alpha(opacity=0); z-index: 1; }    
    input[type="checkbox"] + i { display: inline-block; position: absolute; top: 2px; left: 0px;  }
    input[type="checkbox"] + i:after { display: block; content: '';  @include sp_icons($form-checkbox-off); }
    input[type="checkbox"]:checked + i:after {  @include sp_icons($form-checkbox-on); }
    input[type="checkbox"]:disabled + i      {  @include opacity(0.5);
        &:after { display: block; content: '';  /* @include sp_icons($form-checkbox-dim); */ }
    }
    input[type="checkbox"]:disabled + i +label  { @include opacity(0.5);  /* color:$colorL; */ }
    @media all and (max-width:$G-wMobile)  {   padding-left: 25px; min-height: 17px;
      & + .trp.checkbox-box  { margin-left: 30px; }
      label { height: 17px; 
        font-size: 14px; line-height: 17px;
      }
      input[type="checkbox"] { width: 17px; height: 17px; }    
      input[type="checkbox"] + i:after { display: block; content: '';  @include sp_icons2x($form-checkbox-off-2x); }
      input[type="checkbox"]:checked + i:after {  @include sp_icons2x($form-checkbox-on-2x); }
    }
  }
  .trp.checkbox-box.re {
    label { display: inline-block; height: 20px; text-indent: 0px; }
    input[type="checkbox"] + i { position: relative;  }
  }
  
  //input btn
  .input-group { width: 100%; display: flex; justify-content: space-between;
    input { width: calc( 100% - 140px);}
    .btn { width: 140px; border-left: 0;}
    @media all and (max-width:$G-wMobile)  {
      input { width: calc( 100% - 115px);}
      .btn { width: 115px; height: 43px;}
    }
  }
  .error_text { color:$lineC_e;}

  //search btn
  .search-group { width: 100%; display: flex; justify-content: space-between;
    input { width: calc( 100% - 65px);}
    .btn { width: 85px;}
    @media all and (max-width:$G-wMobile)  {
      input { width: calc( 100% - 85px);}
      .btn { width: 85px; height: 43px;}
    }

    //작은 사이즈
    &.sm {
      input { height: 42px; font-size: 16px;}
      .btn { height: 42px; padding:3px 15px 3px}
      @media all and (max-width:$G-wMobile)  {
        input { width: calc( 100% - 85px); font-size: 13px; padding: 5px 15px 5px; line-height: 22px;}
        .btn { width: 85px;}
      }
    }
  }

  //비밀번호마스킹해제 - 공통
  .pw_mask_box { position: relative;
    input[type="password"] { padding-right:63px; }
    .input-side {position: absolute; right:0; top:0; padding: 10px 16px;}
    .btn_pw_mask { font-size: 0; @include sp_icons($icon_password_hide_off);
      &.on { @include sp_icons($icon_password_hide_on); }
    } 
    @media all and (max-width:$G-wMobile)  {
      input[type="password"] { padding-right:53px; }
      .input-side { padding: 8px 15px;}
    }
  }
  

  /* ========================  select : 44 < 56 */
  .select-box { position: relative;
    select { width: 100%; padding: 9px 15px 9px 15px; background-color: transparent;
      border: 1px solid $lineC_d; border-radius: 0px; -webkit-appearance: none; 
      font-size: 18px; line-height: 24px;
      &:hover {border: 1px solid $colorB2;}
      @media all and (max-width:$G-wMobile)  {
        font-size: 14px;
      }
    }
    /* .icon-select_arrow */ 
    >i{ position: absolute; top:4px; right: 8px; width: 36px; height: 36px; 
      z-index: 1; pointer-events: none; background-color: #fff;
    }
  
    &.h56-point_line  {
      select{ padding: 13px 15px;  font-size: 18px; font-weight: 600;  color:$colorB2;
        line-height: 24px; 
        border: 3px solid $colorMain;
      }
      /* .icon-select_arrow */ 
      > i{ position: absolute; top:10px; right: 8px; width: 36px; height: 36px; }
      @media all and (max-width:$G-wMobile)  {
        select{ padding: 13px 15px 13px 15px; font-size: 15px; line-height: 22px; }
      }
    }
  
    /* h42 -> 43 */
    &.h42-line  { 
      select{ padding: 10px 40px 10px 20px;  font-size: 16px; font-weight: 500; color:$colorD;
        line-height: 20px;  
      }
      .form-select_arrow_md { position: absolute; top:6px; right: 7px; width: 30px; height: 30px; }
      @media all and (max-width:$G-wMobile)  {
        select{  padding: 10px 40px 10px 20px;  font-size: 13px; line-height: 21px; }
        .form-select_arrow_md { position: absolute; top:7px; right: 7px; width: 30px; height: 30px; }
      }
    }
    
    /* 드롭다운 커스텀 */
    .trp-dropdown-area { position: relative;
      &.h42-line {
        button { padding: 10px 40px 10px 0px;  font-size: 18px; font-weight: 500; color:$colorD;
          line-height: 22px;  
          em   { color:$colorP; font-weight: 600; }
          span { font-size: 16px; color:$colorB2; margin-left: 10px; font-weight: bold; }
        }
        .form-select_arrow_md { position: absolute; top:6px; right: 7px; width: 30px; height: 30px; ; }
        @media all and (max-width:$G-wMobile)  {
          button{  padding: 6px 40px 6px 0px;  font-size: 14px; line-height: 23px; 
            em   { font-weight: 600; }
            span { font-size: 13px; font-weight: 600;}
          }
          .form-select_arrow_md { position: absolute; top:2.5px; right: 7px; width: 30px; height: 30px; }
        }
      }
      .trp-dropdown_list-box { position: absolute; top:42px;  display: none;  z-index: 10; 
        padding: 10px 0 10px;
        background-color: #fff; border: 1px solid #ccc; 
        min-width: 100%;
        .search-box { border-bottom: 1px solid #ccc;
          input { border: 0; font-size: 16px;}
          i { top: 9px;}
        }
        .list-box { max-height: 520px;}
        li.on a {  background-color: #ededed; }
        a { display: flex; align-items: center; padding: 10px 15px;
          .image-area { width: 50px; }
          .typo-area  { width: calc(100% - 50px); padding-left: 18px; 
            text-align: left;
          }
          &:hover { background-color: #ededed;}
          em { color:$colorP; font-weight: 600; }
          span { font-size: 16px; font-weight: 600; color:$colorD; }
        }
        @media all and (max-width:$G-wMobile)  { 
          top:35px;   min-width: 200px;
          .search-box { margin-top: 0;
            input { border: 0; font-size: 16px;}
            i { top: 5px;}
          }
          a { font-size: 14px; } 
          
        }
      }

      &.h42-lines {
        button { padding: 10px 40px 10px 20px;  font-size: 18px; font-weight: 500; color:$colorD; border: 1px solid #cccccc;
          line-height: 20px; width: 100%; text-align: left;
          em   { color:$colorP; font-weight: 600; }
          span { font-size: 16px; color:$colorB2; margin-left: 0px; font-weight: bold; }
        }
        .form-select_arrow_md { position: absolute; top:8px; right: 7px; width: 30px; height: 30px; ; }
        @media all and (max-width:$G-wMobile)  {
          button{  padding: 6px 40px 6px 0px;  font-size: 14px; line-height: 23px; 
            em   { font-weight: 600; }
            span { font-size: 13px; font-weight: 600;}
          }
          .form-select_arrow_md { position: absolute; top:2.5px; right: 7px; width: 30px; height: 30px; }
        }
      }

      &.h50-line_center {
        button { padding: 15px 50px 15px 50px;  font-size: 18px; font-weight: 500; color:$colorD; border: 1px solid #cccccc;
          line-height: 20px; width: 100%; text-align: center;
          background-color: #fff;
          em   { color:$colorP; font-weight: 600; }
          span { font-size: 18px; color:$colorB2; margin-left: 0px; font-weight: 600; }
          i { position: absolute; top:10px; right:10px }
          &.active i { transform: rotate(180deg);}
        }
        .trp-dropdown_list-box { top: 50px;
          .legend-box { padding: 10px; text-align: center;
            span { font-size: 15px; line-height: 1.5; }
          }
          .list-box { max-height: 100%;}
            a { display: block; 
              .typo-area { width: 100%; text-align: center; padding-left: 0;}
            }
            li { span{ font-size: 18px; color:$colorS; line-height: 1.5;} }
            li.active { span{ color:$colorE} }
            li.active ~ li { span{ color:$colorB2} }
        }
        @media all and (max-width:$G-wMobile)  {
          button{  padding: 15px 40px 15px 40px;  font-size: 14px; line-height: 20px; 
            em   { font-weight: 600; }
            span { font-size: 15px; font-weight: 600;}
            i {}
          }
          .trp-dropdown_list-box { top: 51px; width: 100%;
            .legend-box { padding: 10px; text-align: center;
              span { font-size: 15px; line-height: 1.5; }
            }
            .list-box { max-width: 100%;
              a { display: block; padding: 8px 15px;
                .typo-area { width: 100%; }
              }
              li { span{ font-size: 15px; } }
              li.active { span{ color:$colorE} }
              li.active ~ li { span{ color:$colorS} }
            }
          }
 
        }
      }

    
    }
  
  
    .select2Basic, 
    .select2Basic42, 
    .select2Basic_pop { width: 100%;  min-width: 100%; height: $formH; }
    .select2-container { text-align: left; font-size: 0; line-height: 0;
      width: 100% !important;
      .selection {  width: 100%; }
      .select2-selection--single { height: $formH;
        @media all and (max-width:$G-wMobile)  {
          height: 43px;
        }
      }
    }
    .select2-results__option { padding:0 15px;}
    .select2-container--default {
      .select2-selection--single {
        border: 1px solid $lineC_d; border-radius: 0px; 
        &:hover {border: 1px solid $lineC_f;}
        &:focus {border: 1px solid $lineC_f;}
        .select2-selection__rendered { line-height: $formH - 2px; padding: 0 48px 0 15px;  
          font-size: 18px;
          @media all and (max-width:$G-wMobile)  {            
            line-height: 43px - 2px; padding: 0 40px 0 15px;  
            font-size: 14px;
          }
        }
        .select2-selection__arrow { top:0; right:0px; height: $formH; width: 43px; 
          b { border: 0; left: 0; margin-left: 0; margin-top: -17px;
            @include sp_icons($icon-select_arrow);
          }
          @media all and (max-width:$G-wMobile)  { height: 43px;
            b {
              @include sp_icons2x($icon-select_arrow-2x);
            }
          }
        }
      }
      &.select2-container--open {
        .select2-selection--single .select2-selection__arrow b {
          /* border-color: #888 transparent transparent transparent; */
          /* border-width: 5px 4px 0 4px; */
          -webkit-transform: rotate(180deg);
          -moz-transform: rotate(180deg);
          -ms-transform: rotate(180deg);
          -o-transform: rotate(180deg);
          transform: rotate(180deg);
        }
      }
    } 

    /* type ===  */
    .select2Basic42  +  .select2-container{
      
      .select2-selection--single { height: 42px; 
        @media all and (max-width:$G-wMobile)  { height: 43px; }
        .select2-selection__rendered { line-height: 42px - 2px; padding: 0 42px 0 15px;  
          font-size: 16px;
          @media all and (max-width:$G-wMobile)  { 
            line-height: 43px - 2px; padding: 0 43px 0 15px;  
            font-size: 14px;
          }
        }
        .select2-selection__arrow { top:0; right:0px; height: 42px; width: 42px; 
          b { border: 0; left: auto; right: 8px; margin-left: 0; margin-top: -15px;
            @include sp_icons($form-select_arrow_md);
          }
          @media all and (max-width:$G-wMobile)  { height: 43px; width: 42px; 
            b {
              @include sp_icons2x($form-select_arrow_md-2x);
            }
          }
        }
      }
      &.none_line {

        
      }
    }
    
    .select2Basic50  +  .select2-container{
      
      .select2-selection--single { height: 48px; 
        @media all and (max-width:$G-wMobile)  { height: 43px; }
        .select2-selection__rendered { line-height: 48px - 2px; padding: 0 42px 0 15px;  
          font-size: 18px; color: #555;
          @media all and (max-width:$G-wMobile)  { 
            line-height: 43px - 2px; padding: 0 43px 0 15px;  
            font-size: 14px;
          }
        }
        .select2-selection__arrow { top:0; right:0px; height: 42px; width: 42px; 
          b { border: 0; left: auto; right: 8px; margin-left: 0; margin-top: -12px;
            @include sp_icons($form-select_arrow_md);
          }
          @media all and (max-width:$G-wMobile)  { height: 43px; width: 42px; 
            b {
              @include sp_icons2x($form-select_arrow_md-2x);
            }
          }
        }
      }
      &.none_line {

        
      }
    }
    

    .select2Basic56_line +  .select2-container{ 
 
      .select2-selection--single { height: 56px; 
        border: 3px solid #4DADA7; border-radius: 0px; 
        @media all and (max-width:$G-wMobile)  { height: 56px; }
        .select2-selection__rendered { line-height: 56px - 6px; padding: 0 50px 0 50px;  
          font-size: 18px; font-weight: 600; color:$colorB2; text-align: center;
          @media all and (max-width:$G-wMobile)  { 
            line-height: 54px - 6px; padding: 0 54px 0 15px;  
            font-size: 15px;
          }
        }
        .select2-selection__arrow { top:0; right:0px; height: 56px; width: 56px; 
          b { border: 0; left: auto; right: 8px; margin-left: 0; margin-top: -18px;
            @include sp_icons($icon-select_arrow);
          }
          @media all and (max-width:$G-wMobile)  { height: 54px; width: 54px; 
            b {
              @include sp_icons2x($icon-select_arrow-2x);
            }
          }
        }
      }
      &.select2-container--disabled {
        .select2-selection--single { background-color: #e7e7e7;
          .select2-selection__rendered { opacity: .4; }
          .select2-selection__arrow    { opacity: .4; } 
        }
      }
    }


  } 

  
  /* ========================  input file */
  .trp.file-box { position: relative; 
    label.screen-reader-text { position:absolute; margin:-1px; padding:0; height:1px; width:1px; overflow:hidden; clip:rect(0,0,0,0); border:0; }
    .trp-Filetext { width: calc( 100% - 120px); height: $formH; padding: 8px 10px 8px 10px;
      font-size: 16px; vertical-align: middle;   border: 1px solid $lineC_d; outline: none; 
      -webkit-appearance: none;
      -moz-appearance: none;
    }
    .btn { position: absolute; top:0px; right:0px; min-width: 110px;  height: $formH; /* background:#757575; color:#fff; font-size:16px; font-weight: 300; */
      -webkit-appearance: none;
      -moz-appearance: none;
    }
    .trp-Filehidden { position: absolute; top:0; left:0; width:100%;  height: $formH;  opacity:0; cursor:pointer; z-index: 1;}

    @media all and (max-width:$G-wMobile)  { 
      .trp-Filetext {  font-size: 14px; padding: 5px 15px 5px; line-height: 22px; height: 43px;} 
      .btn {  font-size: 14px; height: 43px; }
      .trp-Filehidden { height: 43px;}
    }

  }
  
  
  /* ========================  jQuery datepicker */
  .datepicker-box { position: relative;  display: inline-block;  min-width: 155px; height: $formH;
    background-color: #f2f2f2; 
    input { width: 100%;  padding-right: 35px; z-index: 1; background-color: transparent; }
    i { &::after { content: ""; display: block; width: 23px; height: 23px;
        position: absolute; top: 8px; right: 8px; 
        @include sp_icons($form-calender) }
    }
    &.type_label  { display: block; font-size: 0; 
      label { position: relative; display: inline-block;  padding: 10px 13px 10px 15px;
        font-size: 15px; color:$colorB; font-weight: bold;  width: 88px; box-sizing: border-box;
        font-family: $baseFT;  line-height: 30px;
        &::after { content: ''; display: block;  width: 1px; height: 15px; background-color: #d2d2d2;
          position: absolute; top: 18px; right: 0px;
        }
      }
      input { width: calc( 100% - 88px ); vertical-align: top; padding-right: 70px; 
        text-align: center;
      }
    }
  
  }

  
  
  /* 커스텀 카운터 인풋 박스 */
  .ui-count {position: relative;display:inline-block; border-radius: 0;
    .ui-countTxt {width:60px; padding-right:30px; padding-left:10px;  text-align: right; background: #fff; }
      &:before{content:''; position: absolute; right:1px; top:1px; }
      >a { position: relative;  width:($formH / 2); height:($formH / 2) ; display:block; position: absolute;
        &::after { content: ""; position: absolute; right: 5px; }
      }
      .arrow_up { top:1px; right:0; border-left: 1px solid $lineC_d;
        &::after {  top:7px;  
          border-style: solid; border-width: 0px 4px 5px 4px; height: 0;
          border-color: transparent transparent $colorD transparent;
        }
      }
      .arrow_down { bottom:0px; right:0; border-top: 1px solid $lineC_d; border-left: 1px solid $lineC_d;
        &::after { top:8px;
          border-style: solid; border-width: 5px 4px 0px 4px; height: 0;
          border-color: $colorD transparent transparent transparent;
        }
      }
  }
  
  /* ========================  timepicker */
  .ui-ampm { position: relative;display:inline-block; width: 50px; min-width:50px;  height: $formH;
    padding: 8px 10px 8px;  border: 1px solid #bfbfbf; 
    text-align: center; background: #fff;
  }
  .timepicker-area { position: relative; display: inline-block; 
    .trp-timepicker{ display: none; position: absolute; top: $formH; 
        width: 170px;
        z-index: 10;
    }
  }
  .timepicker-box { position: relative;  display: inline-block;  
    min-width: 150px; width: 160px; height: $formH;
    background-color: #fff; 
    input { width: 100%;  padding-right: 35px; z-index: 1; background-color: transparent; 
      &:read-only { background-color: transparent;  }
    }
    input.br { @include border-radius(0) }
    i { &::after { content: ""; display: block; position: absolute; top: 8px; right: 8px; 
        @include sp_icons($form-time);
      }
    }
  }
  
  /* ========================  starpoint */
  .starpoint-box{
    .star { display:inline-block; width:20px; height:20px;
      &:after { content:""; display:block; @include sp_icons($form-star-off);  }
    }
    .star.on { 
      &:after {  @include sp_icons($form-star-on);  }
    }
  }
  .starpoint2-box{
    .star { display:inline-block; width:10px; height:20px; overflow: hidden;
      &:after { content:""; display:block; @include sp_icons($form-star-off);  }
      &:nth-child(even) { 
        &:after { margin-left: -10px; }
      }
    }
    
    .star.on { 
      &:after {  @include sp_icons($form-star-on);  }
    }
  }


} // end


// 선택 오버, 활성화 
.select2-container--default { 
  .select2-dropdown { border-radius: 0; background-color: #fff; border-color:$lineC_d;
    .select2-results { padding: 10px 0;}
  }
  .select2-results__option { 
    line-height: 35px;  padding: 0 20px;
    font-size: 16px; font-weight: 500; color:$colorD;
    &[aria-selected=true] { font-weight: 600;
      color:$colorB2; background-color: #ededed/*$colorMain*/; 

    }
    &.select2-results__option--highlighted[aria-selected] { 
      font-weight: 600;
      color: $colorB2; background-color: #ededed; 
    }

    @media all and (max-width:$G-wMobile)  { 
      font-size: 13px;
    }
  }
    
}


// pop 뎁스 조절 
.select2-dropdown.increasedzindexclass_pop { z-index: 99000; }



/* ========================  달럭팝업 */
.ui-datepicker.ui-widget  { 
  border: 1px solid #f3f3f3;  padding: 0; z-index:10 !important;  @include shadow(0px, 0px, 25px, 0.05); 
  background-color: #fff; height: auto;
}
.ui-corner-all, .ui-corner-bottom, .ui-corner-br, .ui-corner-right {  border-radius: 0;  }

// 달력 타이틀
.ui-datepicker .ui-datepicker-header {padding: 0; height: 40px; background-color: #ffffff;}
.ui-datepicker-title { height: 40px; line-height: 38px; 
  .ui-datepicker-month { line-height: 38px; color:$colorP;  }
  .ui-datepicker-year  { line-height: 38px; color:$colorP;  }
}
.ui-datepicker .ui-datepicker-header { border: 0; border-bottom: 1px solid #f3f3f3; 
  .ui-datepicker-prev  { top:7px; 
    .ui-icon { position: relative; width: 100%; height:100%; margin: 0; padding: 0;
      top:0; left:0;  background-image: none;
      &::before { content: ""; display: block; width: 10px; height:1px;
          position: absolute; top:9px; left:7px; 
          background-color: #999999; @include rotate(-45deg);
      }
      &:after {content: ""; display: block; width: 10px; height:1px;
          position: absolute; top:16px; left:7px; 
          background-color: #999999; @include rotate(45deg);
      }
    }
  }
  .ui-datepicker-next  { top:7px; 
    .ui-icon { position: relative; width: 100%; height:100%; margin: 0; padding: 0;
      top:0; left:0;  background-image: none;
      &::before { content: ""; display: block; width: 10px; height:1px;
        position: absolute; top:9px; left:8px; 
        background-color: #999999; @include rotate(45deg);
      }
      &:after {content: ""; display: block; width: 10px; height:1px;
        position: absolute; top:16px; left:8px; 
        background-color: #999999; @include rotate(-45deg);
      }
    }
  }
}
// 일 달력
.ui-datepicker-calendar { 
  tbody {
    tr {
      td {
        .ui-state-default { width: 27px; height: 27px;
          box-sizing: border-box; padding: .15em .2em .05em; line-height: 1.7;
          text-align: center; border: 0; background-color: #fff; color:#777777; 
        }
      }
      .ui-datepicker-today  .ui-state-default { 
        color: #fff; background-color:$colorP; border-radius: 50%; }
      .ui-datepicker-week-end:first-child  .ui-state-default { color:$colorP;  }
      .ui-datepicker-week-end.ui-datepicker-today   .ui-state-default { color:#ffffff; }
    }
  }
}
/* 월 달력 */
.datepicker-box {
}
.monthPicker {
  .year{ height: 40px; background-color: #fff; border-bottom: 1px solid #f3f3f3;
    strong { line-height: 28px; color: $colorP; font-size: 15px; font-weight: bold;}
    &>span .changeYear {}
    &>span:first-child { top: 6px; left:  2px;  }
    &>span:last-child  { top: 6px; right: 2px; }
    .prev-btn { width: 27px; height: 27px; border:0;
      a { border: 0;
        &::before { content: ""; display: block; width: 10px; height: 1px;
          position: absolute; top: 9px; left: 7px;
          background-color: #999;
          -moz-transform: rotate(-45deg);
          -o-transform: rotate(-45deg);
          -ms-transform: rotate(-45deg);
          -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
        }
        &::after  { content: ""; display: block; width: 10px; height: 1px;
          position: absolute; top: 16px; left: 7px;
          background-color: #999;
          -moz-transform: rotate(45deg);
          -o-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
        }
      }
      a:hover { background-color: #ededed; border: 1px solid #ccc;}
    }
    .next-btn { width: 27px; height: 27px; border:0;
      a { border: 0;
        &::before { content: ""; display: block; width: 10px; height: 1px; 
          position: absolute; top:   9px; left: 8px;
          background-color: #999;
          -moz-transform: rotate(45deg);
          -o-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
        }
        &::after  { content: ""; display: block; width: 10px; height: 1px;
          position: absolute; top: 16px; left: 8px;
          background-color: #999;
          -moz-transform: rotate(-45deg);
          -o-transform: rotate(-45deg);
          -ms-transform: rotate(-45deg);
          -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
        }
      }
      a:hover { background-color: #ededed; border: 1px solid #ccc;}
    }
  }
}






/* ========================  jQuery tooltip */
/* icon_tooltip */
.icon_tooltip { display: inline-block; width: 21px; height: 21px;
  &:after { content: ""; display: block; @include sp_icons($form-tooltip); }   
}

.ui-tooltip{ 
  &.ui-widget-shadow { box-shadow: 0 2px 1px 0 rgba(0,0,0,0.16)}
  &.ui-widget.ui-widget-content {         
    padding: 15px; 
    border: 1px solid #aaaaaa; border-radius: 0;
    .ui-tooltip-content { font-size: 14px; color:$colorB;
      @include  wordBreak-all();
    }
  } 
  font-size: 14px; color:$colorB;
  .arrow { position: absolute;left: 50%; bottom: -15px;
    overflow: hidden;  box-shadow: 0;     
    @include sp_icons($form-dot_tooltip);  
  }
  .arrow.top  {top: -15px; bottom: auto;
    @include sp_icons($form-dot_tooltip-top);  
  }
  //.arrow.left  { left: 20px; right: auto; }
  //.arrow.right { left: auto; right: 20px; }
  .arrow.top:after {bottom: -15px;top: auto;}
}
// 커스텀 툴팁 
.ui-tooltip.tooltip_big{  max-width: 500px;
  .h5 { margin-bottom: 15px; }
  .numbar-list li { font-size: 14; letter-spacing: -1px}
  table { 
    th, td { padding: 5px 10px; font-size: 12px; vertical-align: top; 
      font-size: 14px; text-align: left; line-height: 1.4;
      i { margin-right: 5px;}
      .text_state { font-size: 14px; }
    }
  }
}
