/* ================== 팝업 레이아웃 */  
/** //popup-wrap **/
.popup-dim { position: fixed;top: 0;left: 0;right: 0;bottom: 0; 
  width: 100%;height: calc(var(--vh, 1vh) * 100);background: #000000;opacity: 0.6;
  z-index: 13;
}
.trp.popupfixed-wrap { display: none;  position: absolute; top: 0; left: 0;
// width: 100%;  height: 100%; min-height: 100%;
right: 0; bottom: 0;
z-index: 14; 
-webkit-overflow-scrolling: touch; 
  .popup-dim {  background-color: transparent; z-index: 0; }
  .popup-align    { display: table;*display: block; table-layout: fixed;  width: 100%; height: calc(var(--vh, 1vh) * 100); text-align: center;
    .popup-vertical {
      display: table-cell;width: 100%;padding: 0px 0px;
      text-align: center;vertical-align: middle;
    }

    /* full > full */
    &.mode-full { position: relative; display: block; table-layout: initial;
      .popup-vertical { display: block;  padding:0; height: 100%; } 
      .popup-layer { width: 100%; height: 100%;
        .pop-panel { height: calc(var(--vh, 1vh) * 100);
          .pop-header { height: 60px; }
          .pop-body { height: calc( 100% - 60px - 40px); overflow-y: auto; }
          .pop-footer { height: 40px; }
        }
      }
    }
    /* 1440px > 360 */
    &.mode-big { 
      .popup-layer { width: 100%; max-width: 1440px;}
      .pop-panel {
        .pop-body.scroll-type { width: 100%; 
          max-height: calc(100vh - 96px); 
          max-height: calc(var(--vh, 1vh) * 100 - 96px);
        }
        .section { position: relative; padding: 24px 40px 50px; }
      }
      @media all and (max-width:$G-wMobile)  {
        .popup-layer { position: relative; 
          width:100%; height: calc(var(--vh, 1vh) * 100);
        }
        .pop-panel { position: absolute; top: 0; left: 0; width: 100%; height: 100%;
          .pop-header { .title-box {  padding: 25px 25px 10px;} }
          .pop-body.scroll-type {            
            max-height: calc(100vh - 70px); 
            max-height: calc(var(--vh, 1vh) * 100 - 70px);
          }
          .section { position: relative; width: 100%; padding: 27px 25px 50px; }
        }
        &.mode-mb_full { 
          .pop-panel { min-height: calc(var(--vh, 1vh) * 100);} 
        }
        &.mode-mb_fixed {
          .popup-layer { overflow: hidden; }
          .pop-panel { position: absolute; 
            bottom: 0; left: 0; width: 100%; 
            .pop-body.scroll-type { width: 100%; 
              max-height: calc(100vh - 140px); 
              max-height: calc(var(--vh, 1vh) * 100 - 140px);
            }
          }
        }
      }
    }
    /* 1000 > 360 높이고정 해더스타일다름*/
    &.mode-xxl { 
      .popup-layer { position: relative; width: 1000px;}
      .pop-panel {
        .pop-header { 
          .title-box {  padding: 44px 45px 10px; }
        }
        .pop-body.scroll-type { width: 100%; 
          max-height: calc(100vh - 96px); 
          max-height: calc(var(--vh, 1vh) * 100 - 96px);
        }
        .section { position: relative; padding: 20px 45px 50px; }
      }
      @media all and (max-width:$G-wMobile)  {
        .popup-layer { position: relative; 
          width:100%; height: calc(var(--vh, 1vh) * 100);
        }
        .pop-panel { 
          .pop-header { .title-box {  padding: 25px 25px 10px;} }
          .pop-body.scroll-type {            
            max-height: calc(100vh - 70px); 
            max-height: calc(var(--vh, 1vh) * 100 - 70px);
          }
          .section { position: relative; width: 100%; padding: 25px 25px 40px; }
        }
        &.mode-mb_full { 
          .pop-panel { min-height: calc(var(--vh, 1vh) * 100);} 
        }
        &.mode-mb_fixed {
          .popup-layer { overflow: hidden; }
          .pop-panel { position: absolute; 
            bottom: 0; left: 0; width: 100%; 
            .pop-body.scroll-type { width: 100%; 
              max-height: calc(100vh - 140px); 
              max-height: calc(var(--vh, 1vh) * 100 - 140px);
            }
          }
        }
      }
    }
    /* 960 > 360 */
    &.mode-xl { 
      .popup-layer { position: relative; width: 960px;}
      .pop-panel {
        .pop-header { 
          .title-box {  padding: 44px 50px 10px; }
        }
        .pop-body.scroll-type { width: 100%; 
          max-height: calc(100vh - 96px); 
          max-height: calc(var(--vh, 1vh) * 100 - 96px);
        }
        .section { position: relative; padding: 20px 40px 50px; }
      }
      @media all and (max-width:$G-wMobile)  {
        .popup-layer { position: relative; 
          width:100%; height: calc(var(--vh, 1vh) * 100);
        }
        .pop-panel { 
          .pop-header { .title-box {  padding: 25px 25px 10px;} }
          .pop-body.scroll-type {            
            max-height: calc(100vh - 70px); 
            max-height: calc(var(--vh, 1vh) * 100 - 70px);
          }
          .section { position: relative; width: 100%; padding: 25px 25px 40px; }
        }
        &.mode-mb_full { 
          .pop-panel { min-height: calc(var(--vh, 1vh) * 100);} 
        }
        &.mode-mb_fixed {
          .popup-layer { overflow: hidden; }
          .pop-panel { position: absolute; 
            bottom: 0; left: 0; width: 100%; 
            .pop-body.scroll-type { width: 100%; 
              max-height: calc(100vh - 140px); 
              max-height: calc(var(--vh, 1vh) * 100 - 140px);
            }
          }
        }
      }
    }
    /* 910 > 360 */
    &.mode-lg { 
      .popup-layer { position: relative; width: 910px;}
      .pop-panel {
        .pop-header { 
          .title-box {  padding: 44px 50px 10px; }
        }
        .pop-body.scroll-type {
          width: 100%; 
          max-height: calc(100vh - 96px); 
          max-height: calc(var(--vh, 1vh) * 100 - 96px);
        }
        .section { position: relative; padding: 20px 50px 60px; }
      }
      @media all and (max-width:$G-wMobile)  {
        .popup-layer {/*  width: 360px; */ 
          position: relative; top:0; 
          width:100%; height: calc(var(--vh, 1vh) * 100);
        }
        .pop-panel { position: absolute; top: 0; left: 0; 
          width: 100%; height: calc(var(--vh, 1vh) * 100);
          .pop-header {  /* position: absolute; top:0; width: 100%; */
            .title-box {  padding: 25px 25px 10px;}
          }
          .pop-body.scroll-type {
            width: 100%; 
            max-height: calc(100vh - 70px); 
            max-height: calc(var(--vh, 1vh) * 100 - 70px);
          }
          .section { position: relative; width: 100%; padding: 25px 25px 40px; }
        }

        &.mode-mb_full { .pop-panel { min-height: calc(var(--vh, 1vh) * 100);} }
        &.mode-mb_fixed {
          .popup-layer { overflow: hidden; }
          .pop-panel { position: absolute; 
            bottom: 0; left: 0; width: 100%; 
            .pop-body.scroll-type { width: 100%; 
              max-height: calc(100vh - 140px); 
              max-height: calc(var(--vh, 1vh) * 100 - 140px);
            }
          }
        }

      }

    }
    /* 720 > 360 */
    &.mode-md { 
      .popup-layer {width: 100%; max-width: 720px;}
      .pop-panel {
        .pop-body.scroll-type { width: 100%; 
          max-height: calc(100vh - 96px); 
          max-height: calc(var(--vh, 1vh) * 100 - 96px);
        }
        .section { position: relative; padding: 25px 40px 40px; }
      }
      @media all and (max-width:$G-wMobile)  {
        .popup-layer { position: relative; 
          width:100%; height: calc(var(--vh, 1vh) * 100);
        }
        .pop-panel { 
          .pop-header { .title-box {  padding: 25px 25px 10px;} }
          .pop-body.scroll-type {            
            max-height: calc(100vh - 70px); 
            max-height: calc(var(--vh, 1vh) * 100 - 70px);
          }
          .section { position: relative; width: 100%; padding: 25px 25px 40px; }
        }
        /* 모바일 풀 */
        &.mode-mb_full { .pop-panel { min-height: calc(var(--vh, 1vh) * 100);} }
        /* 모바일 하단 */
        &.mode-mb_fixed {
          .popup-layer { overflow: hidden; }
          .pop-panel { position: absolute; 
            bottom: 0; left: 0; width: 100%; 
            height: calc(var(--vh, 1vh) * 100 - 70px);
            .pop-body.scroll-type { width: 100%; 
              max-height: calc(100vh - 140px); 
              max-height: calc(var(--vh, 1vh) * 100 - 140px);
            }
          }
        }
     
      }
    }
    /* 620 > 360 */
    &.mode-ms { 
      .popup-layer {width: 100%; max-width: 620px;}
      .pop-panel {
        .pop-body.scroll-type { width: 100%; 
          max-height: calc(100vh - 96px); 
          max-height: calc(var(--vh, 1vh) * 100 - 96px);
        }
        .section { position: relative; padding: 25px 40px 40px; }
      }
      @media all and (max-width:$G-wMobile)  {
        .popup-layer { position: relative; 
          width:100%; height: calc(var(--vh, 1vh) * 100);
        }
        .pop-panel { 
          .pop-header { .title-box {  padding: 25px 25px 10px;} }
          .pop-body.scroll-type {            
            max-height: calc(100vh - 70px); 
            max-height: calc(var(--vh, 1vh) * 100 - 70px);
          }
          .section { position: relative; width: 100%; padding: 25px 25px 40px; }
        }
        /* 모바일 중앙 */
        &.mode-mb_center { 
          .popup-layer { padding: 0 25px; height: auto;
            .pop-panel { position: relative; }
          } 
        }
        /* 모바일 풀 */
        &.mode-mb_full { .pop-panel { min-height: calc(var(--vh, 1vh) * 100);} }
        /* 모바일 하단 */
        &.mode-mb_fixed {
          .popup-layer { overflow: hidden; }
          .pop-panel { position: absolute; 
            bottom: 0; left: 0; width: 100%; 
            .pop-body.scroll-type { width: 100%; 
              max-height: calc(100vh - 140px); 
              max-height: calc(var(--vh, 1vh) * 100 - 140px);
            }
          }
        }
      }
    }
    
    /* js 옵셔으로 동작 pc 하단 붙이기 */
    &.footer_fixed { padding-top: 70px;
      .pop-panel {
        .pop-body.scroll-type { 
          width: 100%; 
          max-height: calc(100vh - (96px + 70px)); 
          max-height: calc(var(--vh, 1vh) * 100 - (96px + 70px));
        }
      }
      @media all and (max-width:$G-wMobile)  { padding-top: 0px;
        .pop-panel {  
          max-height: calc(100vh -  (70px)); 
          max-height: calc(var(--vh, 1vh) * 100 -  (70px));
          .pop-body.scroll-type { 
            width: 100%; 
            max-height: calc(100vh -  (70px + 70px)); 
            max-height: calc(var(--vh, 1vh) * 100 -  (70px + 70px));
          }
        }
      }
    }

  }


  .popup-layer {  position: relative; z-index: 1; display: inline-block; *display: inline;zoom: 1;
  }
  .pop-panel {   background: #fff;
    .pop-header { border-top: 6px solid $colorP; ;
      .title-box {  padding: 44px 40px 10px; height: 90px; box-sizing: border-box;
        @include before-middle(); text-align: left; font-size: 0; line-height: 0;
        .txt_title {vertical-align: middle;
          font-size: 28px; font-weight: 600; color: $colorB; line-height: 1.1;
          font-family: $baseFT; 
          &.type-big { font-size: 34px;}
        }
      }
      .btn_close { position: absolute;top: 0px;right: 0px; z-index: 10;
        width: 84px; height: 84px; padding: 30px; margin: 0; 
        font-size: 0px; line-height: 1; color: transparent; box-sizing: border-box;
        &:after { content: ''; display: block; }
      }
      @media all and (max-width:$G-wMobile)  { border-top: 3px solid $colorP; ;
        .title-box {  padding: 25px 20px 12px; height: 67px; 
          .txt_title { font-size: 22px; font-weight: 700; 
            &.type-big { font-size: 22px;}
          }
        }
        .btn_close { top: 10px; width: 57px; height: 57px; padding: 20px; margin: 0; }
      }
     
    }
    .pop-body { text-align: left;
      .section { position: relative; padding: 25px 40px 40px; }
      .articles-typo {margin-top: 20px;}
      .articles-box  {margin-top: 25px;}
      .articles-box + .articles-typo  {margin-top: 14px;}
      .group-box + .group-box { margin-top: 45px;}
      .group-box {
        .box-title { color:$colorB2; }
        .box-title + p { margin-top: 20px; }
        .mark_dot-list { margin-top: 20px; 
          li { padding-left: .8em;}
        }
        @media all and (max-width:$G-wMobile)  { 
          .box-title { }
          .box-title + p { margin-top: 10px; }
          .mark_dot-list { margin-top: 10px; 
            li { padding-left: .6em;}
          }
        }
      }
      .search-group + .articles-box { margin-top: 40px;}
      .articles-typo:first-child {margin-top: 0px;}
      .articles-box:first-child  {margin-top: 0px;}
      .text-base { font-size: 18px; color:#555; font-weight: 500; line-height: 28px;}
      .text-body { font-size: 18px; color:#555; font-weight: 500; line-height: 28px;}
      .text-help { font-size: 16px; color:#555; font-weight: 500; line-height: 28px;}
      .btn_bottom { margin-top: 20px;}
      @media all and (max-width:$G-wMobile)  { 
        .section { position: relative; padding: 15px 20px 35px; }
        .articles-typo {margin-top: 15px;}
        .articles-box  {margin-top: 15px;}
        .articles-box + .articles-typo  {margin-top: 10px;}
        .group-box + .group-box { margin-top: 30px;}
        .search-group + .articles-box { margin-top: 30px;}
        .articles-typo:first-child {margin-top: 0px;}
        .articles-box:first-child  {margin-top: 0px;}
        .text-base { font-size: 15px; color:#555; font-weight: 500; line-height: 22px;}
        .text-body { font-size: 14px; color:#555; font-weight: 500; line-height: 1.5;}
        .text-help { font-size: 14px; color:#555; font-weight: 500; line-height: 21px;}
      }
    }
    .pop-footer {
      .btn_bottom { margin-top: 0px;}
    }
  }



  /* 오늘하루 팝법 띄우기 */
  &.common-popup {
    .popup-vertical { .popup-layer {width: 450px; background-color: transparent;} }
    .pop-panel { position: relative;
      .pop-body { 
        .section { padding: 0;}
        .img-box { background-color: #fff; 
          img { width: 100%;}
        }
      } 
      .pop-fooer { display: flex; justify-content: space-between; 
        padding: 20px 0;
        .trp.checkbox-box { 
          input[type="checkbox"] + i:after { display: block; content: '';  @include sp_icons($form-checkbox_w-off); }
          input[type="checkbox"]:checked + i:after {  @include sp_icons($form-checkbox-on); }
          label { font-size: 18px; font-weight: 500; line-height: 24px; color:#fff;}
        }
        .btn-box {
          font-size: 0; line-height: 0;
          .btn_close { margin-top: 1px;}
        }
      } 
    }
    @media all and (max-width:$G-wMobile)  {
      .popup-vertical { vertical-align: bottom;
        .popup-layer {width: 100%; background-color: transparent;} 
      }
      .pop-panel {
        .pop-body { 
          .section { padding: 0; }
          .img-box { border-top-left-radius: 25px; border-top-right-radius: 25px; overflow: hidden; }; 
        } 
        .pop-fooer { position: absolute;top: -47px; width: 100%; 
          padding: 15px 25px;
          .trp.checkbox-box { 
            input[type="checkbox"] + i:after { display: block; content: '';  @include sp_icons2x($form-checkbox_w-off-2x); }
            input[type="checkbox"]:checked + i:after {  @include sp_icons2x($form-checkbox-on-2x); }
            label { font-size: 15px; font-weight: 600; line-height: 17px; color:#fff;}
          }
          .btn-box {
            font-size: 0; line-height: 0;
            .btn_close { margin-top: 1px;}
          }
        } 
      }
    }
  }

  /* seoul auction 안내 , 응찰중지, 응찰하기 */
  &.auction_info-popup {
    .popup-align.mode-ms .pop-panel .section { padding-bottom: 60px;}
    @media all and (max-width:$G-wMobile)  { 
      .popup-align.mode-ms .pop-panel .section { padding-bottom: 30px;}
    }
    .auction_info-article { margin-top: 40px;
      text-align: center;
      .img   { img{width: 72px;} }
      .title { margin-top: 35px;
        span{ font-size: 36px; font-weight: 600; color:$colorB2; } 
      }
      .desc  { margin-top: 15px;
        span{ font-size: 20px; font-weight: 600; color:$colorB2; } 
      }
      .query  { margin-top: 15px;
        span{ font-size: 18px; font-weight: 500; color:$colorB2; } 
      }

      .gray-box      { padding: 25px 30px; margin-top: 30px;
        background-color: #f6f6f6;
      }
      .check-box   { margin-top: 20px;
        label { color:$colorB2 }
      }
      .mark_dot-list {
        li {font-size: 16px; line-height: 1.78; padding-left: 15px;
          color:$colorD; text-align: left;
          &::after { background-color:$colorL;}
        }
      }

      .btn_set-float {  margin-top: 35px;
        .btn { width: 170px; }
      }
      .btn_set {margin-top: 35px;  justify-content: center;
        .btn { width: 210px; margin: 0 5px;}
      }
      @media all and (max-width:$G-wMobile)  { margin-top: 20px;
        .img   { img{width: 50px;} }
        .title { margin-top: 15px; span{ font-size: 23px; } }
        .desc  { margin-top: 5px; span{ font-size: 15px; } }
        .query  { margin-top: 10px;
          span{ font-size: 14px; } 
        }
        .gray-box      { padding: 18px 15px; margin-top: 20px; }
        .mark_dot-list {
          li {font-size: 13px; line-height: 1.5; padding-left: 7px; 
            &::after { top:8px;}
          }
        }

        .btn_set-float {  margin-top: 20px;
          .btn { width: 100%; }
        }
        .btn_set { margin-top: 20px; justify-content: center;
          .btn { width: calc(50% - 5px); margin: 0 5px;}
        }
      }
    }
  }


  /* 패들번호 부여 */
  &.paddle_number-popup{
