            em   { font-size: 15px;  }
          }
          .gray-box      { padding: 18px 15px; margin-top: 20px; }
          .mark_dot-list {
            li {font-size: 13px; line-height: 1.5; padding-left: 7px; 
              &::after { top:8px;}
            }
          }
        }
        .footer-article { margin-top: 20px; 
          .btn_set-float { .btn { width: 100%; } }
        }
      }
    }
