              font-size: 16px; color:#777777; line-height: 1.5;  
            }
            li + li { margin-left: 10px; padding-left: 10px;
              @include list-div_bar(6px, 0px, 12px, #bbbbbb );
            }
          }
        }
      
  
      }
    }

    .legend-area { margin-top: -20px; display: flex; justify-content: space-between;
      .side_time {  
        span { display: inline-block; padding: 0 18px;
          font-size: 16px; font-weight: 600; color:#555555; line-height: 36px; min-width: 162px; text-align: center;
          background-color: #f6f6f6; 
        }
