          .title_sub{font-size: 20px; color: $colorB2; margin-top: 10px; line-height: 1.7;}
        }
        &.title_md{padding: 25px 0 0px;}
        &.title_concurrent{font-size: 20px; padding: 35px 100px 0; line-height: 1.5;}
        .title_tac{font-weight: 700;color: $colorB2;
        strong{color: $colorP;}
        }
      }
    }
    .pop-body{padding: 0 40px;
      .sns_box{margin-bottom: 30px;
        &.sns_box_pw{margin-bottom: 0;}
        p{font-size: 20px;font-weight: 700; color: $colorB2; border-top: 1px solid #ccc; border-bottom: 1px solid #ccc;
          padding: 25px 0; text-align: center;
          span{color: $colorP;}
        }
      }
      .info_box{
        .change_txt{color: $colorD; line-height: 1.6;}
        ul{background-color: #f6f6f6; padding: 28px; color: $colorS;
          li{padding-left: 12px;
            &:after { 
              background-color: #888888;}
          }
        }
        .info_txt{ padding-left: 13px;
          span{ text-decoration: underline;}
        }
      }
      .confirm_btn{padding: 30px 0 40px;
        &.changenew_btn{padding: 0 0 60px;}
        &.change_btn{padding: 40px 0 40px;}
          .btn_point{margin-left: 5px;}
        &.confirm_btn_md{padding: 35px 0 60px;}
        .btn{width: 210px;}
      }
    }
  }
  @media all and (max-width:$G-wMobile)  { 
    &.login-popup{
      .pop-header{
        .ico_box{margin-top: 40px;
          &.change_ico{margin-top: 105px;
            img{width: 113px;}
          }
          img{width:47.5px;}
        }
        .btn_close{top: -5px; right: -5px;}
        .title-box_tac{padding: 40px 0 20px;
          &.change_title{font-size: 22px; padding: 20px 0 25px;
            .title_sub{font-size: 15px; margin-top: 7px; line-height: 1.5;}
          }
          &.title_concurrent{font-size: 15px; padding: 20px 50px 0; line-height: 1.4;}
          &.title_md{padding: 14 px 0 0px;}
        }
      }
      .pop-body{padding: 0 25px;
        .sns_box{margin-bottom: 20px;
          p{font-size: 15px; padding: 20px 0; }
          }
        .info_box{
          ul{padding: 15px;
            &.change_txt{padding: 20px;}
            li{padding-left: 8px;
              &:after { 
                top: 8px;}
            }
          }
          .info_txt{ padding-left: 8px;
          }
        }
        .confirm_btn{padding: 20px 0 30px;
          &.changenew_btn{padding: 0 0 60px;}

            .btn_set-float{display: flex; justify-content: center;}
            .btn_point{margin-left: 5px;}
      
          &.confirm_btn_md{padding: 15px 0 30px;}
          .btn{width: 100%;}
        }
      }
    }
  }

  /* (공통) 팝업 닫기 버튼 */
  .pop-body {
    .buttons-area {
      padding: 40px 0 10px;
      .btn.js-closepop { width: 170px; }
      .btn.btn_point { width: 170px; }
    }
    @media all and (max-width:$G-wMobile) {
      .buttons-area {
        padding: 30px 0 0;
        .btn.js-closepop { width: 100%; }
        .btn.btn_point { width: 100%; }
      }
    }
  }

  /* (공통) 팝업 하단 버튼 */
  .pop-body .button-area { margin-top: 40px;
    .btn { width: 170px; }
  }
  .mode-md .pop-body .button-area { padding-bottom: 10px;}
  @media all and (max-width : $G-wMobile) {
    .pop-body .button-area { margin-top: 30px;
      .btn { width: 160px; }
    }
    .mode-md .pop-body .button-area { padding-bottom: 0;}
  }

  /* (공통) 데이터없음 */
  .data-empty {
    border-top: 1px solid $colorL;;
    border-bottom: 1px solid #cccccc;
    padding: 90px 0;
    
  }
  @media all and (max-width : $G-wMobile) {
    .data-empty {padding: 84px 0;}
  }


  /* 정회원 이력 툴팁 */
  &.tooltip-popup {
    .popup-align.mode-md {
      .popup-layer { width: 620px; }
      .pop-panel .section {
        padding-bottom: 50px;
      }
    }
    .con-wrap-line { border-top: 1px solid $colorL; border-bottom: 1px solid #ccc; padding: 25px 0;}
    .pop-body {
      .group-box {
        &.buyinfo-box{ margin-top: 25px;}
        .mark_dot-list { margin-top: 0; padding-left: 0.3rem;
          li:after { top: 16px; transform: translateY(-50%);}
        }
      }
      .group-box + .group-box { margin-top: 25px;}  
       /* 결제완료 추가 */
      .buyinfo_txt{ margin-top: 25px;}
    }
    @media all and (max-width:$G-wMobile) {
      .popup-align.mode-md {
        .popup-layer { width: 100%; }
        .pop-panel { height: auto;}
      }
      .pop-body {
        .group-box { .mark_dot-list { li:after { top: 10px; } } } 
      }
      /* [0608]2줄에 맞춰 calc->flex 변경 */
        .pop-panel { display: flex; flex-direction: column;
          .pop-header .title-box { height: unset;}
          .pop-body.scroll-type { max-height: unset;}
        }
    }
  }

  /* 정회원 결제 이용약관 */
  &.membership_upgrade-popup {
    .popup-align.mode-md .popup-layer { width: 620px; }
    .pop-body {
      .group-box { border-top: 1px solid $colorL; }
    }
    .accordion-list{
      border-top: 0;
      > li > .con-area {
        display: block;
        padding: 0;
        .trp.checkbox-box label { font-size: 16px; }
        .policy-list { padding: 20px 30px; }
        .policy-list + .policy-list { border-top: 1px solid #d4d4d4; }
      }
    }
    @media all and (max-width:$G-wMobile) {
      .popup-align.mode-md .popup-layer { width: 100%; }
      .accordion-list > li > .con-area .policy-list {
        padding: 15px;
      }
    }
  }

  /* 정회원 결제 이력 */
  &.memlv2_record-popup {
    .popup-align.mode-md .popup-layer { width: 720px; }
    .memlv2_record-list {
      border: 1px solid #ccc;
      dt, dd { padding: 16px 30px; }
      dt {
        display: flex; justify-content: space-between; line-height: 20px;
        .tb1 { line-height: inherit; font-weight: 600; }
      }
      dd {
        border-top: 1px solid #ccc;
        display: flex; justify-content: space-between;
        .info-li { display: flex;}
        .tit { font-weight: 600; padding-right: 30px;}
      }
      .state-icon {
        border: 1px solid;
        height: 20px; line-height: 18px; padding: 0 16px;
        font-size: 14px; font-weight: 700;
      }
      &.memlv2 {
        .state-icon { border-color: $colorP; color: $colorP; }
      }
      &.expiry {
        background-color: $colorBGw; color: $colorS;
        .txt { color: $colorL; }
        .state-icon { border-color: #bdbdbd; color: $colorS; }
      }
    }
    .memlv2_record-list + .memlv2_record-list { margin-top: 20px; }
    @media all and (max-width:$G-wMobile) {
      .popup-align.mode-md .popup-layer { width: 100%; }
      .memlv2_record-list {
        dd { display: block;
          .info-li + .info-li { margin-top: 6px; }
        }
        .state-icon { font-size: 11px; }
      }
    }
  }

  /* 라이브경매관리 온라인패들 응찰 이력 */
  &.auction_live_record-popup {
    .popup-align.mode-md .popup-layer { width: 740px; }
    .pop-header{ 
      .right_txt{ text-align: right; padding-right: 50px; margin-top: 25px; margin-bottom: -10px;
        span{ background-color: #f6f6f6; padding: 7px 20px; font-weight: 700; color: $colorB2; font-size: 16px;
          em{ padding-left: 5px;}
        }
      }
    }
    .pop-body {
      .article-area { border-top: 1px solid $colorL; max-height: 331px; }
    }
    .auction-bid-history { table-layout: auto; border-top: 0;
      tr {
        th:nth-child(1) { width: per(204px, 741px); }
        th:nth-child(2) { width: per(235px, 741px); }
        th:nth-child(3) { width: per(182px, 741px);}
        th:nth-child(4) { width: per(120px, 741px); }

        td:nth-child(1) { width: per(204px, 741px); }
        td:nth-child(2) { width: per(235px, 741px);  }
        td:nth-child(3) { width: per(182px, 741px); }
        td:nth-child(4) { width: per(120px, 741px); @include wordBreak-all();}
        > th { word-break: keep-all; padding: 0 10px; }
        > td { padding: 10px;}
        > td:first-child { font-weight: 600;}
        > td:last-child .succ { color: $colorP; font-weight: 600;}
      }
    }
    .state-icon {
      height: 21px; line-height: 21px; padding: 0 16px; font-size: 14px;
      background-color: $colorP; color: #fff;
    }
    .data-empty { border-top: 0;}
    @media all and (max-width:$G-wMobile) {
      .popup-align.mode-md .popup-layer { width: 100%; }
      .pop-header{ 
        .right_txt{ padding-right: 25px; margin-top: 25px; margin-bottom: -10px;
          span{ padding: 5px 15px; font-size: 14px;
            em{ padding-left: 5px;}
          }
        }
      }
      .pop-body {
        .article-area { max-height: 401px; }
      }
      .auction-bid-history { 
        tr {
          > td { padding: 5px;}
        }
      }
    }
  }

  /* 아트컨설팅 & 기업마케팅 팝업 */
  &.popup_marketing_pc {
    .pop-panel {
      .pop-body {
        .tab-section { padding: 30px 40px 40px; }
        .tab-con-section { padding-top: 0;}
      }
    }
    .tab-area.type-left_mm .tab-list li a { font-size: 20px;}
    .tab-con-section {
      .tab-con { height: 500px;}
    }

    @media all and (max-width:$G-wMobile) {
      .tab-area.type-left_mm .tab-list li a { font-size: 16px;}
    }
  }

  /* 검색하기 모바일용 팝업 */
  &.popup_search_mobile {
    .pop-panel {
      .pop-header { height: 47px; }
      .pop-body {
        .search-detail-inner {
          .input-wrap {
            input[type="text"] { width: 100%;}
          }
          .period-wrap {
            dt { height: 42px; line-height: 43px; }
            .date-wrap { display: flex; justify-content: space-between; align-items: center;
              input[type="text"] { width: 46%; }
            }
          }
          .date-wrap + .sub { display: block; font-size: 13px; color: $colorL; text-decoration: underline; margin-top: 10px; }
          .button-wrap { margin-top: 20px;
            .btn { width: 100%;}
          }
          .popup-align.mode-md .pop-panel .section { padding-top: 20px;}
        }
      }
    }
    @media all and (max-width:$G-wMobile) { }
  }

  /* 마이 아카데미 신청내역 결제정보 팝업 */
  &.myacademy_pay-popup {
    .popup-align.mode-md .popup-layer { width: 620px; }
    .academy-pay-wrap {
      border-top: 1px solid $colorL;
      dl {
        border-bottom: 1px solid #ccc; padding: 15px 20px; display: flex; justify-content: space-between; font-weight: 600; color: $colorB2;
        dt, dd { font-weight: 600;}
      }
      .total-wrap { background-color: $colorBGw; 
        dd {display: flex; flex-direction: column; justify-content: flex-end; text-align: right; }
        .pay-amount { color: $colorE;}
        .pay-method { color: $colorS;}
      }
    }
    @media all and (max-width:$G-wMobile) {
      .popup-align.mode-md .popup-layer { width: 100%; }
      .academy-pay-wrap {
        dl { border-bottom: 0; padding: 15px; }
        .total-wrap { border-top: #ccc 1px solid; border-bottom: #ccc 1px solid;
          .pay-method { font-weight: 500;}
        }
      }
    }
  }

  
  /* 온라인 경매 번호 인증 팝업 */
  &.online_confirm-popup{
    .form-area{
      .form_label{ border-top: 1px solid #999; padding-top: 20px; margin-top: 20px;
        .mem_txt{ font-size: 20px; color: $colorB2; font-weight: 700;}
        i{color: $colorE;}
      }
      .form_body{ margin-top: 15px;
        .input-group{
          .btn{ border-left: 0px ;}
        }
        .re-check{ margin-top: 12px;
          .error_text{ margin-top: 5px;}
        }
      }
    }
    .accordion-area{ margin-top: 15px;
      .header-area{ 
        label{ font-size: 20px; font-weight: 700; color: $colorB2;
          span{ color: $colorP;}
        }
      }
      .con-area{
        .con-header{
          .policy_cont{ border-bottom: 1px solid #ccc; padding-bottom: 30px; margin-bottom: 30px;
            label{ color: $colorB2; font-weight: 700; }
          }
        }
        .con-body{
          .text-area{ 
            ul{
              li{ font-size: 18px; line-height: 1.6; font-weight: 500; padding-left: 0.7em; 
                em{ color: $colorP;}
                &.point_item{ color: $colorP;}
                label{ line-height: 28px;}
                a { text-decoration: underline;}
              }

            }
            &.check_item{ 
              li+li {margin-top: 10px;}
            }
          }
        }
      }
      >.text-area{ margin-top: 15px; 
        p{ position: relative; padding-left: 12px;
          &:after{ content: ""; display: block; width: 3px; height: 3px; position: absolute; top: 12px; left: 0; background-color: #888 ;}
          em { color:$colorB2; font-weight: 700;}
        }
      }
    }
    .btn-area{
      a{ width: 170px; margin-top: 35px; margin-right: 7px;}
    }
    @media all and (max-width:$G-wMobile) { 
      .form-area{
        .form_label{ padding-top: 20px; margin-top: 20px;
          .mem_txt{ font-size: 15px; }
        }
        .form_body{ margin-top: 10px;
          .re-check{ margin-top: 10px;
          }
        }
      }
      .accordion-area{ margin-top: 15px;
        .header-area{ 
          .accordion_name{ padding-left: 15px;}
          label{ font-size: 15px; 
          }
        }
        .con-area{
          .con-header{
            .policy_cont{ padding-bottom: 15px; margin-bottom: 15px;
            }
          }
          .con-body{
            .text-area{
              ul{
                li{ font-size: 14px; line-height: 22px;
                  &::after{ top: 9px;}
                  label{ line-height: 22px;}
                }
              }
              &.check_item{ 
                li+li {margin-top: 10px;}
              }
            }
          }
        }
        >.text-area{ margin-top: 12px;
          p{ padding-left: 8px;
            &:after{ top: 8px; }
          }
        }
      }
      .btn-area{
        .tac{
          :last-child{ margin-right: 0px;}
          a{ width: 48%; margin-top: 25px; margin-right: 5px;}
        }
        
      }
    }
  }

  /* 회원탈퇴 팝업 */
  &.member_drop-popup{ 
     .title_text{ text-align: center; margin-top: 50px;
       span{ font-weight: 700; color: $colorB2;}
    } 
    .search-group_btn{margin-bottom: 20px;}
  }
  @media all and (max-width:$G-wMobile) {
    &.member_drop-popup{ 
      .title_text{  margin-top: 20px; 
        span{ font-size: 15px;}
     } 
     .search-group_btn{margin: 15px 0 -10px;}
   }
  }
}