@charset "utf-8";
/* ============== spriteimg ========== */
/* pc */
@mixin sprite-width($sprite) {
  width: nth($sprite, 5);
}
@mixin sprite-height($sprite) {
  height: nth($sprite, 6);
}
@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 3);
  $sprite-offset-y: nth($sprite, 4);
  background-position: $sprite-offset-x  $sprite-offset-y;
}
@mixin sprite-image($sprite) {
  $sprite-image: nth($sprite, 9);
  background-image: url(#{$sprite-image});
}
@mixin sp_icons($sprite) {
  @include sprite-image($sprite);
  @include sprite-position($sprite);
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}


/* mobile */
@mixin sprite-width2x($sprite) {
  width: nth($sprite, 5) / 2;
}
@mixin sprite-height2x($sprite) {
  height: nth($sprite, 6) / 2;
}
@mixin sprite-position2x($sprite) {
  $sprite-offset-x: nth($sprite, 3) / 2;
  $sprite-offset-y: nth($sprite, 4) / 2;
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite-image2x($sprite) {
  $sprite-image: nth($sprite, 9); 
  background-image: url(#{$sprite-image});
  background-size: (nth($sprite, 7) / 2) (nth($sprite, 8) / 2);
}
@mixin sp_icons2x($sprite) {
  @include sprite-image2x($sprite);
  @include sprite-position2x($sprite);
  @include sprite-width2x($sprite);
  @include sprite-height2x($sprite);
}





