.print-wrap {
  width: 21cm;
  min-height: 27.7cm;
  margin: 0 auto;
}
@page {
  size: A4 ;
  margin: 0;
}

.print-wrap { //width: 792px;
  #contents { padding-top: 62px; min-height: auto;}
  .print-section { 
    .section-inner { padding: 0 50px;}
  }

  //print header
  .print-header {
    width: 100%; position: fixed; top: 0; z-index: 2; left:  50%; background-color: #fff; border-bottom: 1px solid #dedede;
    transform: translate(-50%, 0); 
    
    .header-inner { padding: 20px 50px 15px; display: flex; justify-content: space-between; align-items: center;
      .header_logo {flex-basis: 160px; width: 160px; height: 26px; line-height: 26px; margin-top: 0; background:none; }
      .header-print { font-size: 16px; color:#333; display: flex;
        i { margin-right: 10px;}
      }
    }
  }

  //print body
  .print-panel {
    //타이틀
    .panel-header { border-bottom:1px solid #dedede; padding: 6px 0 16px;
      .print-title {
        .title { font-size: 22px; line-height: 48px; font-weight: 600; color:$colorB; }
        .data { font-size: 16px; font-weight: 600; color:$colorB2; margin-top: 4px;
          span { position: relative; padding-left: 10px; margin-right:7px;
            &::before { content: ''; display:inline-block; width:1px; height: 12px; background:#ccc; position: absolute; left:0; top: 50%; margin-top: -6px;}
            &:first-child { padding-left:0;
              &::before { display: none;}
            }
          }
        }
      }
    }

    //내용
    .panel-body { padding: 40px 0;
      //작가정보
      .print_detail-article {
        .image-area { text-align: center; margin-bottom: 35px;
          img { max-width: 325px;}
        }
      
        .artist-area { 
          .index-num { font-size: 16px; font-weight: 600; color:$colorB2; margin-bottom: 5px;}

          .name-info { display: flex;  padding-bottom: 25px;  flex-wrap: wrap; flex-wrap: wrap; flex-direction: column;
            .name { font-size: 22px; font-weight: 700; color:$colorB; word-break: keep-all;}
            .desc { color:$colorB2; font-size: 16px; font-weight: 500; display: flex; flex-direction: column;
              span {  margin-top: 8px; }
            }
          }

          //금액
          .price-area { border-top:1px solid #dedede; padding: 20px 0 0;
            .price-list {
              dt, dd { display: inline; font-size: 18px; font-weight: 700; color:$colorB2;}
            }
          }

        }
      }

      //작품정보
      .print_view-article { padding-top: 25px; margin-top: 20px; border-top:1px solid #dedede;
        .view_editor-warp { padding:0; display: flex; justify-content: space-between;
          .info-box { width: calc(50% - 15px);
            .title { font-size: 17px; font-weight: 700; color:$colorB2; }
            .desc { font-size: 15px; font-weight: 500; color:$colorD; }
            .title + .desc {margin-top: 10px;}
          }
          .info-box + .info-box { margin-top: 0;}
        }
      }

    }
  }

 
  //print footer
  .panel-footer { border-top: 1px solid #ccc; padding: 30px 0 24px;
    .footer-logo { 
      img { width: auto; height: 22px;}
    }
    .footer-info { padding-left: 110px; position: relative; margin-top: 11px;
      dt, dd { padding: 6px 0;}
      dt { font-size: 14px; font-weight: 700; color:#333; width: 110px; position: absolute; left:0;}
      dd { font-size: 14px; font-weight: 500; color:#666;}
    }
  }

}

@media print {
  .page-break { page-break-inside:avoid; page-break-after:auto }
  .print-wrap { 
    .print-header { position: static; left:0;  transform: translate(0, 0);
      .header-print { opacity: 0;}
    }
    #container { min-height: auto;
      #contents { padding-top:0;}
    }
  }
}