body{
  .mCustomScrollBox {
    /* ===  컨텐츠 영역  === */
    .mCSB_container { margin-right: 0px;  }

    /* === 스크롤 바 영역 === */
    // 컨텐츠 hover: bar 활성화색
    &:hover .mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
      /* background-color:rgba(0,0,0,1); */
      background-color: rgba(0,0,0, 0.2); 
    }
      // 컨텐츠 hover * active: bar 영역 bg
    .mCSB_scrollTools.mCSB_scrollTools_onDrag .mCSB_draggerRail,
    &:hover .mCSB_scrollTools .mCSB_draggerRail { 
      background-color: rgba(0,0,0, 0.0); 
    }

    .mCSB_scrollTools { opacity: 1;  
        // bar 기본색
      .mCSB_dragger {  opacity: 1; border-radius: 0;
        .mCSB_dragger_bar {  background-color: rgba(0,0,0,0.2); border-radius: 0;
        }
      }
      // bar 활성화색
      .mCSB_dragger.mCSB_dragger_onDrag .mCSB_dragger_bar, 
      .mCSB_dragger:active .mCSB_dragger_bar {
        background-color:rgba(0,0,0,0.2);
      }
      // hover 
      .mCSB_dragger:hover .mCSB_dragger_bar {
        background-color:rgba(0,0,0,0.2);
      }
      // bar bg 기본색
      .mCSB_draggerRail { background-color: rgba(0,0,0, 0.0);  border-radius: 0; 
        -webkit-transition: opacity .2s ease-in-out,background-color .2s ease-in-out;
        -moz-transition: opacity .2s ease-in-out,background-color .2s ease-in-out;
        -o-transition: opacity .2s ease-in-out,background-color .2s ease-in-out;
        transition: opacity .2s ease-in-out,background-color .2s ease-in-out; 
      }
    }

    /* 세로  */
    .mCSB_scrollTools.mCSB_scrollTools_vertical {  width: 5px; 
      // bar 기본색
      .mCSB_dragger { right: 0px; width: 5px; 
          .mCSB_dragger_bar { width: 5px;  }
      }
      // bar bg 기본색
      .mCSB_draggerRail { width: 5px;  }
    }
  }

  /* 가로 horizontal */
  .mCustomScrollbar_x  {  overflow-x: auto; white-space: nowrap;
    .mCustomScrollBox { 
      .mCSB_scrollTools.mCSB_scrollTools_horizontal { width: auto; height: 5px;  opacity: 1;
        // bar 기본색
        .mCSB_dragger { bottom: 0px; height: 5px; 
          .mCSB_dragger_bar { height: 5px; margin: 0px auto;}
        }
        // bar bg 기본색
        .mCSB_draggerRail { height: 5px; margin:0; }
      }
    }
  }


  .scroll-type { overflow: hidden; overflow-y: auto;
    &::-webkit-scrollbar{ 
      width: 5px;
      height: 5px;
      -webkit-appearance: none;
      background-color: transparent;
    }
    &::-webkit-scrollbar-thumb {
      background-color: rgba(0,0,0, 0.2) ; 
    }
  }

  .mobile_scroll-type {}

}