/* footer.scss */
.sub-wrap {

  /* 푸터 main 겝조절 */
  .footer {  position: relative;
    // 회사소개
    .footer-gnbmenu  {
      >li { vertical-align: baseline;
        &::before { margin-top: 3px;}
        a { vertical-align: baseline;}
        @media all and (max-width:$G-width)  {
          &::before { margin-top: 2px;}
        }
      }
    } 
    // 약관
    .footer-infomenu {
      >li { vertical-align: baseline;
        &::before { margin-top: 1px;}
        a { vertical-align: baseline;}
        @media all and (max-width:$G-width)  {
          &::before { margin-top: 2px;}
        }
      }
    } 
  }
}



/* 스크롤 탑 */
.scroll_top-box { position: fixed; bottom:calc( 64px + 52px ); width: 100%; z-index: 9;
  .box-inner { position: relative;  max-width: 1920px; padding: 0 60px;
    width: 100%; height: 0; margin:0 auto;
    text-align: right;}
  .btn-scroll_top { display: inline-block; font-size: 0; line-height: 0;
    width: 52px; height: 52px; border: 1px solid #ddd;
    background-color: #fff;
    border-radius: 50%; @include shadow(0px, 5px, 10px, .05); 
    text-align: center;
    i.icon-scroll_top { margin-top: 14px;  }
    &:hover{  border: 1px solid $colorMain;
      background-color:$colorMain; 
      i.icon-scroll_top { &::after{ @include sp_icons($icon-scroll_top-over)} }
    }
  }
  @media all and (max-width:$G-width)  {
    .box-inner { padding:0 50px; max-width: 1420px; }
  }
  @media all and (max-width:$G-wMobile)  {  bottom:calc(25px + 41px);
    .box-inner { padding: 0 25px; }
    .btn-scroll_top {   width: 41px; height: 41px; 
      i.icon-scroll_top { margin-top: 13px;  }
      &:hover{ border: 1px solid $colorMain;
        background-color:$colorMain; 
        i.icon-scroll_top { &::after{ @include sp_icons2x($icon-scroll_top-over-2x)} }
      }
    }
  }
}



/* 상세 페이지 */
.type-details_view {

  /* 스크롤 탑 */
  @media all and (max-width:$G-wMobile)  {
    .scroll_top-box { bottom:calc(65px + 25px + 41px);
    }
  }

  .footer { }
  @media all and (max-width:$G-wMobile)  {
    .footer { padding-bottom: 65px; }
  }
}


/* 응찰 하기 (모바일 하단 "전체 보기") */
.type-bidding {

  /* 스크롤 탑 */
  @media all and (max-width:$G-wMobile)  {
    .scroll_top-box { bottom:calc(25px + 41px + 55px );
    }
  }

  @media all and (max-width:$G-wMobile)  {
    .last-section { padding-bottom: calc( 60px + 55px) }
  }

}