.content-panel{
 
  /*  */
  &.type_panel-marketing {
    
    .service-marketing { margin-top: 15px;
      @media all and (max-width:$G-wMobile)  { 
        margin-top: 5px;
      }

      .marketing_list {
        border-top:1px solid #333;border-bottom:1px solid #333;
        > li { border-top:1px solid #ddd; padding:60px 0;
          &:first-child { border-top:none;}
        }

      }
      @media all and (max-width:$G-wMobile)  { 
        .marketing_list {
          > li {  padding:25px 0;}
        }
      }

      .service_art_list { display:flex;flex-wrap: wrap;justify-content: space-between;
        > li {
          width: calc(50% - 20px); margin-bottom:20px; position:relative;
        }
        .list_cont {
          border: 1px solid #ccc;
          display: flex; padding:55px ; height:100%;
          .img-area { width: 70px; img {width: 100%;}}
          .typo-area { 
            padding: 0px 0px 0px 45px;
            display: flex; flex-direction: column; justify-content: flex-start;
            .area-header {
              font-size: 26px;  line-height: 1.5; font-weight: bold;
              color:#333;
              em    { display: inline; color:$colorP; }
              span  { display: inline; }
            }
            .area-body   {  margin-top: 20px;
              font-size: 18px; font-weight: 500; line-height: 1.8; color:#555; 
            }
          }
        }
      }
      @media all and (max-width:$G-wMobile)  { 
        .service_art_list { display:flex;flex-wrap: wrap;justify-content: space-between;
          > li {
            width: 100%; margin-bottom:10px;
          }
          .list_cont {
            display: block; padding:20px 5px ; height:100%;
            .img-area { width: 50px; margin:0 auto; img {width: 100%;}}
            .typo-area { 
              padding: 0px; text-align: center; margin-top:20px;
              display: flex; flex-direction: column; justify-content: flex-start;
              .area-header {
                font-size: 18px; 
              }
              .area-body   {  margin-top: 7px;
                font-size: 14px;
              }
            }
          }
        }
      }
      

      .banner { display: flex; 
        .img-area { width: per(620px, 1320px ); img {width: 100%;}}
        .typo-area { width: per(700px, 1320px );
          padding: 10px 30px 10px 100px;
          display: flex; flex-direction: column; justify-content: center;
          .area-header {
            font-size: 26px;  line-height: 1.5; font-weight: bold;
            color:#333;
            em    { display: inline; color:$colorP; }
            span  { display: inline; }
          }
          .area-body   {  margin-top: 20px;
            font-size: 18px; font-weight: 500; line-height: 1.8; color:#555; 
            .txt_ex {padding-left:25px; position:relative;
              a, span { vertical-align: top; display: inline;}
              
                .icon_circle_arrow { vertical-align: top; position:absolute; top: 6px; left: 0;
                  display:inline-block; @include sp_icons($circle_arrow);
                }
              
              display:inline-block; margin-top:10px;color:#333333;
            }
            .desc_list_type0 {
              > li {
                padding-left:15px; position:relative;
                &:before {content:''; position:absolute; top:13px; left:0; width:4px; height:4px; background:$colorMain; border-radius: 50%; }
                em {
                  color:$colorMain;
                }
              }
            }
          }

          .area-footer { margin-top: 10px;
            font-size: 18px; font-weight: 600; color: #333;
            a, span { vertical-align: top; display: inline;}
            i       { vertical-align: top; margin-top: 2px; margin-right: 3px;}
            .btn_wrap { font-size:0;
              .btn { min-width:170px; margin-top: 10px; margin-right:10px; display:inline-block;}
            }
          }
        }
      }
      @media all and (max-width:$G-wMobile)  { 
        .banner { display: block; 
          .img-area { width: 100%  }
          .typo-area { width: 100%;
            padding: 25px 0px;
            display: block; flex-direction: initial; justify-content: normal;
            .area-header { font-size: 18px;  line-height: 1.36; }
            .area-body   {  margin-top: 10px;
              font-size: 14px;  line-height: 1.5; 

              .txt_ex { padding-left:18px; position:relative;
                .icon_circle_arrow {top: 4px; @include sp_icons2x($circle_arrow-2x);
                }
                display:inline-block; margin-top:10px;color:#333333;
              }
              .desc_list_type0 {
                > li {
                  padding-left:7px; margin-bottom:5px;
                  &:before {content:'';top:8px; width:3px; height:3px; border-radius: 50%; }
                }
              }

            }
            .area-footer { margin-top: 10px; font-size: 13px; 
              a, span { vertical-align: top; display: inline;}
              i       { vertical-align: top; margin-top: 0px; margin-right: 3px;}
              .btn_wrap {
                .btn { min-width:160px; margin-right:10px; }
                &.btns { 
                  display: flex;
                  flex-wrap: wrap;
                  justify-content: space-between;
                  .btn {
                    width:49%; margin-top:2vw; margin-right:0; min-width: auto;
                  }
                }
              }
            }
          }
        }
      }
    }

  }

}
