.content-panel{
  /* 회원가입 */
  &.type_panel-member {max-width: 462px; margin: 0 auto;
    .mem_txt{font-size: 20px; font-weight: 700;}
    .panel-header { position: relative; padding: 35px 0 35px;
      &.complete_title{ padding: 35px 0 10px;}
      .title{text-align: center;
        &.txt_input{text-align: left;}
      }
      .required { position: absolute; right:0; top:50%;
        i { color:#db1b1b}
      }
      .sub_txt { margin-top: 20px; font-weight: 500;}
    }
    .panel-body{
      .join_txt-wrap{text-align: center;
        .join_txt{font-size: 20px;}
      }
    }
    .panel-footer { padding-top: 40px;}

    //회원가입sns
    .id_name_box { 
      span { vertical-align: middle; }
    }
    .sns-icon { font-size:0; width: 36px; height: 36px; margin-left: 10px;
      img { width:100%; height: 100%;}
    }

    //회원정보입력폼
    .form_list { border-top:1px solid #999; border-bottom:1px solid #ccc; padding-bottom: 25px;
      >li { margin-top: 20px;
        .form_label { color:#333; margin-bottom: 10px;
          i { color:#db1b1b; font-size: 20px; font-weight: 700;}
        }
        .form_body{
          .input_group{ display: flex;
            input:first-child{ width: 40%; margin-right: 10px;}
          }
        }
        .error_text { margin-top: 7px; }
        .search-add {
          .add_txt { min-height: 32px;}
        }
        .re-check {margin-top: 15px;}
        .search-add { margin-top: 5px;
          .add_txt, .form_label { margin-bottom: 6px;}
        }
        .error_text + .re-check { margin-top: 14px;}
        .error_text + .search-add { margin-top: 14px;}

        //파일첨부
        .trp.file-box { width: 157px; margin-top: 25px;
          .btn { background:#f6f6f6; border:1px solid #bdbdbd; color:#555; font-weight: 600;}
          &:hover .btn { background:#F0F0F0; border-color:#bdbdbd;}
        }
        //영문추가
        .mark_dot-list{ color: $colorS;}
        .radio-box{ margin: 20px 0 25px;}
        .warning_text{ color: $colorE;}
        .passport-wrap{ background-color: #f6f6f6; padding: 30px; margin-top: 25px;
          span{ color: $colorB2; font-weight: 700; margin-bottom: 5px;}
        }
      }
    }

    //약관동의리스트
    .policy_accordion { 
      .checkbox_title{ margin-top: 55px; color: $colorB2;}
      .all_check, .accordion_name {
        .checkbox-box {
          label { font-size: 20px; font-weight: 700; line-height: 1.5; color:#333; height: auto;}
          i{ top: 5px;}
        }
        .required { color:#21a099;}
      }
      .check_all-wrap { 
        .all_check {padding:15px 0 28px;}
      }
      .con-area{ padding: 25px 20px 25px 30px;
        .policy_cont{ height: 230px;
          &.scroll_no{ height: 100%;}
          .tb1{ line-height: 1.6;}
          h4{ font-size: 18px; font-weight: 700; color: $colorB2; margin: 30px 0 5px;}
          .title_txt{ font-weight: 700; color: $colorB2; margin-top: 30px;
            &:first-child{margin-top: 0px;}
          }
        }
      }
      

      //정보수신방법
      .receive_info { margin-top: 30px;
        dt { color:#333; margin-bottom: 15px;}
      }

    }

    //라벨, 파일 리스트
    .file-box-list,
    .label_list_box  { display: flex; flex-wrap: wrap; margin-top: 15px; width: calc( 100% + 20px); margin-left: -10px;
      p { display:inline-block; display: flex; align-items: center; color:#333; margin: 5px 10px;
        i { color:#777; margin-right: 3px;}
      }
      .btn_del { display:inline-block; margin-left: 8px; height: 16px;}
      //파일리스트
      .icon_down { display: inline-block; margin-right:15px;}
      .staff-list {  font-size: 0; padding: 0 10px;
        li {display: inline-block; padding:9px 15px; height: 38px; 
          margin-right: 9px; margin-bottom: 5px;
          background-color: #efefef; border-radius: 19px; font-size: 0;
          span, button { vertical-align: middle;}
          span {font-size:  16px;  line-height: 20px; font-weight: 500;}
          button { font-size: 0;  padding-left: 8px; margin-left: -5px;}
        }
      }
    }

    //회원가입완료
    .join_txt { font-weight: 500;
      b { color:#111; font-weight: 600;}
      .level { color:#21a099; font-weight: 600; }
    }
    .complete-wrap { //margin-top: -23px;
      .complete_area{ margin: 45px 0 35px;
        .complete_msg{ border-top: 1px solid #ccc; border-bottom: 1px solid #ccc; text-align: center; padding: 25px 0; margin: 35px 0;
          em{ color: $colorP;}
          strong{ color: #111; font-weight: 700;}
        }
        .complete_msg + p a { display: inline; text-decoration: underline;}
      }
      .tit { color:#111; margin-bottom:25px; } 
      .join_txt + .tit { margin-top:50px;}
    }

    //회원가입가이드박스 
    .join_txt-wrap + .join_guide { margin-top: 35px;}
    .join_guide + .join_guide { margin-top: 40px;}
    .join_guide { border:1px solid #ccc; padding: 40px;
      .tt4{margin-bottom: 30px; color: #111;
        &.sub_title{ margin-bottom: 5px;}
      }
      dl + dl{border-top:1px solid #ccc; padding-top: 35px; margin-top: 35px;}
      dl { 
        dt { color:#111; margin-bottom: 5px;}
        dd { 
          p{ padding-left: 13px;}
        }
        dd + dd { margin-top: 35px;}
        .signup_btn{ margin-top: 30px;}
      }
      .dot_list {
        li { color:#555;
          &::before { content:''; top: 13px; width: 4px; height: 4px; background:#777;}
        }
      }
      .btn_line { margin-top: 25px;
        .btn { padding: 5px 20px; }
      }

      //sns
      .sns_set { margin-top:40px; border-top: 1px solid #ccc;
        .title{font-size: 20px; font-weight: 700; margin-top: 30px;}
        .sns_icon_wrap { margin-top: 35px; display: flex; justify-content: space-around;
          ul { display: flex; 
            li { margin: 0 20px;}
          }
        }
      }
    }
    //영문추가
    .join_account{ background-color: #f6f6f6; padding: 27px; margin-top: 45px;
      .btn-wrap{ margin-top: 20px; 
        a{width: 170px;}
      }
    }

    @media all and (max-width:$G-wMobile)  {
      .mem_txt{font-size: 15px; }
      .panel-header { position: relative; padding: 15px 0 25px;
        .required {  margin-top: -5px;}
      }
      .panel-body{
        .join_txt-wrap{
          .join_txt{font-size: 15px;}
        }
      }
      .panel-footer { padding-top: 30px;
        .button-area {
          .btn { width: 100%;}
        }
      }
      //회원가입sns
      .sns-icon { width: 28px; height: 28px; margin-left: 5px;}
  
      //회원정보입력폼
      .form_list {padding-bottom: 20px;
        >li {
          .form_label{
            i { font-size: 15px; }
          }
          .search-add {
            .add_txt { min-height: 20px;}
          }

          .re-check, .search-add { margin-top: 10px;
            .add_txt, .form_label { margin-bottom: 6px;}
          }
          .error_text + .re-check { margin-top: 10px;}
          .error_text + .search-add { margin-top: 10px;}

          //파일첨부
          .trp.file-box { width: 160px; }
          //영문추가
          .passport-wrap{  padding: 20px 10px; margin: 20px auto; display: flex; justify-content: space-around;
            .passport-box{ 
              img{ width: 270px; height: 165px;}
            }  
          }
          .radio-box{ margin: 5px 0 15px;}
        }
      }
  
      //약관동의리스트
      .policy_accordion { margin-top: 22px;
        .checkbox_title{ margin-top: 35px; }
        .all_check, .accordion_name {
          .checkbox-box {
            label { font-size: 15px;}
            i{ top: 2px;}
          }
        }
        .check_all-wrap { 
          .all_check {padding:5px 0 18px 0;}
        }
        .con-area{ padding: 20px 15px 20px 15px;
          .policy_cont{ height: 175px;
            .tb1{ line-height: 1.6;}
            h4{ font-size: 14px; margin: 20px 0 5px;}
            .title_txt{ margin-top: 20px;
              &:first-child{margin-top: 0px;}
            }
          }
        }
  
        //정보수신방법
        .receive_info { margin-top: 25px;
          dt { color:#333; margin-bottom: 10px;}
        }
      }
    
      //라벨,파일리스트리스트
      .file-box-list,
      .label_list_box  { 
        .btn_del { margin-top: -2px}
        //파일리스트
        .icon_down { margin-right:10px;}
        .staff-list { width: calc(100% - 43px); font-size: 0;
          li {display: inline-block; padding:9px 17px 8px; height: 32px; 
            margin-right: 10px; border-radius: 16px; 
            span {font-size:  13px;  line-height: 15px;}
            button { padding-left: 8px; margin-left: 0px;}
         }
        }
      }

      //회원가입완료
      .complete-wrap { //margin-top: -20px;
        .complete_area{ margin: 15px 0 25px;
          .complete_msg{  padding: 20px 0; margin: 20px 0;
          }
        }
        .tit { margin-bottom:20px; }
        .join_txt + .tit { margin-top:40px;}
      }

      //회원가입가이드박스
      .join_txt + .join_guide { margin-top: 40px;}
      .join_guide + .join_guide { margin-top: 10px; }
      .join_guide { padding: 30px 20px;
        .tt4{margin-bottom: 30px;}
        dl + dl{padding-top: 30px; margin-top: 30px;}
        dl { 
          dd + dd { margin-top: 20px;}
          .signup_btn{ margin-top: 20px;}
        }
        
        .dot_list {
          li { 
            &::before { content:''; top: 9px; width: 2px; height: 2px;}
          }
        }
        .btn_line { margin-top: 20px;
          .btn { padding:3px 20px; }
        }

        //sns
        .sns_set { margin-top:24px; padding-top: 20px; 
          .title{font-size: 15px; margin-top: 0px;}
          .sns_icon_wrap { margin-top: 20px;
            ul { 
              li { margin: 0 10px;}
            }
          }
          
        }
       
      }
       //영문추가
       .join_account{  padding: 20px; margin-top: 25px;
        .btn-wrap{ margin-top: 10px; 
         a{width: 130px;}
          }
        }

    }

  }
}







/* ============ 팝업 ========= */
.trp.popupfixed-wrap { 
  &.XXXXXXXX-popup {


  }
}