
.tab-cont-section {}


/* 메뉴 , 컨텐츠 */
.tab-area{}
.tab-cont {}

.content-panel{
 
  /*  */
	&.type_panel-loan {
      .panel-header {
         font-size: 18px;line-height: 32px; color: $colorD;
      }
      
      @media all and (max-width:$G-wMobile)  {
         .panel-header {
            font-size: 13px;line-height: 21px; color: $colorD; padding: 16px 0;
         }
      }
		.loan-step-area {
			margin: 18px 0 0; padding: 30px 0 55px;
			border: 1px solid #333;border-width: 1px 0;
         .loan-step  {
            
            .item-area {
               padding: 60px 0 57px; 
               .icon-box {
                  display: block; margin: 0 auto; font-size: 0;line-height: 0;
               }
               .item-header {
                  text-align: center;
                  .title {
                     margin-top: 6px;
                     font-weight: 700;
                     span {display: block;color: $colorP;font-size: 16px;line-height: 28px;}
                     b{display: block;color: $colorB2;line-height: 28px; margin-top: 2px;}
                  }

               }
               .item-body {
                  font-size: 18px;line-height: 32px;color: $colorD;
                  p + p {margin-top: 6px;}
                  a {display: inline; text-decoration: underline; color: $colorP;}
                  .guide-emailtitle {
                     margin-top: 23px; font-weight: 500; 
                     b {font-weight: 600; color: $colorB2;}
                     p + p {margin-top: 5px;}
                  }
               }
            }

            &.loan-step-first {
               .item-area {
                  display: flex; align-content: center; 
                  .item-header {width: 20%;}
                  .item-body {
                     width: 76.22%; padding: 0 7.954%; border-left: 1px solid #ccc;
                  }
               }
            }
            &.loan-step-other {
               /* border-top: 1px solid #ccc; */
               display: flex;  align-content: flex-start; flex-wrap: wrap;
               .item-area {
                  width: 33.3333%;  padding:45px 0 0px;
                  .item-header {
                     .title {
                        margin-top: 11px;
                        b{margin-top: 2px;}
                     }
                  }
                  .item-body {margin-top: 20px;text-align: center;}
               }
            }
         }
		}
      @media all and (max-width:$G-wMobile)  {
         .loan-step-area {
            margin-top: 10px; padding: 20px 0 25px;
            .loan-step  {
               
               .item-area {
                  padding: 25px 0 19px; 
                  .item-header {
                     .title {
                        margin-top: 14px;
                        span {font-size: 12px;line-height: 14px;}
                        b{line-height: 1; margin-top: 9px;}
                     }

                  }
                  .item-body {
                     font-size: 13px;line-height: 21px;margin-top: 12px;
                     p + p {margin-top: 12px;} 
                     .guide-emailtitle {
                        margin-top: 17px;
                        p + p {margin-top: 2px;}
                     }
                  }
               }

               &.loan-step-first {
                  padding-left: 15px;padding-right: 15px;
                  .item-area {
                     display: block;
                     .item-header {width: 100%;}
                     .item-body {
                        width: 100%; padding: 0; border-left: 0;
                        margin-top: 20px;padding-top: 16px;border-top: 1px solid #ccc;
                     }
                  }
               }
               &.loan-step-other {
                  padding-bottom: 10px;
                  display: flex; flex-flow:row wrap;
                  .item-area {
                     width: 50%; padding-top: 35px; padding-bottom: 0px;
                     .item-header {
                        .title { margin-top: 14px; b{margin-top: 9px;} }
                     }
                     .item-body {margin-top: 12px;}
                  }
               }
            }
         }
      }

      .help-box { position: relative;  padding-left: 30px;
         i { position: absolute; top:5px; left: 0;}
         span, em { font-size: 18px; line-height: 32px; color:$colorB2}
         @media all and (max-width:$G-wMobile)  {
            padding-left: 20px;
            i { position: absolute; top:2px; left: 0;}
            span, em { font-size: 14px; line-height: 21px;}
         }
      }

		.dot {
			li {
				position: relative; padding-left: 13px;
				&:before {
					content:"·"; position: absolute; top:0;left:2px;
				}
			}
		}
		.refer {
			p {
				position: relative; padding-left: 13px; color: $colorS;
				&:before {
					content:"※"; position: absolute; top:0;left:0;
				}
			}
		}
		.loan-document-area {
			margin-top: 80px;
			display: flex; justify-content: space-between; flex-wrap: wrap; /*align-content: space-between;*/
         .loan-document {
            width: calc(50% - 10px); 
            .item-area {	
               border: 1px solid #ccc; width: 100%;
               & +.item-area {margin-top: 20px;}
               .item-header {
                  background: #f8f8f8; height: 100px;
                  padding: 0 40px; display:flex; align-items:center;
                  color: $colorB2;
               }
               .item-body {
                  padding: 32px 35px;
               }



            }
            &+.loan-document {
               display:flex; 
            }
            .refer {
               margin-top: 22px; padding-top: 24px; border-top: 1px solid #ccc;
            }
         }
		}
      @media all and (max-width:$G-wMobile)  {
         .dot {
            li {
               padding-left: 10px;
               &:before {
                  left:0px;
               }
            }
         }
         .refer {
            p {
               padding-left: 10px; 
            }
         }
         .loan-document-area {
            margin-top: 25px;
            display: block;
            .loan-document {
               width: 100%; 
               
               .item-area {	
                  & +.item-area {margin-top: 15px;}
                  .item-header {
                     height: 60px;padding: 0 20px;
                  }
                  .item-body {
                     padding: 16px 20px;
                  }
               }
               &+.loan-document {
                  margin-top: 15px;
                  display:block; 
               }
               .refer {
                  margin-top: 13px; padding-top: 14px; 
               }
            }
         }
      }

		.loan-condition-area {
			margin-top: 74px;
			.table-panel {
				position: relative;
				.p-header {
					line-height: 1; margin-bottom: 30px;
					.title {color: $colorB2;}
				}
				.p-body {
					border-top: 0;
					.refer { margin-top: 10px;}
				}
			}
         .loan-company {
            margin-top: 40px;padding: 33px 40px;
            border: 1px solid #ccc;
            .dot {
               display:flex;align-content: flex-start; justify-content: flex-start;flex-wrap: wrap;
               li {
                  width: 33.33333333%; color: $colorD; line-height: 34px;
                  &:nth-child(1) {order:1}
                  &:nth-child(2) {order:4}
                  &:nth-child(3) {order:2}
                  &:nth-child(4) {order:5}
                  &:nth-child(5) {order:3}
               }

            }
            .btn-box {
               margin-top: 23px;
               .btn {
                  padding-left: 22px;padding-right: 22px;
                  span {letter-spacing: 0.025em;}
               }
            }
            .description {
               margin-top: 30px;padding-top: 30px;border-top: 1px solid #ccc;
               font-size: 16px; line-height: 32px; color: $colorD;
            }
         }
		}

      @media all and (max-width:$G-wMobile)  {

         .loan-condition-area {
            margin-top: 52px;
            .table-panel {
               .p-header {
                  margin-bottom: 15px; 
               }
               .p-body {
                  .refer {position: static; top:auto;right:auto; margin-top: 12px;}
               }
            }
            .loan-company {
               margin-top: 17px;padding: 22px 20px;
               border: 1px solid #ccc;
               .dot {
                  display:block;
                  li {
                     width: 100%; color: $colorD; line-height: 1.5; 
                  }

               }
               .btn-box {
                  margin-top: 15px;
                  .btn {
                     padding-left: 15px;padding-right: 15px; 
                  }
               }
               .description {
                  margin-top: 20px;padding-top: 18px;
                  font-size: 12px; line-height: 19px;
               }
            }
         }
      }

 

	}

   &.type_panel-storage {
      .storage-area {
         padding-top: 93px;
         .article-header {
            margin-bottom: 34px;  color:$colorB2;
         }
      }
      .storage-intro {margin-top: -17px;}
      .storage-image {margin-top: 90px;}
      .inquiry-article  { margin-top: 93px ;}
      @media all and (max-width:$G-wMobile)  { 
         .storage-area {
            padding-top: 36px; padding-bottom: 10px;
            .article-header {
               margin-bottom: 26px;
            }
         }
         .storage-intro {margin-top: -14px;}
         .storage-image {margin-top: 20px;}
         .inquiry-article  { margin-top: 35px ;}
      }

      .storage-system {
         display: flex; justify-content: space-between; /*align-content: space-between;*/
         .item-area {
            &:nth-child(2) { 
               .item-body {
                  .desc {padding-right: 0;}
               }
            }
            width: calc(33.333333333333% - 40px / 3);
            border: 1px solid #cfcfcf;
            .item-body {
               padding: 36px 38px 33px;
               .title {color: $colorB2;}
               .desc { margin-top: 10px; padding-right: 10%;color: $colorD;
                  
               }
               .info {
                  margin-top: 23px;padding-top: 23px; border-top: 1px solid #ccc;
                  color: $colorD;
                  li {
                     position: relative;padding-left: 28px;
                     .icon-system_arrow{
                        position: absolute; top:8px;left:0;
                     }
                  }
               }
            }
         }
      }
      @media all and (max-width:$G-wMobile)  { 
         .storage-system {
            flex-flow:row wrap;
            .item-area {
               & + .item-area {margin-top: 10px;}
               width: 100%; 
               .item-body {
                  padding: 22px 20px 22px;
                  .desc {padding-right: 0; height: auto;}
                  .info {
                     margin-top: 16px;padding-top: 16px; 
                     li {
                        padding-left: 20px;
                        .icon-system_arrow{
                           top:4px;left:0;
                        }
                     }
                  }
               }
            }
         }
      }

      .storage-navigation {
         .map-area {border: 1px solid #dedede;}
         .navigation-area {
            border: 1px solid #dedede; border-width: 0 1px 1px;
            display: flex; 
            .item-area {
               flex: 1;
               &+.item-area {border-left: 1px solid #dedede;}
               .item-header {
                  background: #f6f6f6; 
                  .title {
                     display: flex; align-content: center; align-items: center;
                     height: 80px;padding: 0 40px;
                     span {display: inline-block; vertical-align: middle; margin-left: 19px;}
                  }
               }  
               .item-body {
                  padding: 33px 40px;
               }
               .navi-info {
                  .key {font-weight: 600;color: $colorB2; margin-bottom: 6px;}
                  .value {color: $colorS; word-break: keep-all; }
                  &.line-style {
                     display: flex;justify-content: flex-start;align-items: flex-start;
                     .key {width: 105px; margin-bottom: 0;}
                     &+.navi-info {margin-top: 6px;}
                  }
                  &+.navi-info {margin-top: 16px;}
                  .subway-color {
                     font-weight: 600; 
                     i {
                        display: inline-block; vertical-align: 1px; width: 20px;height: 20px;line-height: 20px; @include border-radius(10px);color: #fff;text-align: center;font-size: 14px;  margin-right: 2px; 
                     }
                     &.line-1 {
                        color: #263c96;
                        i {background: #263c96;padding-right: 2px;}
                     }
                     &.line-3 {
                        color: #f06e00;
                        i {background: #f06e00;}
                     }
                     &.line-4 {
                        color: #2c9ede;
                        i {background: #2c9ede;padding-right: 2px;}
                     }
                     &.line-5 {
                        color: #8936e0;
                        i {background: #8936e0;padding-right: 1px;}
                     }

                  }
               }
            }
         }
         


      }
      @media all and (max-width:$G-wMobile)  { 
         .storage-navigation {
            .map-area { height: auto; 
               img { width: 100%;}
            }
            .navigation-area {
               display: flex; flex-flow:row wrap; 
               .item-area {
                  flex: none; width: 100%;
                  &+.item-area {border-top: 1px solid #dedede;border-left: 0;}
                  .item-header {
                     .title {
                        height: 54px; text-align: center; justify-content: center;
                        span {margin-left: 10px;}
                     }
                  }  
                  .item-body {
                     padding: 22px 25px;
                  }
                  .navi-info {
                     .key {
                        margin-bottom: 0;
                        &.subway-line {margin-bottom: 12px;}
                     }
                     &.line-style {
                        display: block;
                        .key {width: 100%; margin-bottom: 0;}
                        &+.navi-info {margin-top: 21px;}
                     }
                     &+.navi-info {margin-top: 21px;}
                     .subway-color {
                        i {
                           vertical-align: -1px;margin-right: 2px; 
                        }
                     }
                  }
               }
            }
            


         }
      }
      
   }
