.basis-section{
  /* 마이페이지 */
  .pc-ver { display: inline-block;}
  @media all and (max-width:$G-wMobile) {
    .pc-ver { display: none;}
  }
  
  &.mypage-section { margin-top: 100px;
    .mypage-container {
      display: flex; width: 100%;
      /* LNB */
      // > .aside-area { width: per(280px, 1320px); min-width: 240px; }
      > .aside-area { width: per(280px, 1320px); min-width: 280px; }
      /* Content */
      > .content-area { padding-left: per(80px, 1320px); width: per(1040px, 1320px); }
      // > .content-area { padding-left: per(80px, 1320px);}
    }
    .aside-area {
      .mem-name-wrap { color: $colorB;
        .tt4 { padding-left: 0.2em;}
      }
      .mem-info-wrap {
        display: block;
        margin-top: 5px;
        .mem-lv-box {
          > div { display: inline-block; }
          .mem-lv { padding: 0px 15px; height: 32px; line-height: 32px; letter-spacing: 0; color: #fff;
            &.lv-1 { background-color: $colorL; }
            &.lv-2 { background-color: $colorP; }
          }
          .mem-period { color: $colorP; font-size: 20px; font-weight: 700; margin-left: 10px; }
        }
        .mem-record-box { margin-top: 12px;
          > a { display: inline-block; line-height: 26px; }
          .record-button { text-decoration: underline; font-weight: 500; color: $colorS; }
          .tooltip-button { position: relative; margin-left: 5px;
            > i { vertical-align: middle; }
            .txt-icon-tooltip {
              width: 20px; height: 20px; border-radius: 50%; background: #dedede; font-size: 0; text-align: center;
              &::before { display: inline-block; content: "?"; color: #fff; line-height: 20px; font-size: 16px;}
            }
            &:hover .txt-icon-tooltip { background: $colorP;}
          }
          .tooltip-subbox {
            display: none;
            position: absolute; top: 100%; left: 50%; transform: translateX(-50%); width: 380px; padding-top: 15px;
            z-index: 2;
            .bg-layer {
              background: #333; position: absolute; left: 0; right: 0; top: 15px; bottom: 0; border-radius: 10px; z-index: -1; opacity: 0.95;
              &:before {
                display: block; content: ""; position: absolute; width: 16px; height: 16px; background: #333; border-radius: 5px; top: -5px;
                transform: rotate(45deg) skew(10deg, 11deg); left: calc(50% - 8px);}
            }
            .tooltip-con-inner {color: #fff;padding: 30px;}
          }
          .tooltip-button:hover .tooltip-subbox { display: block; }
          // .tooltip-button.on .tooltip-subbox { display: block; }
        }
      }
      .mem-button-wrap {
        margin-top: 30px;
        .btn { width: 100%;}
      }
      .mypage-lnb-wrap {
        margin-top: 45px;
        .mypage-lnb {
          ul, li { display: block; }
          > ul > li:not(.on), .cate1 > li:not(.on){ color: $colorB2;}
          > ul > li.on { font-weight: 700;}
          li { line-height: 30px; font-size: 20px; font-weight: 700; margin-bottom: 20px; position: relative;
            > a, > span { display: inline-block; padding: 5px 0; }
            > a {
              i { position: absolute; right: 25px; top: 50%; transform: translateY(-50%); }
            }
            li { font-size: 18px; font-weight: 500; margin-bottom: 0; color: $colorS; }
            &.on { font-weight: 600; color: $colorP;
              > a i:after { @include sp_icons($icon-view_more_on); }
            }
            @media (min-width: 1024px) {
              .cate1 > li { font-size: 20px; font-weight: 700; margin-bottom: 20px;}
            }
          }
        }
      }
    }
    .content-area {
      .subtitle-wrap { padding-bottom: 30px;
        .title { color: $colorB;}
      }
      .search-wrap { height: 70px; border-bottom: 1px solid $colorL; position: relative;
        .search-bar { display: flex; justify-content: space-between; margin-top: 10px;
          .date-wrap {
            .select-box { margin-left: -20px;
              select { border: 0; outline: 0; color: $colorB2; font-weight: 600; }
            }
          }
          .search-detail-btnbox {
            .search-detail-btn { margin-top: 5px;
              i { display: block; }
            }
          }
        }
        .search-detail-box {
          display: none;
          border-top: 1px solid #ccc; height: 68px; background: #f6f6f6; padding-left: 30px; position: absolute; top: 0; left: 0; right: 0; z-index: 1;
          .search-detail-inner {
            display: flex; position: relative; top: 50%; transform: translateY(-50%);
            .input-wrap {
              input[type="text"] { width: 200px;}
            }
            .period-wrap { margin-left: 20px;
              dl { display: flex; align-items: center;
                dt { font-size: 18px; font-weight: 700px; padding-right: 10px; }
                dd { display: flex; align-items: center;
                  input[type="text"] { width: 150px; }
                  input[type="text"] + span { padding: 0 5px;}
                }
              }
            }
            .btn-search { margin-left: 10px; }
          }
          .btn_close {
            position: absolute; top: 0px; right: 0px; z-index: 10; width: 70px; height: 70px; padding: 23px; font-size: 0;
          }
        }
      }
    }
    .button-area {
      .btn_lg { min-width: 170px;}
      @media all and (max-width:$G-wMobile) {
        .btn_lg { min-width: auto;}
      }
    }

  }
  @media all and (max-width:$G-wMobile) {
    &.mypage-section {
      margin-top: 0;
      padding: 0;
      .mypage-container { display: block;
        > .aside-area { width: auto; }
        > .content-area { width: auto; padding: 0;
          .subtitle-wrap { background: #f2f2f2; border: 1px solid #dedede; border-width: 1px 0; padding: 15px 25px; text-align: center; position: relative; margin-bottom: 10px;
            .btn-wrap { position: absolute; left: 15px; top: 50%; transform: translateY(-50%); width: 30px; height: 30px; padding: 5px; }
          }
          .contents-wrap { padding: 0 25px;
            .search-wrap { height: auto;
              .search-bar { margin-top: 5px; padding: 5px 0; }
            }
          }
        }
      }
    }
  }
}




.content-panel{

  /* 마이페이지 메인 */  
  &.type_panel-mypage_main {
    @media all and (max-width:$G-wMobile) {
      .mypage-container {
        > .aside-area {
          .mem-infobox-wrap { background: $colorBGw; padding: 37px 25px 25px; }
          .mypage-lnb-wrap { padding: 0 25px; margin-top: 30px;
            .mypage-lnb li > a i { display: none;}
          }
        }
        > .content-area { display: none;}
      }
      .aside-area {
        .mem-infobox-wrap {
          .mem-name-wrap { padding-right: 10px; }
          .mem-info-wrap { margin-top: 6px;
            .tt2 { font-size: 25px; line-height: 1; }
            .mem-lv-box {
              .mem-period { margin-left: 5px; font-size: 15px; }
              .mem-lv { font-size: 13px; }
            }
            .mem-record-box{ margin-top: 8px;
              a { font-size: 15px; }
              .tooltip-subbox {
                width: 290px; left: auto; right: -15px; transform: none;
                .bg-layer {
                  &:before {left: auto;right: 18px;}
                }
                .tooltip-con-inner { padding: 20px; line-height: 1.5; font-size: 13px; word-break: keep-all;}
              }
            }
          }
          &::after { display: block; content: ""; clear: both; }
        }
        .mem-button-wrap { margin-top: 25px; }
        .mypage-lnb-wrap {
          .mypage-lnb {
            li { font-size: 17px; line-height: 40px; margin-bottom: 0;
              li, li.on { font-weight: 600; color: $colorB2;}
              > a, > span { padding: 0 }
              &.cate1-li + .cate1-li { border-top: 0; margin: 0; padding: 0;}
            }
            > ul > li {
              span { font-size: 13px; color: $colorS; font-weight: 500; line-height: 36px;}
              & + li { border-top: 1px solid #dedede; margin-top: 10px; padding-top: 10px;}
            }
          }
        }
      }
    }
  }

  /* 응찰신청내역 */
  .bid-list-wrap {
    .bid-list {
      .bid-item {
        + .bid-item { margin-top: 30px; }
        > dt {
          padding: 30px 0; border-bottom: 1px solid $colorL; display: flex; justify-content: space-between;
          .title-area {
            .title { color: $colorB; @include ellipsis-line(2);
              span { @include ellipsis-line(2); }
            }
            .title.line-1 { @include ellipsis-line(1);
              span { @include ellipsis-line(1); }
            }
            .sub { display: flex;}
            .desc { margin-top: 5px; color: $colorB2; display: flex;
              .tit { padding-right: 12px; margin-right: 10px; position: relative; color: $colorB2;
                &::after { content: ""; display: block; position: absolute; right: 0; top: 50%; transform: translateY(-50%); height: 16px; width: 1px; background: #ccc;}
              }
              .paddle-num { font-weight: 600;}
            }
            .desc + .desc { margin-left: 30px;}
          }
          .btn-area {
            padding-left: 40px;
            .btn { width: 210px; }
          }
        }
        > dd {
          padding: 30px; border-bottom: 1px solid #ccc;
          display: flex;
          .product-infobox {  width: 100%;
            .product-infobox-inner::after { display: block; content: ""; clear: both;}
          }
          .thumb-area { width: 160px; min-width: 160px; min-height: 160px; float: left;
            .img-ratio { background-color: $colorBGw;
              .img-align {padding: 30px;}
            }
            .success {position: absolute;left: 0;top: 0;
              .bid_result-icon { width: 58px; height: 21px; line-height: 21px; font-size: 14px; background-color: $colorP; color: #fff; }
              .bid_result-icon.fail { background-color: $colorL;}
              @media all and (max-width:$G-wMobile) {
                .bid_result-icon { width: 48px; height: 17px; line-height: 17px; font-size: 11px;}
              }
            }
          }
          .text-area {
            padding: 0 40px; color: $colorB2; 
            min-height: 160px; display: flex; flex-direction: column; justify-content: center; float: left; width: calc(100% - 160px);
            > div {
              font-size: 18px;
              > div { display: inline-block; }
            }
            > div + div { margin-top: 10px;}
            .title { color: $colorB2;}
            .desc { margin-top: 5px;}
            .num { font-weight: 600;
              .m-ver { display: none;}
            }
            .titlename { font-weight: 700; font-size: 22px; }
            .year, .sub { font-weight: 600; }
            .price { font-size: 16px;
              .tit { font-weight: 600; margin-right: 15px; }
            }
            .sub-box { margin-top: 25px; font-size: 16px; line-height: 26px;
              .sub-li { display: flex; color: $colorS;
                .tit { position: relative; padding-right: 11px; margin-right: 10px; font-weight: 600; white-space: nowrap;
                  &:after { content: ""; display: block; position: absolute; right: 0; top: 50%; transform: translateY(-50%); height: 16px; width: 1px; background: #ccc;}
                }
                span + span { position: relative; padding-left: 11px; margin-left: 10px;
                  &::before { display: block; content: ""; position: absolute; left: 0; top: 50%; transform: translateY(-50%); height: 16px; width: 1px; background: #ccc;}
                }
              }
            }
          }
  
          .pay-infobox {
            padding-left: 50px; border-left: 1px solid #dedede; display: flex; flex-direction: column; justify-content: space-between;
            .pay-area { color: $colorB2;
              dl { display: flex; justify-content: space-between; font-size: 18px; line-height: 30px;
                dt { font-weight: 600; white-space: nowrap;}
                dd { font-weight: 500; text-align: right;
                  .sub { font-size: 16px; line-height: 24px; font-weight: 500;
                    span { display: block;}
                  }
                }
                &.way { font-size: 16px; line-height: 1.5;}
              }
              dl.price.succ dd { font-weight: 600; color: $colorE; }
            }
            .btn-area { margin-top: 20px;
              .btn {width: 280px;}
            }
            .pay-area + .btn-area { width: 280px; display: flex; justify-content: space-between; flex-direction: column;
              .btn { height: 48px;}
              .btn-half + .btn-half { margin-top: 10px;}
              // .btn-half { width: 135px; padding: 5px; }
              .btn-print {
                span { display: block; line-height: 18px;}
                span + span { font-weight: 500; }
              }
            }
          }
        }
      }
    }
    .button-area { margin-top: 60px;
      .btn { width: 170px; }
    }
    .paging-area { margin-top: 50px;}
    .data-empty { border-top: 1px solid #999; border-bottom: 1px solid #ccc; padding: 300px 0; font-size: 18px; color: $colorD; margin-top: 15px; }

    /* 중간사이즈 */
    @media all and (min-width: $G-wMobile) and (max-width: $G-mdWidth) {
      .bid-list {
        .bid-item {
          > dt {
            .title-area {
              .sub { flex-direction: column;
                .desc + .desc { margin: 0;}
              }
            }
          }
          > dd { padding: 10px 20px; flex-direction: column;
            .product-infobox, .pay-infobox { padding: 30px 0;}
            .product-infobox-inner { display: flex;}
            .thumb-area { /* width: 270px; min-width: 270px; */ float: none;
              // .img-ratio .img-align { padding: 85px;}
            }
            .text-area { padding: 0 0 0 25px; display: flex; flex-direction: column; justify-content: center; float: none; width: auto;
              .num .m-ver { display: inline-block; }
              .pc-ver { display: none; }
              .sub-box {
                .sub-li { 
                  .tit { position: static; padding-right: 0; margin-right: 0; font-weight: 600;
                    &::after { display: none; }
                  }
                }
              }
            }
            .pay-infobox { border-left: 0; border-top: 1px solid #dedede;

              .btn-area { margin-top: 30px;
                .btn { width: 100%; }
              }
              .pay-area + .btn-area { width: auto;
                .btn-half { width: 100%; }
              }
            }
          }
        }
      }
    }
    
    /* 모바일 */
    @media all and (max-width:$G-wMobile) {
      .bid-list {
        .bid-item {
          display: block;
          + .bid-item { margin-top: 25px; }
          > dt { flex-direction: column;
            .title-area {
              .sub { flex-direction: column;
                .desc + .desc { margin: 0;}
                .tit::after { height: 14px;}
              }
            }
            .btn-area { padding: 0; margin-top: 20px; }
          }
          > dd {
            padding: 5px 10px; flex-direction: column;
            .product-infobox, .pay-infobox { padding: 20px 0;}
            .product-infobox-inner { display: flex;}
            .thumb-area { width: 130px; min-width: 130px; min-height: 130px; float: none;
              .img-ratio {
                .img-align { padding: 15px;}
              }
            }
            .text-area { padding: 0 0 0 25px; min-height: 130px; float: none; width: auto;
              > div { font-size: 14px; }
              .titlename { font-size: 16px; }
              .price { font-size: 13px;
                .tit { font-size: 13px;}
              }
              .num .m-ver { display: inline-block; }
              .pc-ver { display: none; }
              .sub-box { margin-top: 15px; font-size: 13px; line-height: 1.5;
                .sub-li { 
                  .tit { position: static; padding-right: 0; margin-right: 0; font-weight: 600;
                    &::after { display: none; }
                  }
                  span + span { padding-left: 6px; margin-left: 7px;}
                }
              }
            }
            .pay-infobox { border-left: 0; border-top: 1px solid #dedede;
              .pay-area{
                dl { font-size: 14px; line-height: 24px;
                  &.way { font-size: 13px; line-height: 1.5;}
                  dd { .sub { font-size: 13px; line-height: 1.5; margin: 3px 0;}}
                }
              }
              .btn-area { margin-top: 14px;
                .btn { width: 100%; }
              }
              .pay-area + .btn-area { width: auto;
                .btn-half { width: 100%; }
              }
            }
          }
        }
      }
      .bid-list-wrap {
        .button-area { margin-top: 30px;
          .btn { width: 160px; }
        }
        .paging-area { margin-top: 30px;}
      }
    }
  }

  /* 관심작품 내역 */
  .favorite-list-wrap {
    .favorite-list {
      .bid-item {
        > dd { padding: 30px 20px; position: relative;
          .product-infobox { position: static;
            .btn-heart { position: absolute; top: 30px; right: 20px;
              &.on i { &::after { @include sp_icons2x($icon-heart_on-2x) } }
            }
          }
          .text-area { padding-right: 0; justify-content: flex-start;}
          .pay-infobox { padding-left: 0; border-left: 0; justify-content: flex-end;
            .pay-area { position: static; transform: none;
              dl { display: block;
                dt, dd { text-align: right; }
                dd { white-space: nowrap;}
              }
            }
          }
        }
      }
    }

    @media all and (max-width: $G-mdWidth) {
      .favorite-list {
        .bid-item {
          > dd { padding: 25px 10px;
            .product-infobox, .pay-infobox { padding: 0;}
            .product-infobox {
              .btn-heart { top: 20px; right: 5px;}
            }
            .pay-infobox { border-top: 0; margin-top: 20px;}
          }
        }
      }
    }
  }

  /* help-box */
  .help-box-wrap {
    padding: 10px 0 30px; color: $colorS;
    .help-box {display: flex; font-size: 18px; line-height: 30px; width: 100%;
      i { margin-top: 5px; margin-right: 10px;}
      li { font-weight: 500;}
    }
    @media all and (max-width:$G-wMobile) {
      & { padding: 30px 0 0; }
      .help-box { font-size: 14px; line-height: 1.5;
        i { margin-top: 3px; margin-right: 5px;}
        &.tel { position: relative; 
          ul { width: 100%; li {display: flex;}}
          span { display: inline-block; }
          span+span { margin-left: 5px;}
          
        } 
      }
    } 
   
   /*  .help-box { position: relative; padding-left: 30px;
      i { position: absolute; top: 5px;left: 0;}
      ul { font-size: 18px; line-height: 32px;}
    }
    @media all and (max-width:$G-wMobile) {
      & {.help-box { padding-left: 20px;
          ul { font-size: 14px; line-height: 1.5; 
            li { display: flex; }
          }
          span { display: inline-block; }
        }
      }
    } */
  }


  /* 라이브경매 > 온라인패들응찰내역,
  응찰신청내역 디테일(버튼있음) */
  &.type_panel-mypage_auction {
    .auction-detail {
      .bid-list {
        .bid-item {
          dt {
            display: block;
            .title-area {
              display: flex; justify-content: space-between; align-items: center;
              position: relative; padding-bottom: 40px;
              .title { color: $colorB;}
              .desc {
                position: absolute; bottom: 0; left: 0;
              }
              .btn-area {
                padding-left: 40px;
                .btn { width: 170px; }
              }
            }
          }
          dd {
            .text-area { color: $colorB2;}
            .btn-area {
              padding-left: 40px;
              .btn { width: 140px;}
            }
          }
        }
      }
    }

    @media all and (max-width:$G-wMobile) {
      .aside-area { display: none;}
      
      .auction-detail {
        .bid-list {
          .bid-item {
            dt {
              display: block;
              .title-area {
                display: block; position: static; padding-bottom: 0;
                .desc { position: static;}
                .btn-area { padding: 0; margin-top: 20px;
                  .btn { width: 160px; }
                }
              }
            }
            dd {
              flex-wrap: wrap; padding: 25px 0;
              .text-area { width: calc(100% - 140px); }
              .btn-area {
                width: 100%; padding-left: 0; padding-top: 25px;
                .btn { width: 100%; }
              }
            }
          }
        }
      }
      .bid-list-wrap .data-empty { padding: 65px 0; font-size: 14px; }
    }
  }

  /* 온라인경매 > 응찰내역 */
  &.type_panel-mypage_auction.online {
    .tabmenu-wrap { padding-bottom: 15px; }
    
    @media all and (max-width:$G-wMobile) {}
  }

  /* 온라인경매 > 결제/구매내역 */
  &.type_panel-mypage_auction.online-buy {
    .textlist-wrap {
      background-color: $colorBGw; padding: 20px 30px; margin-top: 20px;
    }
    .list_count-wrap {
      margin-top: 25px; margin-bottom: 10px;
      .count-area {
        > div { display: inline-block;}
        .tit { font-size: 20px; color: $colorB; font-weight: 600;}
        .sub { font-size: 18px; }
        .point { color: $colorP;}
      }
    }
    .bid-list .bid-item dd.item-ea {
      flex-direction: column;
      .item-ea-tit { display: flex; margin-bottom: 30px; font-size: 20px; line-height: 24px;
        .paystate { font-weight: 600;
          &.pending { color: $colorP; }
          &.complete { color: $colorB2; }
          &.cancel { color: #b2b2b2; }
        }
        .txt { font-weight: 500; position: relative; padding-left: 11px; margin-left: 11px; }
        .txt:before { content: ""; display: block; position: absolute; left: 0; top: 50%; transform: translateY(-50%); height: 16px; width: 1px; background: #ccc;}
      }
      .item-ea-inner {
        display: flex;
        .product-infobox-inner {
          display: flex;
        }
      }
    }
    .auction-detail .bid-list .bid-item dd {
      .btn-area {
        // width: 280px; text-align: right;
        .btn { width: 140px;}
        .btn-buy { width: 240px;}
      }
    }

    @media all and (min-width: $G-wMobile) and (max-width: $G-mdWidth) {
      .bid-list .bid-item dd.item-ea {
        .item-ea-inner {
          flex-direction: column;
        }
        .item-ea-tit {
          margin-bottom: 0; margin-top: 20px; flex-direction: column;
          .txt { position: static; margin-left: 0; padding-left: 0; margin-top: 6px; }
        }
      }
    }

    @media all and (max-width:$G-wMobile) {
      .textlist-wrap { padding: 20px; margin-top: 25px;}
      .list_count-wrap { margin-bottom: 0;
        .count-area {
          > div { display: block; }
          .tit { font-size: 15px; }
          .sub { font-size: 14px; }
        }
      }
      .bid-list .bid-item dd.item-ea {
        .item-ea-inner {
          flex-direction: column;
        }
        .item-ea-tit {
          margin-bottom: 0; margin-top: 20px; font-size: 15px; line-height: 17px; flex-direction: column;
          .paystate { font-size: 15px; }
          .txt { position: static; margin-left: 0; padding-left: 0; margin-top: 6px; }
        }
      }
      .auction-detail .bid-list .bid-item dd {
        .text-area { width: calc(100% - 160px);}
        .btn-area { width: 100%;
          .btn { width: 100%;}
        }
      }
    }
  }

  /* 관심작품 */
  &.type_panel-mypage_favorite {
    .contents-wrap.part + .tabmenu-wrap { margin-top: 6px; }
    .tabmenu-wrap + .contents-wrap { padding-top: 10px; }
    @media all and (max-width:$G-wMobile) {
      .aside-area { display: none;}
    }
  }

  /* 아카데미신청내역 */
  &.type_panel-mypage_academy {
    .academy-list-wrap { padding-top: 30px; }
    .academy-list { table-layout: auto;
      th, td { border-left: 0; border-right: 0; padding: 25px;}
      td { font-size: 18px; color: $colorB2;
        &:first-child { text-align: left; font-size: 20px; font-weight: 600; color: $colorB;}
      }
      .bbs-subject { font-size: 20px; font-weight: 600; color: $colorB;}
      .pay { font-weight: 600;
        &.complete { color: $colorP; }
        &.cancel { color: $colorL;}
      }
    }
    .paging-area { margin-top: 60px;}
    @media all and (max-width:$G-wMobile) {
      .aside-area { display: none;}
      .academy-list { border-top: 1px solid $colorL;
        .academy-list-ea {
          padding: 20px 0; border-bottom: 1px solid #ccc; font-size: 14px; line-height: 1.5;
          .bbs-subject { font-size: 15px;}
          .cont-wrap { margin-top: 16px;
            dl {
              display: flex; justify-content: space-between;
              dt { font-weight: 600;}
            }
            dl + dl { margin-top: 6px;}
          }
        }
      }
    }
  }

  /* 1:1문의 */
  &.type_panel-mypage_inquiry {
    .tit-textbox { margin-bottom: 60px;}
    .form_table-list {
      li {
        .half { width: calc(50% - 5px);}
        .trp.file-box { width: 170px; }
        .dot_list {
          li { color: $colorS;}
        }
      }
    }
    .button-area { margin-top: 60px;
      .btn + .btn { margin-left: 5px;}
    }
    @media all and (max-width:$G-wMobile) {
      .aside-area { display: none;}
      .tit-textbox { margin: 30px 0 25px;}
      .form_table-list {
        li {
          .half { width: 100%;}
        }
      }
      .button-area { margin-top: 30px;
        .btn { width: calc(50% - 5px);}
      }
    }

    /* 목록 */
    .tit-btnbox { position: absolute; top: 0; right: 0;
      .btn { width: 170px; }
    }
    .inquiry-list-wrap { border-top: 1px solid $colorL;
      .button-area { margin-top: 40px;
        .btn { width: 160px; }
      }
    }
    .inquiry-list {
      > li { border-bottom: 1px solid #ccc; padding: 30px 20px;
        .bbs-division { font-size: 18px;}
        .bbs-subject { color: $colorB2; margin-top: 15px; @include ellipsis-line(2); 
          span {@include ellipsis-line(2); word-break: keep-all;}
        }
        .bbs-subbox { margin-top: 20px; display: flex; align-items: center;
          .statebox { margin-right: 16px;
            .state-icon { font-size: 14px; font-weight: 700; width: 80px; height: 21px; line-height: 20px; text-align: center;
              &.complete { border: 1px solid $colorP; color: $colorP; }
              &.pending { background: $colorBGd; color: $colorS;}
            }
          }
        }
      }
    }
    @media all and (max-width:$G-wMobile) {
      .tit-btnbox { position: static; margin: 30px 0 25px;
        .btn { width: 100%; }
      }
      .inquiry-list-wrap {
        .button-area { margin-top: 30px;}
      }
      .inquiry-list {
        > li { padding: 20px 0;
          .bbs-division { font-size: 14px;}
          .bbs-subject { margin-top: 5px;}
          .bbs-subbox { margin-top: 10px; display: flex;
            .statebox { margin-right: 10px;
              .state-icon { font-size: 11px; width: 55px; height: 15px; line-height: 15px;
                &.complete { line-height: 13px;}
              }
            }
          }
        }
      }
    }

    /* 상세보기 */
    .view-header {
      border-top: 1px solid #999999; background-color: #f6f6f6; padding: 0 40px;
      .title-area { padding: 40px 0;
        .title { color: $colorB2; }
      }
      .sub-area { border-top: 1px solid #ccc; padding: 30px 0; display: flex; flex-wrap: wrap;
        dl { margin-right: 45px; display: flex;
          dt { font-weight: 600; padding-right: 5px; color: $colorB2;}
        }
      }
    }
    .view-body { padding: 40px ;
      &.inquiry-view-wrap {
        dl {
          dt.tit { color: $colorB2; margin-bottom: 25px;}
          dd { border: 1px solid #ccc; padding: 20px 30px;
            .con-area { padding: 10px 0;}
            .file-area { border-top: 1px solid #ccc; padding-top: 20px; margin-top: 10px;}
          }
          dl.reply { margin-top: 40px;
            i { margin-right: 5px; vertical-align: text-top;}
          }
        }
        .file-item { display: inline-block; margin-right: 20px; cursor: pointer;
          > i { vertical-align: sub; }
          > i + span { margin-left: 5px;}
        }
      }
    }
    .view-footer { border-top: 1px solid #ccc; margin-top: 10px;
      .button-area { margin-top: 60px;
        .btn { width: 170px;}
      }
    }
    @media all and (max-width:$G-wMobile) {
      .view-header { padding: 0 20px; margin-top: 40px;
        .title-area { padding: 25px 0;}
        .sub-area { flex-direction: column; padding: 20px 0; }
      }
      .view-body { padding: 20px;
        &.inquiry-view-wrap {
          dl {
            dt.tit { margin-bottom: 15px;}
            dd { padding: 20px;
              .con-area { padding: 0;}
              .file-area { margin-top: 20px; padding-top: 20px;}
            }
          }
        }
      }
      .view-footer { margin-top: 5px;
        .button-area { margin-top: 30px;
          .btn { width: 160px;}
        }
      }
    }
  }

  /* 회원정보수정 */
  &.type_panel-mypage_myinfo {
    .tit-textbox { margin-bottom: 60px;}
    .form_table-list {
      input[type="text"], input[type="email"], input[type="tel"], input[type="password"] { width: 455px;}
      .error_text, .guide_text { margin-top: 7px; }
    }
    .button-area { margin-top: 60px;
      .btn + .btn { margin-left: 5px;}
    }
    /* 회원탈퇴안내 */
    .mem-infobox-wrap { margin-bottom: 60px;
      .mem-infobox { border-top: #333 1px solid; border-bottom: #ccc 1px solid; padding: 40px 0;
        .tit { margin-bottom: 20px;}
      }
      .agr-checkbox { padding-top: 30px;}
    }
    
    @media all and (max-width:$G-wMobile) {
      .aside-area { display: none;}
      .tit-textbox { margin-bottom: 30px; padding-top: 30px;}
      .form_table-list {
        input[type="text"], input[type="email"], input[type="tel"], input[type="password"] { width: 100%;}
      }
      .button-area { margin-top: 30px;
        .btn { width: calc(50% - 5px);}
      }
      /* 회원탈퇴안내 */
      .mem-infobox-wrap { margin-bottom: 25px; padding-top: 30px;
        .mem-infobox { padding: 25px 0;}
        .agr-checkbox { padding-top: 25px;
          .trp.checkbox-box label { line-height: 1.5;}
        }
      }
    }
  }

  
  /* SNS 연동 설정 */
  &.type_panel-mypage_sns {
    .tit-textbox { margin-bottom: 60px;
      & + .help-box-wrap { padding-top: 0;}
    }
    .sns-mem-wrap {
      ul { display: flex; flex-wrap: wrap; justify-content: space-between;
        li { width: calc(50% - 10px); margin: 10px 0; padding: 40px; background: #f2f2f2; display: flex; justify-content: space-between; align-items: center;
          &.on { background: #e9f4f3;}
          .sns-wrap { display: flex; align-items: center;
            .sns-logo { width: 68px; height: 68px; background: #fff; border-radius: 50%; position: relative; margin-right: 26px;
              i { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);}
            }
            .sns-txt {
              .tit { color: $colorB2;}
              .disc { color: $colorL;}
            }
          }
          .btn-wrap {
            .btn { width: 112px}
            .btn.btn_gray_line:not(:hover) { color: #777;
              span { color: #777;}
            }
          }
        }
      }
    }

    @media all and (max-width:$G-wMobile) {
      .aside-area { display: none;}
      .tit-textbox { margin: 30px 0 25px;}
      .sns-mem-wrap {
        ul { flex-direction: column;
          li { width: 100%; margin: 20px 0 0; padding: 20px;
            .sns-wrap {
              .sns-logo { width: 50px; height: 50px; margin-right: 15px;}
            }
            .btn-wrap {
              .btn {width: 75px; padding: 2px;}
            }
          }
        }
      }
    }
  }
  
  /* 회원정보수정 */
  &.type_panel-mypage_memberModify{
    .contents-wrap{ margin-top: 25px;
      .required{ position: absolute; right: 0; top: 63px;
        span{ font-weight: 500;
          i{color: $colorE;}}
      }
      .error_text{ margin-top: 7px;}
      .form_list{ border-top: 1px solid #a0a0a0;
        >li{ border-bottom: 1px solid #c9c9c9; display: flex;
          .form_label{ display: flex; align-items: center; padding: 10px 0 10px 20px; width: 165px; height: 70px; 
            .label_text{ 
              font-size: 20px; font-weight: 700; color: $colorB2;
            }
            i{ color: $colorE; font-size: 20px; font-weight: 700; padding-left: 5px;}
          }
          .form_body{ display: flex; align-items: center; padding: 10px 0 10px 20px; width: 475px;
            /* 세로확장 */
            &.vertical_item{ display: block;
              .input-group + .input-group{ margin-top: 10px;}
            }
            /* 가로확장 & 아이템 아래로 떨어짐 */
            &.widthExtend{ width: calc( 100% - 165px ); display: block; padding: 15px 0 15px 20px;
              .trp.checkbox-box + .trp.checkbox-box { margin-left: 0;}
              .trp.checkbox-box { margin-right: 35px; margin-top: 8px;}
              .input-group{ max-width: 455px;}
              .writer_list{ 
                li{ display: inline-block; background-color: #efefef; border-radius: 20px; padding: 6px 13px 6px 15px; font-weight: 500; margin-top: 10px; margin-right: 5px;
                  .icon-filter_del{ margin-top: 3px; margin-left: 2px;}
                }
              }
            }
            .mem_text{ font-size: 18px; font-weight: 500;}
            .sns_icon-wrap{ display: flex; align-items: center;
              .sns_icon{ width: 35px; vertical-align: middle; margin-left: 25px;}
            }
            .select-box{ width: calc( 100% / 3 );
              &.country_box{ width: 100%;}
            }
            .select-box + .select-box { margin-left: 10px;}
            .address_text{ margin: 12px 0 5px;}
            .address_text2{ margin: 10px 0 0px;}
            .trp.checkbox-box + .trp.checkbox-box { margin-left: 35px;}
          }
        }
      }
      .dropOut_text{ font-size: 18px; color: $colorB2; font-weight: 500; padding: 30px 0 10px 20px; text-decoration: underline;}
      .btn_area{ text-align: center;
        .btn{ width: 170px;}
      }
    }

    @media all and (max-width:$G-wMobile) {
      .aside-area { display: none;}
      .contents-wrap{ margin-top: 75px;
        .required{ position: absolute; right: 23px; top: 95px;
          span{ font-size: 13px; }
        }
        .form_list{ border-bottom: 1px solid #ccc; padding-bottom: 25px;
          >li{  display: block; border-bottom: 0px;
            .form_label{ display: block; padding: 0; width: 100%; height: 100%; margin-top: 20px;
              &.name_space{ margin-top: 13px;}
              .label_text{ font-size: 15px;}
              i{ font-size: 15px;  padding-left: 3px;}
            }
            .form_body{ display: block; padding: 0; width: 100%; margin-top: 10px;
              &.id_space{ margin-top: 5px;}
              &.select-box_wrap{ display: flex;
                .select-box:nth-child(1){ width: 40%;}
                .select-box:nth-child(2){ width: 30%;}
                .select-box:nth-child(3){ width: 30%;}
              }
              /* 가로확장 */
              &.vertical_item{ 
                .input-group + .input-group{ margin-top: 10px;}
              }
              /* 가로확장 & 아이템 아래로 떨어짐 */
              &.widthExtend{ width: 100%; display: block; padding: 0;
                &.box_Over{ margin-top: 0;}
                .trp.checkbox-box + .trp.checkbox-box { margin-left: 0;}
                .trp.checkbox-box { margin-right: 18px; margin-top: 10px;}
                .input-group{ width: 100%;  max-width: 100%;}
                .writer_list{ margin-top: 5px;
                  li{ padding: 5px 13px 5px 15px;  margin-top: 10px; margin-right: 5px; font-size: 14px;
                    .icon-filter_del{ margin-top: 3px; margin-left: 2px;}
                  }
                }
              }
              .mem_text{ font-size: 14px;}
              .sns_icon-wrap{ 
                .sns_icon{ width: 29px;  margin-left: 15px;}
              }
              .select-box{ width: 100%;}
              .select-box + .select-box { margin-left: 5px;}
              .address_text2{ margin: 0;}
              .trp.checkbox-box + .trp.checkbox-box { margin-left: 18px;}
            }
          }
        }
        .dropOut_text{ font-size: 14px;  padding: 15px 0 20px 0px; }
      }
    }
  }
  
}