  /* 응찰하기(오프라인) */
  .bidding_offline-section { padding-bottom: 120px; margin-top: 36px;
    /* 비회원 */
    &.type-non_member {
      .section-header{ width: 630px; margin: 0 auto;}
      .section-body{
        .bidding-offline-wrap { 
          .bidding-offline-center { position: relative; width: 630px; padding-right: 0px; margin: 0 auto; }
        }
      }
    }
  
  
    .mCSB_scrollTools { width:4px;}
    .mCSB_outside+.mCS-minimal-dark.mCSB_scrollTools_vertical, .mCSB_outside+.mCS-minimal.mCSB_scrollTools_vertical { margin:0;}
  
    .section-header { display: flex; justify-content: space-between;
      .title-area {
        .title { font-size: 28px; font-weight: 600; color:$colorB; line-height: 36px; }
      }
      .btn-area {
        .select-box {
          select {  padding: 8px 36px 8px 15px; border: 0;
            font-size: 14px; font-weight: 500; color:$colorD; line-height: 20px;
            background-color: #f3f3f3;
          }
          i { position: absolute; top: 9px; right: 12px; width: 20px; height: 20px;
            pointer-events: none; background-color: transparent;
          }
        }
     
  
      }
    }
    .section-body   { margin-top: 30px;}
    .section-footer { margin-top: 60px; 
      .btn { width: 170px;}
    }
    @media all and (max-width:$G-wMobile)  { margin-top: 0; padding: 23px 0 60px;
      .section-header { display: block; padding: 0 25px;
        .title-area { height: 55px; 
          @include before-middle();
          .title { display: inline-block;
            font-size: 18px; font-weight: 600; color:$colorB; line-height: 1.36; vertical-align: middle; 
          }
        }
        .btn-area {
          .select-box {
            select {  padding: 8px 36px 8px 15px; border: 0;
              font-size: 14px; font-weight: 500; color:$colorD; line-height: 20px;
              background-color: #f3f3f3;
            }
            i { position: absolute; top: 9px; right: 12px; width: 20px; height: 20px;
              pointer-events: none; background-color: transparent;
            }
          }
        }
      }
      .section-body   { margin-top: 0px;}
      .section-footer   { margin-top: 30px;
        .btn { width: 160px;}
      }
    }
  }
  
  
  
  .bidding_pc {
    
    /* .section { position: relative; padding: 20px 50px 60px; } */
    .bidding-offline-wrap { display: flex;  overflow: hidden;
      >div { position: relative;  }
      .bidding-offline-left   { width: per(350px, 1360px); }
      .bidding-offline-center { position: relative; width: per(660px, 1360px); 
        padding-left: per(30px, 1360px);  padding-right: per(30px, 1360px); 
      }
      .bidding-offline-right  { width: per(350px, 1360px); }
    }
    .lot-view { 
      .lot-view_top {border:1px solid #ccc; padding-top:21px; padding-bottom:20px;}
      .view-img_wrap { 
        width:150px; margin:0 auto ; 
      }
    }
    .lotlist-tabmenu {  padding:0px 15px 0; border-bottom:1px solid #ccc;
      .btn_item { display:inline-block; margin-right:32px;
        &:last-child {margin-right:0;}
        .lot-btn_tabmenu { font-size:18px; font-weight: 500; color:$colorS; padding-bottom:10px; position:relative;
          
          &.on { color:$colorMain; font-weight: 700;
            &:after {content:''; position:absolute; z-index: 1; bottom:-1px; width:100%; height:3px; background:$colorMain; left:0; }
          }
        }
      }
    }
  
    .bidding_situation {
      margin-top:20px; background-color:#f3f3f3;
      padding-top:20px; 
      .alert_wrap { padding: 0 20px;
        .situ_alert { text-align: center; padding:15px;
          background:#fff; color:#db1b1b; font-size:16px; font-weight: 700;
        }
      }
      .mCustomScrollbar { height:202px}
      .situation_list { padding:15px 0;
        .st_item {
          padding: 12px 40px; display:flex; font-weight: 700; font-size:16px;
          .txt { width:50%; font-weight: 600; color:$colorB2;}
          .price { width:50%; text-align: right; font-weight: 700; color:$colorB2;}
          &:first-child .price{color:#db1b1b; }
        }
      }
    }
    .biddingprice_wrap { 
      .now_price { border-left:1px solid #ccc; border-right:1px solid #ccc; text-align: center; padding: 10px; color:#111; position: relative;
        display: flex; flex-direction: column;justify-content: center; min-height: 80px;
        .txt {font-size:18px; font-weight: 600;}
        .price_unit1 {font-size:14px;font-weight: 600; line-height: 20px;}
        .price_unit2 {font-size:12px;font-weight: 500; }
        .sel_unit { position:absolute; right:18px; top:50%; margin-top:-17px;
          select { background:#f3f3f3; border:none; font-size:14px; color:#555; line-height: 20px; padding:8px 11px; font-weight: 500;}
        }
      }
      .bid_price { background:$colorMain; text-align: center;  color:#fff; position: relative;
        display: flex; flex-direction: column;justify-content: center; min-height: 80px;
        .btn_bid { display:block; width:100%; padding: 10px; min-height: 80px;
          &:hover { background-color: #089B93; }
          &:disabled,  &:disabled:hover  {  background:#ccc; cursor: default; } 
        }
        &.view_only {  background:#ccc;  cursor: default;
          .btn_bid {
            .txt {font-size:18px; font-weight: 600; }
          }
        }
        .txt {font-size:18px; font-weight: 600;}
        .price_unit1 {font-size:14px;font-weight: 600; line-height: 20px;}
        .price_unit2 {font-size:12px;font-weight: 500; }
        .sel_unit { position:absolute; right:18px; top:50%; margin-top:-17px;
          select { background:#f3f3f3; border:none; font-size:14px; color:#555; line-height: 20px; padding:8px 11px; font-weight: 500;}
        }
      }
    }
    .lotlist-wrap { 
      .lotlist-header {border:1px solid #ccc; border-bottom:none;
        .header_top { margin-bottom:28px; padding: 15px 15px 0px 15px;  position:relative;}
        .btn_movelot { font-size:16px; color:#333; font-weight: 600; position:relative;  display:inline-block;
          &:after {content:''; position:absolute; bottom:0; left:0; width:100%; height:2px; background:#333;}
        }
        .totalcount { color:#333; font-size:16px; font-weight: 600; 
          .num {color:$colorP;}
        }
      }
      .lotlist-tabCont { 
        .mCustomScrollbar, 
        .scroll-type   { height:715px;border-bottom:1px solid #ccc; }
        .lotlist-box { border-left:1px solid #ccc; border-right:1px solid #ccc;
          .lotlist-inner { 
            .lotitem { position:relative;
            
              .lotitem_wrap{ padding: 20px 10px 20px 110px; }
              .btn-lotChk { font-size:0; 
                position:absolute; top: 23px; right:25px; z-index: 2;
                width: 16px; height: 16px;
                &:after {
                  content:''; position:absolute; top:0; left:0; @include sp_icons($icon-favorite0);
                }
                &.on:after {
                  @include sp_icons($icon-favorite1);
                }
              }
              .view-img { position: absolute; left:10px; top: 20px; width: 80px; height: 80px; font-size: 0; line-height: 0;
                  text-align: center; background:#ededed; 
                .img-box { position: absolute; vertical-align: middle; left: 0; right: 0; width: auto; height: 100%; text-align: center; font-size: 0; overflow: hidden;  
                  .box-inner { position: relative; display: inline-block; width: auto; height: 100%; vertical-align: middle; text-align: center; font-size: 0;
                    &::before { content: ''; height: 100%; width: 0; display: inline-block; vertical-align: middle; font-size: 0;}
                  }
                }
                img { display: inline-block; max-width: 100%; max-height: 100%; vertical-align: middle; height: auto; width: auto; }
              }
              .item-cont { position:relative;
                .num {
                  .snum {font-size:12px; color:#333; font-weight: 600;}
                  .live_box { position:relative;margin-left:10px; font-size:12px; color:#fff; background:#db1b1b; font-weight:500;padding:3px 6px 3px 16px;
                    &:before {content:''; background:#fff; width:4px; height:4px; border-radius: 2px; position:absolute; top:8px; left:6px;} 
                  }
                }
                .title {
                  font-size:18px; font-weight: 600; color:#333; margin-top:6px;
                }
                .year  { font-size:14px; font-weight: 500; color:#333; margin-top:3px;}
                .desc { font-size:14px; font-weight: 500; color:#333; margin-top:3px;}
                .price { font-size:14px; color:#333;  margin-top:3px; font-weight: 600;}
              }
  
              &.active {
                .lotitem_wrap{ 
                  &::after{ content: ""; display: block;
                    position: absolute; 
                    top:-1px; left: -1px; right: -1px; bottom: -1px;
                    border: 2px solid $colorMain;
                  }
                } 
              }
  
  
              &.on {
                &:before {content:''; width:100%; background: $colorMain;  height:1px; position:absolute; top:-1px; left:0;}
                &:after {content:''; width:100%; background: $colorMain;  height:1px; position:absolute; bottom:-1px; left:0;}
                .lotitem_wrap{
                  &:before {content:''; width:1px; background: $colorMain;  height:100%; position:absolute; top:0px; left:-1px;}
                  &:after {content:''; width:1px; background: $colorMain;  height:100%; position:absolute; top:0px; right:-1px;}
                }
              }
  
              &.bidded {
                .item-cont .price {color:#db1b1b;}
              }
              &.cancel {
                background:#f6f6f6; padding:45px 0;
                .txt {text-align: center; font-size:16px; font-weight: 500; color:#777777;}
              }
              border-bottom:1px solid #ccc;
              &:last-child { border-bottom:none;}
            }
          }
        }
      }
    }
  
    .notice{  background:#f3f3f3; padding:15px 24px 15px 60px; position:relative; font-size: 0;
      .icon-notice { display:inline-block; position:absolute;top:15px; left:24px; }
      .txt { font-size:16px; color:#333; font-weight: 500; line-height: 20px; }
    }
  
  
    .video_wrap {display:block; position: relative;
  
        .video_thumbnail_wrap {display:block; overflow:hidden; padding-bottom:56.25%; position:relative;
          font-size: 0; line-height: 0;
          .video_thumbnail {position:absolute; top:0; left:0; width:100%; height:100%;  z-index: 3;
            
            background-size:cover; transition: .3s all; transform: scale(1.01);}
          iframe { position:absolute; top:0; left:0; width:100%; height:100%;  z-index: 2; }
          img { position:absolute; top:0; left:0; width:100%; height:100%;  z-index: 3; }
        }
        &:hover {
          .video_thumbnail_wrap {
            .video_thumbnail { transform: scale(1.05);}
          }
        }
        .ic_video_play {
          @include sp_icons($video_play);
          position:absolute; top:50%; left:50%; margin-left:-30px; margin-top:-30px;  z-index: 5;
        }
    }
    .view_info { margin-top:20px; margin-bottom:65px; position:relative;
      .view_count {font-size:14px; font-weight: 500;
        .unit, .num,  .icon-viewer { vertical-align: middle;}
        .unit {}
        .num {font-weight: 700;}
        .icon-viewer { margin-right:10px; }
      }
      .btn.btn_refresh { background:#f3f3f3; border:none;
        font-size:14px; font-weight: 500; padding:16px; padding-left:30px; position:absolute; right:0; top:-5px;
        .ic_refresh {display:inline-block;@include sp_icons($icon-refresh); position:absolute; top:9px; left:12px;}
      }
    }
  
    .my_lot {
      .my_lot_info { position:relative;
        .count { position:absolute; top:0px; right:0; text-align: right; font-size:16px; color:#333; font-weight: 500;
          .txt {font-weight: 700;}
          .num { margin-left: 5px; }
        } 
      }
      .my_lot_wrap {
        .scroll-inner{ padding:10px 0;}
        .table_base.my_lot_table {
          tr{
            th:first-child, td:first-child { width: 16%; }
            th:last-child , td:last-child { width: 12%; }
          }
          th {
            height:50px; background:#f3f3f3; 
            font-size: 16px;
          }
          td {
            height:38px; font-weight: 500;
            font-size: 16px;
            .lot-stat{color:$colorMain; font-weight: 700;}
          }
        }
        .my_lot_body .mCustomScrollbar { height:247px;}
  
        .my_lot_type1 {  border:1px solid #ccc; border-top:none;
          position:relative; padding:30px; /* padding-left: per( 270px , 640px); */ height:299px;
           display: flex; justify-content: center; align-items: center;
          .lot_link { position: absolute; top:18px; right:20px;
            font-size: 16px; color: $colorB2; font-weight: 600; text-decoration: underline;  }
          .view-img_wrap {
            /* position:absolute; top:60px; left:per( 75px , 640px); */
            .view-img { position: relative; width: 165px; height: 165px; font-size: 0; line-height: 0;
              text-align: center;
              .img-box { position: absolute; vertical-align: middle; left: 0; right: 0; width: auto; height: 100%; text-align: center; font-size: 0; overflow: hidden;  
                .box-inner { position: relative; display: inline-block; width: auto; height: 100%; vertical-align: middle; text-align: center; font-size: 0;
                  &::before { content: ''; height: 100%; width: 0; display: inline-block; vertical-align: middle; font-size: 0;}
                }
              }
              img { display: inline-block; max-width: 100%; max-height: 100%; vertical-align: middle; height: auto; width: auto; }
            }
          }
          .type2_info_box { padding-left: 30px;
            display: flex; align-items: center; height:100%; 
            .num {
              font-size:20px; font-weight: 700; color:$colorB2
            }
            .title {
              font-size:20px; font-weight: 700; margin-top:10px; color:$colorB2
            }
            .year {
              font-size:16px; font-weight: 700; color:$colorB2; margin-top:5px;
            }
            .desc {
              font-size:16px; font-weight: 500; color:#777; margin-top:5px;
            }
            .standard { margin-top: 10px; font-size: 16px; font-weight: 500; color:$colorS; 
              span { line-height: 1.36; }
              .size_year { 
                span { position: relative; display: inline-block;  }
                span + span { margin-left: 5px; padding-left: 6px; @include list-div_bar(5px, 0, 11px, #777); }
              }
            }
            .price {
              font-size:16px; font-weight: 600; color:#777;  margin-top:10px; 
            }
            .detail .btn { margin-top:10px; background:#f3f3f3; border:none;
              font-size:14px; font-weight: 500; padding:17px; color:#777; font-weight: 500;
            }
          }
        }
  
        .my_lot_type2 {  border:1px solid #ccc; border-top:none; }
  
        .my_lot_type3 { border:none;
          position:relative; padding:30px; height:auto; text-align: center;
  
          .title { font-size:24px; font-weight: 600; color:$colorB2;
            span { color:$colorMain;}
          }
          .txt {font-size:18px; color:#777; margin-top:15px; line-height:1.5; font-weight: 500;}
          .member_price {border-top:1px solid #ccc; padding:24px 0 0; max-width:480px; width: 100%; margin: 24px auto 0;
            .p_txt {color:#333; font-size:18px; font-weight: 500;}
            .btn_member { background:#333; color:#fff; font-size:18px; font-weight: 500; text-align: center; padding:12px; min-width: 170px ; display:inline-block; margin-top:26px; line-height: 1.3;}
          }
  
        }
      }
    }
  
    .view-area { 
      .view-img { position: relative; width: 100%; height: 0; font-size: 0; line-height: 0;
        padding-bottom: 100%; text-align: center; 
        .img-box { position: absolute; vertical-align: middle; left: 0; right: 0; width: auto; height: 100%; text-align: center; font-size: 0; overflow: hidden;  
          .box-inner { position: relative; display: inline-block; width: auto; height: 100%; vertical-align: middle; text-align: center; font-size: 0;
            &::before { content: ''; height: 100%; width: 0; display: inline-block; vertical-align: middle; font-size: 0;}
          }
        }
        img { display: inline-block; max-width: 100%; max-height: 100%; vertical-align: middle; height: auto; width: auto; }
      }
      .view-typo { margin-top: 16px;text-align: center;
        .typo-header {
          .num {font-size: 16px; font-weight: 700;}
          .title { color:$colorB2; line-height: 30px;
            span { font-size: 20px; font-weight: 700; }
          }
          .year { color:$colorB2; 
            span { font-size: 16px; font-weight: 700; }
          }
          .desc { margin-top: 5px;
            font-size: 16px; color:#777; font-weight: 500;}
        }
        .typo-body   { margin-top: 8px;  font-weight: 500;
          span  { font-size: 16px; color:$colorB2; }
          .price_won   { font-weight: 600;}
          .price_other { margin-top: 5px; font-weight: 500;}
          ul {  font-size: 0;
            li { position: relative; display: inline-block; 
              font-size: 16px; color:#777777; line-height: 1.5;  
            }
            li + li { margin-left: 10px; padding-left: 10px;
              @include list-div_bar(6px, 0px, 12px, #bbbbbb );
            }
          }
        }
      
  
      }
    }
  }
  
  
