
/* =============================== 섹션 이름  */
.basis-section{
  /* 섹션 검색 */
  &.search-section { margin-top: 0px;  padding:0 60px; max-width: 1920px;}
  @media all and (max-width:$G-width)  {
    .search-section { margin-top: 0px; padding:0 50px; max-width: 1420px; }
  }
  @media all and (max-width:$G-wMobile)  {
    &.search-section { margin-top: 0px;  padding:0 25px; }
  }

}




/* =============================== 페널이름 */
.content-panel{
  /* 검색결과 */  
  &.type_panel-search { 
    .panel-header { padding-top: 70px; }
    .panel-body   { }
    @media all and (max-width:$G-wMobile)  {
      .panel-header { padding: 35px 0; }
      .panel-body   { }
    }
    /* 검색결과 */
    .search_results {
      font-size: 24px; font-weight: 500px; color:$colorB2;
      strong { font-weight: 600; color:$colorB;}
      em     { font-weight: 600; color:$colorP;}
      @media all and (max-width:$G-wMobile) {
        font-size: 17px; 
        strong { font-weight: 700; }
        em     { font-weight: 600; }
      } 
    }
    
    
  } 


  /* 텝 조건 */
  &.type_panel-search_tab2 {
    .panel-header { padding:0; } 
    .panel-body   { padding:0; } 
    .search_tab-article { padding: 30px 0;
      .article-body { display: flex;  justify-content: space-between; }
      
      .col_item { display: flex; 
        &.positon-col1 { width: calc(100% - 320px);}
        &.positon-col2 { justify-content: flex-end; width: 320px; }

        .filter-box { display: flex; 
          .btn_set { width: 45px;  display: block;
            .filter_btn { font-size: 0; height: 38px;}
          }
          .filter-list { /* width: calc(100% - 85px); */ font-size: 0;
            li {display: inline-block; padding:9px 15px; height: 38px; 
              margin-right: 9px; margin-bottom: 5px;
              background-color: #efefef; border-radius: 19px; font-size: 0;
              span, button { vertical-align: middle;}
              span {font-size:  16px;  line-height: 20px; font-weight: 500;}
              button { font-size: 0;  padding-left: 8px; margin-left: -5px;}
            }
          }
        }
        .select-box { max-width: none; }
        .select-box:first-child { min-width: 180px;}
        .select-box + .select-box{ margin-left: 10px;}
        .select-box + .search-box{ margin-left: 10px;}
        .search-box { width: 230px;
          input { 
            //&:focus , &:hover {border-color: $colorB2;}
          }
        }
      }
    }
    @media all and (max-width:$G-wMobile)  {
      .panel-body   { padding:0 25px; } 
      .search_tab-article { padding: 30px 0;
        .article-body { display: block;  justify-content: initial; }
        .col_item { display: block; 
          &.positon-col1 { margin-top: 0px; width: 100%;}
          .filter-box { display: flex; 
            .btn_set { width: 43px;  
              .filter_btn {  height: 33px;}
            }
            .filter-list { width: calc(100% - 43px); font-size: 0;
              li {display: inline-block; padding:9px 17px 8px; height: 32px; 
                margin-right: 10px; border-radius: 16px; 
                span {font-size:  13px;  line-height: 15px;}
                button { padding-left: 8px; margin-left: 0px;}
              }
            }
          }
          &.positon-col2 { display: flex;  position: relative; top:auto; right: 0;
            width: 100%; margin-top: 25px;
            .select-box { flex : 1; max-width: inherit; }
            .select-box:first-child  { margin-top: 0px;  margin-right: 5px;}
            .select-box:last-child   { margin-top: 0px;  margin-left:  5px;}
          }
         
        }
      }
    }
  }


}


//검색영역 공통 
/* 검색 영역 */
.search-area {  width: 690px; margin: 0 auto;
  .search-box     { position: relative; 
    // ie
    input[type="text"] { &.placeholdersjs { font-size: 24px; font-weight: 500;} }
    // Internet Explorer 10 이상에 적용select2Basic42
    input[type="text"] { &:-ms-input-placeholder { font-size: 24px; font-weight: 500;} } 
    //  Webkit, Blink, Edge에 적용
    input[type="text"] { &::-webkit-input-placeholder { font-size: 24px; font-weight: 500;} }
    // Mozilla Firefox 4 부터 18에 적용
    input[type="text"] { &::-moz-placeholder    { font-size: 24px; font-weight: 500; } }

    .input_del { position: relative; 
      padding: 18px 110px 18px 0; 
      border-bottom: 3px solid #333333;
      input { height: 50px; line-height: 50px; 
        border: 0; padding: 0px;
        font-size: 36px; font-weight: 600; color:$colorB;
      }
    }
    .btn_del , .btn_search { font-size: 0;
      position: absolute; ;
    }
    .btn_del    { top:50%; margin-top: -25px; right: 60px; }
    .btn_search { top:50%;  @include transform( translateY(-50%) ); right:-3px; }
  }
  .suggestion-box { position: relative; padding: 15px 0;
    display: flex;
    span { margin-top: 5px; line-height: 40px; }
    .word { width: 180px;
      span {font-size: 20px; font-weight: 600; color:$colorB; }
    }
    .word-items { width: calc(100% - 114px);  
      font-size: 0;
      button{ display: inline-block; box-sizing: border-box;
        margin-top: 10px; margin-right:10px; padding: 0 15px;
        border: 1px solid #cfcfcf; border-radius: 20px;
        font-size: 16px; color:$colorD; line-height: 38px; font-weight: 500;
      }
    }
  }

  @media all and (max-width:$G-wMobile)  { width: 100%;
    .search-box     {
      // ie
      input[type="text"] { &.placeholdersjs { font-size: 17px; font-weight: 400;} }
      // Internet Explorer 10 이상에 적용select2Basic42
      input[type="text"] { &:-ms-input-placeholder { font-size: 17px; font-weight: 400;} } 
      //  Webkit, Blink, Edge에 적용
      input[type="text"] { &::-webkit-input-placeholder { font-size: 17px; font-weight: 400;} }
      // Mozilla Firefox 4 부터 18에 적용
      input[type="text"] { &::-moz-placeholder    { font-size: 17px; font-weight: 400; } }


      .input_del { padding: 18px 70px 17px 0; 
        input { height: 25px; line-height: 25px; font-size: 28px; }
      }
      .btn_del , .btn_search { font-size: 0; position: absolute; ; }
      .btn_del    { top:50%; margin-top: -15px; right: 40px; }
      .btn_search { top:50%;  @include transform( translateY(-50%) ); right:-3px; }
    }
    .suggestion-box { padding: 25px 0 5px; display: block;
      span { margin-top: 10px; line-height: 40px; }
      .word { width: 100%;
        span {font-size: 17px; line-height: 27px; }
      }
      .word-items { width: 100%;  
        button{ margin-top: 10px; margin-right:10px; padding: 0 15px;
          border-radius: 17px;
          font-size: 13px; line-height: 33px;
        }
      }
    }
  }
}


/* =============================== 필터 팝업 레이어 */
.filter_fixed-wrap{
  &.active {
    .popup-dim { display: block;  }
    .fixed-panel { left: 0; }
  }
  .popup-dim { display: none; z-index: 11;}  
  .fixed-panel { left: -100%;
    position: fixed; top:0; width: 560px; z-index: 12;
    height: calc(100vh );
    height: calc(var(--vh, 1vh) * 100 );
    background-color: #fff;
    @include transition(left, .3s, ease-out);
    .panel-header { 
      .filter_close { position: absolute; top:30px; right: 30px; }
      .title-box { padding: 0px 70px; padding-top: 84px;
        .box-inner { position: relative; border-bottom: 1px solid #999;
          padding: 30px 0px 42px; 
          display: flex; justify-content: space-between;
        }
        span { 
        font-size: 42px; color:$colorB; font-weight: bold; line-height: 50px; 
        }
        .btn-filter_refresh { vertical-align: top; margin-top: 20px; 
          height: 30px;
          i { vertical-align: bottom; }
          span { font-size: 18px; color: $colorD; font-weight: 500;
            line-height: 30px;
            vertical-align: bottom; 
          }
        }
      }
    }
    .panel-body { 
      height: calc(100vh - ( 207px + 160px));
      height: calc(var(--vh, 1vh) * 100 - (207px + 160px));
    }
    .panel-footer { padding: 70px; padding-top: 34px;
      .btn_set { }
    }
    @media all and (max-width:$G-wMobile)  {
      width: 100%; left: -100%;
      .panel-header {
        .filter_close { position: absolute; top:20px; right: 25px; }
        .title-box   { padding: 0px 25px; padding-top: 35px;
          .box-inner { padding: 25px 0;}
          span { font-size: 25px; line-height: 30px; }
          .btn-filter_refresh { vertical-align: top; margin-top: 0px; 
            height: 30px;
            span { font-size: 14px; line-height: 20px; }
          }
        }
      }
      .panel-body { 
        height: calc(100vh - ( 115px + 103px));
        height: calc(var(--vh, 1vh) * 100 - (115px + 103px));
      }
      .panel-footer { padding: 20px 25px 30px;}
    }


    .filter_potion-article { padding: 0 70px;
      > ul {
        >li + li {border-top: 1px solid #ccc;}
      }
      .accordion-header { position: relative; 
        .header-box { display: block;
          padding: 15px 0; height: 60px; box-sizing: border-box; 
          span { vertical-align: top; line-height:30px;
            ;
            font-size: 20px; font-weight: 600; color:$colorB2;
          }
          &[data-active="true"] { i {@include transform( rotate(0deg) )} }
        }
        i { position: absolute; top:15px; right: -4px; @include transform( rotate(180deg)) }
      }
      .accordion-body{ 
        .check-list { padding: 25px 0 30px;
          >li  { padding: 8px 0;}
        }
        .input-list { padding: 22px 0 30px;
          >li  { padding: 8px 0;}
        }
      }
      @media all and (max-width:$G-wMobile)  { padding: 0 25px;
        .accordion-header { position: relative; 
          .header-box { padding: 10px 0; height: 50px; 
            span { margin-top: 0px; font-size: 15px; line-height: 30px; }
          }
          i { position: absolute; top:15px; right: 0px;  }
        }
        .accordion-body{ 
          .check-list { padding: 14px 0 23px;
            >li  { padding: 6px 0;}
          }
          .input-list { padding: 15px 0 23px;
            >li  { padding: 6px 0;}
          }
        }
      }
    }

    
  }
}